<!--  -->
<template>
  <div class="partList container-fluid py-4">
   <div>
       <h3>&nbsp;&nbsp; </h3>
   </div>
   <div class="d-flex">
       <div class="p-2 flex-fill" style="width: 500px;">
            <!--    -->
    <div class="main-container" style="width:450px;">
       <div class="content" style="height: auto; width: 400px ; margin: 20px; padding-bottom: 30px;" >
          <form class="d-flex flex-column">
           <div class="d-flex">
                     <div class="p-2 flex-fill">
                         <!--  -->
                         <div class="mb-3">
                           <label class="col-form-label fw-bold" for="clientNum"></label>
                           <input type="text" readonly class="form-control" style="background-color: white; padding-left: 20px;" id="clientNum" v-model="clientNum" >
                         </div>
                         <!--  -->
                         <div class="mb-3">
                            <label class="col-form-label fw-bold" for="comName"></label>
                            <input type="text" class="form-control" style="background-color: white; padding-left: 20px;" id="comName" v-model="comName" >
                         </div>

                     </div>
                     <div class="p-2 flex-fill">
                         <!--  -->
                         <div class="mb-3">
                            <label class="col-form-label fw-bold" for="comNum"></label>
                            <input type="text" class="form-control" style="background-color: white; padding-left: 20px;" id="comNum" v-model="comNum" >
                         </div>
                          <!--  -->
                          <div class="mb-3">
                            <label class="col-form-label fw-bold" for="trade"></label>
                            <input type="text" class="form-control" style="background-color: white; padding-left: 20px;" id="trade" v-model="trade" >
                         </div>
                     </div>
                   </div>
                          <!--  -->
                          <div class="mb-1">
                           <label class="col-form-label fw-bold" for="address"></label>
                           <input type="text" class="form-control" style="background-color: white; margin-left: 8px; width: 384px; padding-left: 20px; " id="address" v-model="address"  autocomplete="off"  >
                         </div>
                   <div class="d-flex">
                     <div class="p-2 flex-fill">
                      <!--  -->
                      <div class="mb-3">
                         <label class="col-form-label fw-bold" for="boss"></label>
                         <input type="text" class="form-control" style="background-color: white; padding-left: 20px;" id="boss" v-model="boss" >
                      </div>
                      <!--  -->
                      <div class="mb-3">
                          <label class="col-form-label fw-bold" for="empName"></label>
                          <input type="text" class="form-control" style="background-color: white; padding-left: 20px;" id="empName" v-model="empName" >
                      </div>

                    </div>
                    <div class="p-2 flex-fill">
                          <!--  -->
                          <div class="mb-3">
                            <label class="col-form-label fw-bold" for="bossTel"></label>
                            <input type="text" class="form-control" style="background-color: white; padding-left: 20px;" id="bossTel" v-model="bossTel" >
                          </div>

                           <!--  -->
                           <div class="mb-3">
                               <label class="col-form-label fw-bold" for="empTel"></label>
                               <input type="text" class="form-control" style="background-color: white; padding-left: 20px;" id="empTel" v-model="empTel" >
                           </div>
                    </div>
                   </div>
             <!--  -->
             <div class="col-sm-5">
                <div class="d-flex">
                  <div class="p-2 flex-fill">
                    <button style="position:relative; width:120px ; top:29px;" type="button" class="btn btn-success me-5" @click="upin? input_update(2) : input_update(1)"  >/</button>
                  </div>
                  <div class="p-2 flex-fill">
                    <button style="position:relative; width:120px ; top:29px;" type="button" class="btn btn-warning me-5" @click="refresh"  ></button>
                  </div>
                </div>
              </div>
             
          </form>
       </div>
           <!--     -->
       </div>
   </div>
       <div class="p-2 flex-fill" style="width: 1000px;">
           <div class="grid-container" style="padding-top: 10px;">
               <ag-grid-vue
               :rowData="rowData"
               :columnDefs="columnDefs"
               :theme="theme"
               :pagination="true"
               :paginationPageSize="10"
               @grid-ready="onReady"
               style="height: 513px;"
               rowSelection="multiple"
               @cellClicked="onCellClicked"
               >
               </ag-grid-vue>
           </div>
       </div>
   </div>
   <deleteModal
      :showModal="showDeleteModal"
      @deleteConfirmed="onDeleteConfirmed"
      @deleteCancelled="onDeleteCancelled"
   />
   </div>
    </template>
    <script>
    import axios from 'axios';
    import { ajaxUrl } from '@/utils/commons.js';
    // import userDateUtils from '@/utils/useDates.js';
    import theme from "@/utils/agGridTheme";
    import { mapMutations } from "vuex";
    import deleteModal from './deleteModal.vue';
    
    export default {
      components: {
      deleteModal, //   
     },
      data() {
        return {
            clientNum: '', // 
            comName: '', // 
            boss: '', // 
            empName: '', // 
            trade: '', // 
            comNum: '', // 
            address: '', // 
            bossTel: '', // 
            empTel: '', // 
            showDeleteModal: false, //    
            selectedCustomer: null,
          columnDefs: [
            { headerName:"" , field:"com_name"},
            { headerName:"" , field:"address"},
            { headerName:"" , field:"trade" , cellStyle: { textAlign: 'center' }},
            {
              headerName: "",
              field: "",
              upin : '',
              cellStyle: { textAlign: 'center' },
              editable: false,
              cellRenderer: (params) => {
                const button2 = document.createElement('button');
                button2.innerText = '';
                button2.style.marginRight = '10px';
                button2.style.cursor = 'pointer';
                button2.style.backgroundColor = '#f44335';
                button2.style.width = '60px';
                button2.style.height = '30px';
                button2.style.color = 'white';
                button2.style.border = 'none';
                button2.style.padding = '0';
                button2.style.borderRadius = '4px';
                button2.style.textAlign = 'center';
                button2.style.lineHeight = '30px';
                button2.addEventListener('click', () => {
                  console.log(" [] : ", JSON.stringify(params.data));
                  console.log("  : ", params.data.client_num , " : ", params.data.com_name);
                  this.selectedCustomer = params.data;
                  this.showDeleteModal = true; //  
                });
                return button2;
              }
            },
          ],
          newList: [], //  , 
          rowData: [], //    api 
          theme:theme,
        };
      },
      methods: {
        onDeleteCancelled() {
          this.showDeleteModal = false;
        },
        async onDeleteConfirmed(){
          if (this.selectedCustomer) {
            try{
              axios.delete(`${ajaxUrl}/customerDelete/${this.selectedCustomer.client_num}`)
                .then(res => {
                  if (res.data === '') {
                    this.$notify({ text: ' .', type: 'success' });
                    this.customerList();
                  } else {
                    this.$notify({ text: '.', type: 'error' });
                  }
                })
            }catch(error){
              console.log(error);
              this.$notify({ text: '.', type: 'error' });
            }
            this.showDeleteModal = false;
          }
        },
        ...mapMutations(["addLoginInfo"]),
        async checkLogin(){
          this.loginInfo = this.$store.state.loginInfo;
          console.log('',this.loginInfo);
          if(this.loginInfo.job === ''){
            console.log('');
          }else{
              this.$notify({ text: '  ', type: 'error' });
              this.$router.push({ name : 'MainPage' });
          }
      },
       async customerList() {
           const result = await axios.get(`${ajaxUrl}/customerList`)
                                     .catch(err => console.log(err));
             this.rowData = result.data;
       },
       onReady(param) {
         param.api.sizeColumnsToFit(); //  api      
       },
       onCellClicked(params) {
           if(params.colDef.field !== ''){   
             console.log(" [] : ", JSON.stringify(params.data));
              this.clientNum = params.data.client_num;
              this.comName = params.data.com_name;
              this.boss = params.data.boss;
              this.empName = params.data.emp_name;
              this.trade = params.data.trade;
              this.comNum = params.data.com_num;
              this.address = params.data.address;
              this.bossTel = params.data.boss_tel;
              this.empTel = params.data.emp_tel;
              this.upin = 'update';
           }
       },
       async customerInsert(newList){
           const result = await axios.post(`${ajaxUrl}/customerInsert`, newList)
                                     .catch(err => console.log(err));
           if(result.data === ''){
              this.$notify({ text: ' .', type: 'success' });
               this.customerList();
           }else{
              this.$notify({ text: ' .', type: 'error' });
           }
       },
       async customerUpdate(newList){
          const result = await axios.post(`${ajaxUrl}/customerUpdate/${this.clientNum}`, newList)
                                     .catch(err => console.log(err));
          if(result.data === ''){
            this.$notify({ text: ' .', type: 'success' });
              this.customerList();
              this.upin = '';
              this.clientNum = '';
              this.comName = '';
              this.boss = '';
              this.empName = '';
              this.trade = '';
              this.comNum = '';
              this.address = '';
              this.bossTel = '';
              this.empTel = '';
          }else{
            this.$notify({ text: ' .', type: 'error' });
          }
       },
        refresh(){
            this.customerList();
            this.upin = '';
              this.clientNum = '';
              this.comName = '';
              this.boss = '';
              this.empName = '';
              this.trade = '';
              this.comNum = '';
              this.address = '';
              this.bossTel = '';
              this.empTel = '';
        },
       input_update(number) {
         console.log('    ');
          console.log(this.clientNum);
          console.log(this.comName);
          console.log(this.boss);
          console.log(this.empName);
          console.log(this.trade);
          console.log(this.comNum);
          console.log(this.address);
          console.log(this.bossTel);
          console.log(this.empTel);
          if(number === 1){
            if(this.comName === '' || this.boss === '' || this.empName === '' || this.trade === '' || this.comNum === '' || this.address === '' || this.bossTel === '' || this.empTel === ''){
              this.$notify({ text: ' ', type: 'error' });
                return;
            }else{
                console.log('');
                let dataresult = 'ok';
                if(dataresult === 'ok'){
                    this.newList = {
                      com_num: this.comNum,
                      client_num: this.clientNum,
                      com_name: this.comName,
                      address: this.address,
                      boss: this.boss,
                      boss_tel: this.bossTel,
                      emp_name: this.empName,
                      emp_tel: this.empTel,
                      trade: this.trade,
                    }
                    console.log(' ',this.newList);
                    this.customerInsert(this.newList);
                return
                }
            }
        }else if(number === 2){
            console.log('');
            this.newList = {
              com_num: this.comNum,
              client_num: this.clientNum,
              com_name: this.comName,
              address: this.address,
              boss: this.boss,
              boss_tel: this.bossTel,
              emp_name: this.empName,
              emp_tel: this.empTel,
              trade: this.trade,
            }
            this.customerUpdate(this.newList);
        }
          
          
        },
      },
      //   
      mounted() {
        //   
        this.checkLogin();
        this.customerList();
      },
    };
    </script>
    
    <style lang="scss" scoped>
    .main-container{
        background-color:  #e9ecef;
        margin-left: 20px;
        margin-right: 20px;
        margin-top: 0px;
        margin-bottom: 0px;
        border-radius: 10px;
    }
    .content{
       margin-left: 20px;
       margin-right: 20px;
       margin-top: 0px;
       margin-bottom: 0px;
       padding: 0px;
    }
    input {
          background-color: $white;
          border: solid 1px  ;
    }
/* input     */
input:focus {
  background-color: #ffffff; /*      */
  border-color: #86b7fe; /*       */
  outline: none; /*      */
}
    </style>