<!--  -->
<template>
  <div class="partList container-fluid py-4">
<div>
    <h3>&nbsp;&nbsp; </h3>
</div>
<div class="d-flex">
    <div class="p-2 flex-fill" style="width: 500px;">
         <!--    -->
 <div class="main-container" style="width:450px;">
    <div class="content" style="height: auto; width: 400px ; margin: 20px; padding-bottom: 30px;" >
       <form class="d-flex flex-column">
        <div class="d-flex">
                  <div class="p-2 flex-fill">
                      <!--  -->
                      <div class="mb-3">
                         <label class="col-form-label fw-bold" for="empNum"></label>
                         <input type="text" placeholder="ex)20250000"  autocomplete="off"  class="form-control" style="background-color: white; " id="empNum" v-model="empNum" >
                      </div>
                      <!--  -->
                      <div class="mb-3">
                         <label class="col-form-label fw-bold" for="birth"></label>
                         <input type="date" class="form-control"  autocomplete="off"  style="background-color: white; padding-left: 20px;" id="birth" v-model="birth" >
                      </div>
                      <!--  -->
                      <div class="mb-3">
                        <label class="col-form-label fw-bold" for="job"></label>
                        <select
                          class="form-select"
                          v-model="job"
                          @change="updateJobNum"
                          style="background-color: white; text-align: center; border: solid 1px;"
                          aria-label=" "
                        >
                          <option value=""> </option>
                          <option value=""></option>
                          <option value=""></option>
                          <option value=""></option>
                          <option value=""></option>
                          <option value=""></option>
                          <option value=""></option>
                        </select>
                      </div>
                        <!--  -->
                      <div class="mb-3">
                        <label class="col-form-label fw-bold" for="position"></label>
                        <select
                          class="form-select"
                          v-model="position"
                          style="background-color: white; text-align: center; border: solid 1px;"
                          aria-label=" "
                        >
                          <option value=""> </option>
                          <option value=""></option>
                          <option value=""></option>
                          <option value=""></option>
                          <option value=""></option>
                          <option value=""></option>
                          <option value=""></option>
                        </select>
                      </div>
                      <!--  -->
                      <div class="mb-3">
                         <label class="col-form-label fw-bold" for="employmentDate"></label>
                         <input type="date"  autocomplete="off"  class="form-control" style="background-color: white; padding-left: 20px;" id="employmentDate" v-model="employmentDate" >
                      </div>
                  </div>
                  <div class="p-2 flex-fill">
                      <!--  -->
                      <div class="mb-3">
                         <label class="col-form-label fw-bold" for="name"></label>
                         <input type="text" class="form-control"  autocomplete="off"  style="background-color: white; padding-left: 20px;" id="name" v-model="name" >
                      </div>
                         <!--  -->
                         <div class="mb-3">
                            <label class="col-form-label fw-bold" for="tel"></label>
                            <input type="tel"  autocomplete="off"  class="form-control" placeholder="010-0000-0000" style="background-color: white; padding-left: 20px;" id="tel" v-model="tel" >
                        </div>
                        <!--  -->
                        <div class="mb-3">
                            <label class="col-form-label fw-bold" for="jobNum"></label>
                            <input type="text" placeholder="  " class="form-control" style="background-color: white; padding-left: 20px;" id="jobNum='101'" v-model="jobNum" readonly >
                        </div>
                        <!--  -->
                        <div class="mb-3">
                           <label class="col-form-label fw-bold" for="level"></label>
                                <div id="level" style="padding-left: 0px;">
                                <select class="form-select" v-model="level" style="width: 184px; border: solid 1px; background-color: white; text-align: center;" aria-label="  ">
                                        <option value="">1</option>
                                        <option value="1">1</option>
                                        <option value="2">2</option>
                                        <option value="3">3</option>
                                </select>
                                </div>
                           <!-- <input type="text" class="form-control" style="background-color: white; padding-left: 20px;" id="level" v-model="level" > -->
                        </div>
                        <!--  -->
                        <div class="mb-3">
                           <label class="col-form-label fw-bold" for="resignationDate"></label>
                           <input type="date" class="form-control" style="background-color: white; padding-left: 20px;" id="resignationDate" v-model="resignationDate" >
                        </div>
                  </div>
                </div>
          <!--   -->
          <div class="col-sm-5">
            <div class="d-flex">
              <div class="p-2 flex-fill">
                <button style="position:relative; width:120px ; top:29px;" type="button" class="btn btn-success me-5" @click="input_update" text-align: right >/</button>
              </div>
              <div class="p-2 flex-fill">
                <button style="position:relative; width:120px ; top:29px;" type="button" class="btn btn-warning me-5" @click="refresh"  ></button>
              </div>
          </div>
        </div>
       </form>
    </div>
        <!--     -->
    </div>
</div>
    <div class="p-2 flex-fill" style="width: 1000px;">
        <div class="grid-container" style="padding-top: 10px;">
            <ag-grid-vue
            :rowData="rowData"
            :columnDefs="columnDefs"
            :theme="theme"
            :pagination="true"
            :paginationPageSize="10"
            @grid-ready="onReady"
            style="height: 513px;"
            rowSelection="multiple"
            @cellClicked="onCellClicked"
            >
            </ag-grid-vue>
        </div>
    </div>
</div>
<deleteModal
      :showModal="showDeleteModal"
      @deleteConfirmed="onDeleteConfirmed"
      @deleteCancelled="onDeleteCancelled"
 />
  </div>
 </template>
 <script>
 import axios from 'axios';
 import { ajaxUrl } from '@/utils/commons.js';
 // import userDateUtils from '@/utils/useDates.js';
 import theme from "@/utils/agGridTheme";
 import { mapMutations } from "vuex";
 import deleteModal from './deleteModal.vue';
 export default {
    components: {
        deleteModal, //   
    },
   data() {
     return {
        empNum: '',   // 
        name: '', // 
        birth: '',  // 
        tel: '', // 
        job: '',  // 
        jobNum: '', // 
        position: '', // 
        level: '', // 
        employmentDate: '', // 
        resignationDate: '', // 
        showDeleteModal: false, //    
        selectedEmployee: null,
       columnDefs: [
         { headerName: "", field: "emp_num", width: 220 ,cellStyle: { textAlign: 'right' }},
         { headerName: "", field: "name" },
         { headerName: "", field: "tel" ,cellStyle: { textAlign: 'center' }},
         { headerName: "", field: "job" },
        //  { headerName: "", field: "job_num" },
         { headerName: "", field: "position" },
         {
           headerName: "",
           field: "",
           upin : '',
           editable: false,
           cellStyle: { textAlign: 'center' },
           cellRenderer: (params) => {
             const button2 = document.createElement('button');
             button2.innerText = '';
             button2.style.marginRight = '10px';
             button2.style.cursor = 'pointer';
             button2.style.backgroundColor = '#f44335';
             button2.style.width = '60px';
             button2.style.height = '30px';
             button2.style.color = 'white';
             button2.style.border = 'none';
             button2.style.padding = '0';
             button2.style.borderRadius = '4px';
             button2.style.textAlign = 'center';
             button2.style.lineHeight = '30px';
             button2.addEventListener('click', () => {
               console.log(" [] : ", JSON.stringify(params.data));
               console.log("  : ", params.data.emp_num , " : ", params.data.name);
               this.selectedEmployee = params.data;
               this.showDeleteModal = true;
             });
             return button2;
           }
         },
       ],
       newList: [], //  , 
       rowData: [], //    api 
       theme:theme,
     };
   },
   methods: {
    onDeleteCancelled() {
      this.showDeleteModal = false;
    },
    async onDeleteConfirmed() {
      if (this.selectedEmployee) {
        try{
          axios.delete(`${ajaxUrl}/employeeDelete/${this.selectedEmployee.emp_num}`)
            .then(res => {
              if (res.data === '') {
               this.$notify({ text: ' .', type: 'success' });
                this.employeeList();
              } else {
               this.$notify({ text: '.', type: 'error' });
              }
            })

        }catch(error) {
          console.log(error);
          this.$notify({ text: '.', type: 'error' });
        }
        this.showDeleteModal = false;
    }
  },
    refresh(){
            this.employeeList();
            this.upin = '';
            this.empNum = '';
            this.name = '';
            this.birth = '';
            this.tel = '';
            this.job = '';
            this.jobNum = '';
            this.position = '';
            this.level = '';
            this.employmentDate = '';
            this.resignationDate = '';
    },
    updateJobNum() {
    console.log('hjkhjkhkj',this.job);
      //    
      const departmentJobNumbers = {
        "": "1",
        "": "2",
        "": "3",
        "": "4",
        "": "5",
        "": "6",
      };
      this.jobNum = departmentJobNumbers[this.job] || ''; //  
    },
    async employeeList() {
        const result = await axios.get(`${ajaxUrl}/employeeList`)
                                  .catch(err => console.log(err));
          this.rowData = result.data;
    },
    ...mapMutations(["addLoginInfo"]),
      async checkLogin(){
          this.loginInfo = this.$store.state.loginInfo;
          console.log('',this.loginInfo);
          if(this.loginInfo.job === ''){
            console.log('');
          }else{
              this.$notify({ text: '  .', type: 'error' });
              this.$router.push({ name : 'MainPage' });
          }
      },
    onReady(param) {
      param.api.sizeColumnsToFit(); //  api      
    },
    onCellClicked(params) {
        if(params.colDef.field !== ''){   
          console.log(" [] : ", JSON.stringify(params.data));
            this.empNum = params.data.emp_num;
            this.name = params.data.name;
            this.birth = params.data.birth;
            this.tel = params.data.tel;
            this.job = params.data.job;
            this.jobNum = params.data.job_num;
            this.position = params.data.position;
            this.level = params.data.level;
            this.employmentDate = params.data.employment_date;
            this.resignationDate = params.data.resignation_date;
        }
    },
    async employeeInsert(newList){
      const empNumber = await axios.get(`${ajaxUrl}/employeeList`);
      const existingEmployee = empNumber.data.find(employee => employee.emp_num === newList.emp_num);
        const result = await axios.post(`${ajaxUrl}/employeeInsert`, newList)
                                  .catch(err => console.log(err));
            if (result.data === '') {
                  if(existingEmployee){
                    this.$notify({ text: ' .', type: 'success' });
                  }else{
                    this.$notify({ text: ' .', type: 'success' });
                  }
                  this.employeeList();
            }else {
              this.$notify({ text: '.', type: 'error' });
            }
        console.log(result.data);
        this.employeeList();
    },
    input_update() {
      if(this.empNum === '' || this.name === '' || this.birth === '' || this.tel === '' || this.job === '' || this.jobNum === '' || this.position === '' || this.employmentDate === ''){
        this.$notify({ text: ' ', type: 'error' });
        return;
      }
      
      if(this.tel.length !== 13){
        this.$notify({ text: ' ', type: 'error' });
        return;
      }

      console.log('    ');
      this.newList = { emp_num: this.empNum,
                       name: this.name,
                       birth: this.birth, 
                       tel: this.tel, 
                       job: this.job, 
                       job_num: this.jobNum, 
                       position: this.position, 
                       level: this.level || null, 
                       employment_date: this.employmentDate, 
                       resignation_date: this.resignationDate || null };
      this.employeeInsert(this.newList);
      this.employeeList();
    },
   },
   //   
   mounted() {
     //   
     this.checkLogin();
     this.employeeList();
   },
 };
 </script>
 
 <style lang="scss" scoped>
input.form-control {
  padding-left: 20px;
  padding-right: 20px;
}
#birth{
text-align: center;
}
#tel{
text-align: center;
}
#employmentDate{
text-align: center;
}
#resignationDate{
  text-align: center;
}
 .main-container{
     background-color:  #e9ecef;
     margin-left: 20px;
     margin-right: 20px;
     margin-top: 0px;
     margin-bottom: 0px;
     border-radius: 10px;
 }
 .grid-container{
    margin-top: 9px;
 }
 .content{
    margin-left: 20px;
    margin-right: 20px;
    margin-top: 0px;
    margin-bottom: 0px;
    padding: 0px;
 }
 input {
       background-color: $white;
       border: solid 1px  ;
 }
 /* input     */
input:focus {
  background-color: #ffffff; /*      */
  border-color: #86b7fe; /*       */
  outline: none; /*      */
}
 </style>