<!--  -->
<template>
   <div class="partList container-fluid py-4">
    <div>
       <h3>&nbsp;&nbsp; </h3>
    </div>
 <!--    -->
 <div class="main-container">
    <div class="content" style="height: 130px; margin: 20px;">
       <form class="row gx-3 gy-2 align-items-center">
         <div class="d-flex">
            <div class="p-2 flex-fill">
          <!--  -->
             <div class="col-sm-8">
               <label class="col-form-label fw-bold" for="materialCode"></label>
               <input type="text" class="form-control" style="background-color: white; padding-left: 20px;" id="materialCode" v-model="materialCode" >
             <!--  -->
             <div class="col-sm-12">
                <label class="col-form-label fw-bold" for="materialName"></label>
                <input type="text" class="form-control" style="background-color: white; padding-left: 20px;" id="materialName" v-model="materialName" >
             </div>
          </div>
         </div>
         <div class="p-2 flex-fill">
            <!--  -->
            <div class="col-sm-8">
               <label class="col-form-label fw-bold" for="safetyInventory">(g,)</label>
               <input type="text" class="form-control" style="background-color: white; padding-left: 20px;" id="safetyInventory" v-model="safetyInventory" >
            </div>
            <!--  -->
            <div class="col-sm-8">
               <label class="col-form-label fw-bold" for="expirationDate"></label>
               <input type="text" class="form-control" style="background-color: white; padding-left: 20px;" id="expirationDate" v-model="expirationDate" >
            </div>
            
         </div>
         <div class="p-2 flex-fill">
            <!--  -->
            <div class="col-sm-8">
               <label class="col-form-label fw-bold" for="regiDate"></label>
               <input type="date" readonly class="form-control" style="background-color: white; padding-left: 20px;" id="regiDate" v-model="regiDate" >
            </div>
            <div class="p-2 flex-fill">
               <button style="position:relative; top:29px;" type="button" class="btn btn-success me-5" @click="upin? input_update(2) : input_update(1)">/</button>
               <button style="position:relative; width:120px ; top:29px;" type="button" class="btn btn-warning me-5" @click="refresh"  ></button>
            </div>

          </div>
         </div>
      </form>
    </div>
 </div>
 <!--     -->
 <div class="grid-container" style="padding-top: 10px;">
    <ag-grid-vue
       :rowData="rowData"
       :columnDefs="columnDefs"
       :theme="theme"
       :pagination="true"
       :paginationPageSize="10"
       @grid-ready="onReady"
       style="height: 513px;"
       rowSelection="multiple"
       @cellClicked="onCellClicked"
    >
    </ag-grid-vue>
 </div>
 </div>
 </template>
 <script>
 import axios from 'axios';
 import { ajaxUrl } from '@/utils/commons.js';
 // import userDateUtils from '@/utils/useDates.js';
 import theme from "@/utils/agGridTheme";
 import { mapMutations } from "vuex";

 
 export default {
   data() {
     return {
       materialCode: '',   // 
       materialName: '',  // 
       safetyInventory: '',  // 
       expirationDate: '', //  
       regiDate: new Date().toISOString().split('T')[0], // 
       columnDefs: [
         { headerName: "", field: "material_code", width: 220 },
         { headerName: "", field: "material_name" },
         { headerName: "(g, )", 
           field: "safety_inventory" ,
           cellStyle: { textAlign: 'right'} ,
           cellRenderer: params => {
               if (params.value != null) {
               const formatted_t_qty = Number(params.value).toLocaleString();
               return `<span style="text-align: right;">${formatted_t_qty}</span>`;
               } else {
               return `<span style="text-align: right;"></span>`;
               }
            },
         },
         { headerName: "()", field: "expiration_date" ,cellStyle: { textAlign: 'right' }},
         { headerName: "", field: "regi_date",cellStyle: { textAlign: 'center' } },
         {
           headerName: "",
           field: "material_delete",
           cellStyle: { textAlign: 'center' },
           upin : '',
           editable: false,
           cellRenderer: (params) => {
             const button2 = document.createElement('button');
             button2.innerText = '';
             button2.style.marginRight = '10px';
             button2.style.cursor = 'pointer';
             button2.style.backgroundColor = '#f7b84d';
             button2.style.width = '60px';
             button2.style.height = '30px';
             button2.style.color = 'white';
             button2.style.border = 'none';
             button2.style.padding = '0';
             button2.style.borderRadius = '4px';
             button2.style.textAlign = 'center';
             button2.style.lineHeight = '30px';
             button2.addEventListener('click', () => {
               console.log(" [] : ", JSON.stringify(params.data));
               console.log(params.data.material_code);
               if (confirm('?')) {
                  axios.delete(`${ajaxUrl}/materialDel/${params.data.material_code}`)
                       .then(res => {
                          if(res.data === ''){
                           this.$notify({ text: ' .', type: 'success' });
                             this.materialList();
                          }else{
                           this.$notify({ text: ' .', type: 'error' });
                          }
                       })
                       .catch(err => console.log(err));
               }
               //   
               // lotinfo(params.data);
             });
             return button2;
           }
         },
       ],
       newList: [], //      
       rowData: [], //    api 
       theme:theme,
     };
   },
   methods: {
      refresh(){
         this.materialList();
         this.upin = '';
         this.materialCode = '';
         this.materialName = '';
         this.safetyInventory = '';
         this.expirationDate = '';
         this.regiDate = '';
      },
      ...mapMutations(["addLoginInfo"]),
      async checkLogin(){
          this.loginInfo = this.$store.state.loginInfo;
          console.log('',this.loginInfo);
          if(this.loginInfo.job === ''){
            console.log('');
          }else{
              this.$notify({ text: '  .', type: 'error' });
              this.$router.push({ name : 'MainPage' });
          }
      },
      async materialList(){
         const result = await axios.get(`${ajaxUrl}/materialList`)
                              .catch(err => console.log(err));
         console.log(result.data);
         this.rowData = result.data; //  rowData 
         console.log(' : ', this.rowData);

      },
     onReady(param) {
       param.api.sizeColumnsToFit(); //  api      
     },
      onCellClicked(params) {
         if(params.colDef.field !== 'material_delete'){   
            console.log(' [] : ', JSON.stringify(params.data));
            this.materialCode = params.data.material_code;
            this.materialName = params.data.material_name;
            this.safetyInventory = params.data.safety_inventory;
            this.expirationDate = params.data.expiration_date;
            this.regiDate = params.data.regi_date;
            this.upin = 'update';
         }
      },
     async input_update(number) {
       console.log('    ');
         console.log(this.materialCode);
         console.log(this.materialName);
         console.log(this.safetyInventory);
         console.log(this.expirationDate);
       if (number === 1) {
         if (this.materialCode === '' || this.materialName === '' || this.safetyInventory === '' || this.expirationDate === '') {
            this.$notify({ text: ' ', type: 'error' });
           return;
         }else{
           console.log('');
           let dataresult = 'ok';
           for(let i = 0; i < this.rowData.length; i++){
             if(this.rowData[i].material_code === this.materialCode){
               this.$notify({ text: '  .', type: 'error' });
               dataresult = 'no';
               return;
             }
           }
           if(dataresult === 'ok'){
               this.newList = {
                   material_code: this.materialCode,
                   material_name: this.materialName,
                   safety_inventory: this.safetyInventory,
                   expiration_date: this.expirationDate,
               }
               console.log(this.newList);
               this.materialInsert(this.newList);
            return
           } 
         }
      }else if(number === 2){
         console.log('');
         this.newList = {
             material_code: this.materialCode,
             material_name: this.materialName,
             safety_inventory: this.safetyInventory,
             expiration_date: this.expirationDate,
         }
         this.materialUpdate(this.newList);
       }
     },
     async materialUpdate(newList){
        const result = await axios.post(`${ajaxUrl}/materialUpdate/${this.materialCode}`, newList)
                                   .catch(err => console.log(err));
        if(result.data === ''){
         this.$notify({ text: ' .', type: 'success' });
           this.materialList();
           this.upin = '';
           this.materialCode = '';
           this.materialName = '';
           this.safetyInventory = '';
           this.expirationDate = '';  
        }else{
         this.$notify({ text: ' .', type: 'error' });
        }
     },
      async materialInsert(newList){
         const result = await axios.post(`${ajaxUrl}/materialInsert`, newList)
                                    .catch(err => console.log(err));
         console.log('',result.data);
         if(result.data === ''){
            this.$notify({ text: ' .', type: 'success' });
            this.materialList();
         }else{
            this.$notify({ text: ' ', type: 'error' });
         }
      
      },
   },
   //   
   mounted() {
      this.checkLogin();
      this.materialList();
   },
 };
 </script>
 
 <style lang="scss" scoped>
 .main-container{
     height: 220px;
     background-color:  #e9ecef;
     margin-left: 20px;
     margin-right: 20px;
     margin-top: 0px;
     margin-bottom: 0px;
     border-radius: 10px;
 }
 .content{
    margin-left: 20px;
    margin-right: 20px;
    margin-top: 0px;
    margin-bottom: 0px;
    padding: 0px;
 }
 input {
       background-color: $white;
       border: solid 1px  ;
 }
 input:focus {
  background-color: #ffffff; /*      */
  border-color: #86b7fe; /*       */
  outline: none; /*      */
}
 </style>