<template>
    <div>
            <!--    -->
        <input type="text" v-model="inputListsearch" class="text-center styled-input" placeholder=" "/>
        <div class="grid-container">
            <ag-grid-vue
            
            :rowData="rowData"
            :columnDefs="columnClientlist"
            :theme="theme"
            @grid-ready="onReady"
            :quickFilterText="inputListsearch"
            :noRowsOverlayComponent="noRowsOverlayComponent"
            @rowClicked="selectClient"
            rowSelection="single"
        />
        </div>
        <div style="display: none">
            <CustomNoRowsOverlay/>
        </div>
    </div>

    
</template>
<script>
import axios from "axios";
import { ajaxUrl } from '@/utils/commons.js';
import theme from "@/utils/agGridTheme";
import CustomNoRowsOverlay from "@/views/natureBlendComponents/grid/noDataMsg.vue";

export default{
    name : "ClientList",
    components:{
        CustomNoRowsOverlay,
    },
    emits: ['selectclient'], // selectclient  

    data(){
        return{
            rowData:[], //  
            theme : theme,
            selectedCom : "", //   
            columnClientlist : [
            {headerName : "", field:"client_num",resizable: true, sortable: true},
            {headerName : "", field:"com_name",resizable: true, sortable: true}
            ],
            //  ( )
            inputListsearch: "", // 1 ()
            noRowsOverlayComponent:'CustomNoRowsOverlay',
        }
    },
    created(){
        this.getClientList()
    },
    methods: {
        async getClientList(){
            let result = await axios.get(`${ajaxUrl}/orderlist/clients`) //
                                    .catch(err=> console.log(err));
            this.rowData = result.data; // 
        },

        selectClient(event){
            const client = event.data;
            this.$emit('selectclient',client);
        },
        onReady(event){
            this.gridApi = event.api;
            //event.api.sizeColumnsToFit(); // api   
            //    
            const allPanels = document.querySelectorAll('.ag-paging-panel');
            const paginationPanel = allPanels[0];
            if (paginationPanel) {
               //  
               const container = document.createElement('div');
               container.style.display = 'flex';
               container.style.alignItems = 'center';
               container.style.gap = '5px'; //    

               
                // 
                const inputText = document.createElement('input');
                inputText.type = 'text';
                inputText.placeholder = '';
                inputText.style.padding = '5px';
                inputText.style.width = '250px';
                inputText.style.border = '1px solid #ccc';
                inputText.style.borderRadius = '4px';

                //    ag 
                inputText.addEventListener('input',(event)=>{
                    const value = event.target.value;
                    //console.log(" :", value);

                    //
                    this.inputListsearch = value;
                });

                // ,   
                
                container.appendChild(inputText);

                
            }

           


        },
        

        
        
    }
}
    
    
</script>
<style scoped lang="scss">
.card {
  box-shadow: none;
}
.emp:hover {
  background-color: $main3;
  cursor: pointer;
  transition: all .1s ease-in-out;
  h6, p {
    color: $white;
  }

}
.selected {
  background-color: $main1;
  h6, p {
    color: $white;
  }
}

.styled-input {
    display: block; /*      */
    margin: 0 auto; /*      */
    margin-top: 10px;
    padding: 10px; /*   */
    width: 250px; /*    */
    text-align: center; /*    */
    border: 1px solid #ccc; /*  */
    border-radius: 20px; /*   */
    outline: none; /*      */
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); /*   */
    transition: box-shadow 0.3s ease; /*    */
}

/*      */
.styled-input:focus,
.styled-input:hover {
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2); /*    */
    border-color: #007bff; /*      */
}
</style>