<template>
    <div class="container-fluid py-4">
    <!--  -->
    <h3></h3>
    <div class= "main-container ps-4">
        <div class= "pt-5 pb-5">
                <!-- -->
                <div class="row align-items-center mb-3">
                    <label class="col-sm-2 col-form-label fw-bold" ></label>
                    <div class="col-sm-4">
                        <input 
                            id="clientSearch"  class="form-control border p-2"
                            v-model="searchCom.com_name" @click="openModal('client')"  autocomplete="off"   placeholder=" "/>
                            <Modal
                                :isShowModal="isShowModal.client"
                                :modalTitle="''"
                                :noBtn="''"
                                :yesBtn="''"
                                @closeModal="closeModal('client')"
                                @confirm="confirm('client')"
                            >
                            <template v-slot:list>
                                <ComList v-show="isShowModal.client" @selectclient="selectclient"/>
                            </template>
                            </Modal>
                    </div>    
                </div>
                <!--  -->
                <div class="row align-items-center mb-5">
                    <label class="col-sm-2 col-form-label fw-bold"></label>
                    <div class="col-sm-4">
                        <input 
                        type="date" 
                        id="startDate" class="form-control border p-2"
                        v-model="startDate"  autocomplete="off"  />
                    </div>
                    <div class="col-sm-1 text-center">~</div>
                    <div class="col-sm-4">
                        <input 
                        type="date" 
                        id="endDate" class="form-control border p-2"
                        v-model="endDate" autocomplete="off"   />
                    </div>
                </div>
            </div>
                    <!--  -->
            <div class="pb-3 text-center">
                <material-button size="sm" color="success" class="button" @click="searchOrderlists"></material-button>
                <material-button size="sm" color="warning" class="button" @click="resetSearch"></material-button>
            </div>
        </div>
    </div>
    <!-- <div class="grid-container" v-show="rowData.length != 0"> -->
    <div class="grid-container">
        <ag-grid-vue
        style ="height: 450px;"
        :rowData="rowData"
        :columnDefs="columnOrderlist"
        :theme="theme"
        @grid-ready="onReady"
        :quickFilterText="inputListsearch"
        :noRowsOverlayComponent="noRowsOverlayComponent"
        @rowClicked="onOrderRowClicked"
        @cellClicked="onCellClicked" 
        rowSelection="multiple"
        :pagination="true"
        :paginationPageSize="10"
        :paginationPageSizeSelector="[10, 20, 50, 100]"
    />
    </div>
    <div style="display: none">
        <CustomNoRowsOverlay/>
    </div>
    <div>
        <Modal
            :isShowModal="isShowModal.returns"
            :modalTitle="''"
            :noBtn="''"
            :yesBtn="''"
            @closeModal="closeModal('returns')"
            @confirm="confirm('returns')"
        >
        <template v-slot:list>
            <ReturnList v-show="isShowModal.returns" @selectreturn="selectreturn"/>
        </template>
        </Modal>
    </div>
         
    
</template>

<script>
import MaterialButton from "@/components/MaterialButton.vue";
import Modal from "@/views/natureBlendComponents/modal/Modal.vue";
import ComList from "@/views/sales/Order/clientModal.vue";
import ReturnList from "@/views/sales/ProductReturn/ReturnModal.vue"
import theme from "@/utils/agGridTheme";
import userDateUtils from '@/utils/useDates.js';
import CustomNoRowsOverlay from "@/views/natureBlendComponents/grid/noDataMsg.vue";
import axios from "axios";
import { ajaxUrl } from '@/utils/commons.js';

export default{
    name :"orderlistSearch",
    components:{
            MaterialButton,
            Modal,
            ComList,
            ReturnList,
            CustomNoRowsOverlay,
            
    },
    data(){
        return{
            
            originalValue: null,  //    
            originalReturnReason : null,
            originalReturnCode : null,

            //  
            startDate:"", //  
            endDate:"", //  
            //  
            searchCom:"", //     
            selectedCom: "", //  

            //
            selectedReturnCode: "", //    
            searchReturnCode : "", //   
            selectedReturnReason:"", //   
            searchReturnReason : "", //    

            //  ( )
            inputListsearch: "", //

            //   
            noRowsOverlayComponent: 'CustomNoRowsOverlay',
            filteredOrderlist : [],
            theme : theme,
            rowData : [],
            columnOrderlist : [
            {headerName: "",
            headerCheckboxSelection: true,
            field: "check",
            resizable: false,
            editable: true,
            sortable: false,
            checkboxSelection: true,
            flex: 1,
            cellStyle: { textAlign: "center" }
            },
            { headerName : "", field:'returnlist_num',resizable: true, sortable: true,flex: 2,cellStyle: { textAlign: "right" }},
            { headerName : "", field:'output_num',resizable: true, sortable: true,flex: 2,cellStyle: { textAlign: "right" }},
            { headerName : "",field:'com_name',resizable: true, sortable: true,flex: 2,cellStyle: { textAlign: "left" }},
            { headerName : "LOT",field:'product_lot',resizable: true, sortable: true,flex: 3,cellStyle: { textAlign: "center" }},
            { headerName : "",field:'product_code',resizable: true, sortable: true,flex: 2,cellStyle: { textAlign: "center" }},
            { headerName : "",field:'product_name',resizable: true, sortable: true,flex: 3,cellStyle: { textAlign: "left" }},
            { headerName : ""
            ,field:'output_amount'
            ,resizable: true
            , sortable: true
            ,flex: 2
            ,cellStyle: { textAlign: "right" }
            ,cellRenderer: params =>{
                    if(params.value){
                        const formattedValue = params.value.toLocaleString(); //   
                        return `<span>${formattedValue}</span>`;
                    }
            }},
            { headerName : ""
            ,field:'return_num'
            ,editable:true
            ,resizable: true
            , sortable: true
            ,flex: 2
            ,cellStyle: { textAlign: "right",
                         //backgroundColor: "#fff", //  
                        //border: "0.5px dashed #fb8c00", //  
                        cursor: "text", //  
             }
            ,cellRenderer: params =>{
                    if(params.value){
                        const formattedValue = params.value.toLocaleString(); //   
                        return `
                                    <span style="display: flex; align-items: center; justify-content: flex-start;">
                                        <i class="fas fa-edit" style="color:#6c757d88" title="  "></i>
                                        <span style="flex-grow: 1; text-align: right;">${formattedValue}</span>
                                    </span>
                                `;
                    }else{
                         //       
                        return `<span><i class="fas fa-edit" style="color:#6c757d88" title="  "></i></span>`;
                    }
            }},
            { headerName : "",field:'name',resizable: true, sortable: true,flex: 2,cellStyle: { textAlign: "left" }},
            { headerName : "",field:'return_date',resizable: true, sortable: true,flex: 3,cellStyle: { textAlign: "center" }},
            { headerName : ""
            ,field:'return_reason'
            ,resizable: true
            , sortable: true
            ,flex: 4
            ,cellStyle: {
                         textAlign: "right",
                        //backgroundColor: "#fff", //  
                        //border: "0.5px dashed #fb8c00", //  
                        cursor: "text", //  
            }
            ,cellRenderer: params =>{
                    if(params.value){
                        const formattedValue = params.value.toLocaleString(); //   
                        return `
                                    <span style="display: flex; align-items: center; justify-content: text-start;">
                                        <span style="flex-grow: 1;  text-align: left;">${formattedValue}</span>
                                        <i class="fas fa-edit" style="color:#6c757d88" title="  "></i>
                                    </span>
                                `;
                    }else{
                         //       
                        return `<span><i class="fas fa-edit" style="color:#6c757d88" title="  "></i></span>`;
                    }
            }},
            ],
          


            isShowModal: {
            client: false, //  
            },

            
           

            
        }
    },
 

    methods:{
    //         
    onCellClicked(col) {
        if (col.colDef.editable) {
            if (!this.originalValues) {
                this.originalValues = {};
            }
            this.originalValues[col.node.id] = this.originalValues[col.node.id] || {};
            this.originalValues[col.node.id][col.column.colId] = col.value; //     
            //console.log("==",this.originalValues[col.node.id][col.column.colId]);
            //console.log("==",col);
        }
        // 'product_name'     
        if (col.colDef.field === 'return_reason') {
                this.openModal('returns');
            }
    },
    //         
    rollbackCellChange(col) {
        let nodeId = col.node.id;
        let colId = col.column.colId;
        if (this.originalValues && this.originalValues[nodeId] && this.originalValues[nodeId][colId] !== undefined) {
            let originalValue = this.originalValues[nodeId][colId];
            col.node.setDataValue(col.column,originalValue); //   
        }
    },
    // 
    rollbackEdit() {
        if (this.originalValues) {
            this.gridApi.forEachNode((node) => {
                //      
                Object.keys(this.originalValues[node.id] || {}).forEach(colId => {
                const originalValue = this.originalValues[node.id][colId];
                node.setDataValue(colId, originalValue); //     
                });
            });
        
            
        }
        if (this.selectedCol) {
        //  
        this.selectedCol.data.return_reason = this.originalReturnReason;
        this.selectedCol.data.return_code = this.originalReturnCode;

        // ag-Grid 
        this.gridApi.applyTransaction({
            update: [this.selectedCol.data] //   
        });

        //console.log("  :", this.originalReturnReason, this.originalReturnCode);
        }
        //  
        this.gridApi.deselectAll();
        this.originalValue = null; //     
        this.originalReturnReason = null;
        this.originalReturnCode = null;
      
    },

        selectclient(client){
            this.selectedCom = client; 
        },
        selectreturn(returns){
        this.selectedReturnCode = returns.return_code;
        this.selectedReturnReason = returns.return_reason;
        },
        
        openModal(modalType){
            this.isShowModal[modalType] = true; 
            //console.log(`${modalType} modal open`);

            // return_reason    
            if (modalType === 'returns') {
                // ag-Grid    
                const selectedNodes = this.gridApi.getSelectedNodes();
                if (selectedNodes.length > 0) {
                    this.selectedCol = selectedNodes[0];  //     .
                    //console.log(" :", this.selectedCol);
                    //,    
                    this.originalReturnReason = this.selectedCol.data.return_reason;
                    this.originalReturnCode = this.selectedCol.data.return_code;
                    //console.log( this.originalReturnReason,this.originalReturnCode)
                } else {
                    console.log("  .");
                }
            }
        },
        
        confirm(modalType){
            if (modalType === 'client') {
                this.searchCom = this.selectedCom;
            }else if (modalType === 'returns') {
                this.searchReturnCode = this.selectedReturnCode;
                this.searchReturnReason = this.selectedReturnReason;
                
                this.selectedCol.data.return_reason = this.searchReturnReason;
                this.selectedCol.data.return_code = this.searchReturnCode;

                // ag-Grid 
                this.gridApi.applyTransaction({
                    update: [this.selectedCol.data] //   
                });
            
                

            }
            this.closeModal(modalType); //  
        },
        closeModal(modalType) {
            this.isShowModal[modalType] = false;
        },
        // 
        resetSearch(){
            this.searchCom = ""; //      
            this.startDate = "";  //   
            this.endDate = "";    //   
            this.rowData =[];
            //console.log(this.orderData);

        },
        //
        async searchOrderlists(){
        this.filters  = {
            clientName : this.searchCom,
            startDate : this.startDate,
            endDate : this.endDate,
        }
        if( new Date(this.filters.startDate) > new Date(this.filters.endDate)){
          this.$notify({
                  text: `     . `,
                  type: 'error',
              });
          return;
        }
    

        //console.log(this.filters);

        let result = await axios.put(`${ajaxUrl}/return/search`,this.filters )
                                .catch(err => console.log(err));
        
        this.rowData = result.data;
        this.rowData = result.data.map((col) => ({
            ...col,
            return_date: this.dateFormat(col.return_date, "yyyy-MM-dd"),
            })
        );  
    },
    dateFormat(value, format) {
        return userDateUtils.dateFormat(value, format);
    },
            


    onReady(event){
            this.gridApi = event.api;
            //event.api.sizeColumnsToFit(); // api   
            //    
            const allPanels = document.querySelectorAll('.ag-paging-panel');
            const paginationPanel = allPanels[1];
            if (paginationPanel) {
               //  
               const container = document.createElement('div');
               container.style.display = 'flex';
               container.style.alignItems = 'center';
               container.style.gap = '5px'; //    

               //  
               const button1 = document.createElement('button');
               button1.textContent = '';
               button1.style.cursor = 'pointer';
               button1.style.backgroundColor = '#4caf50';
               button1.style.color = 'white';
               button1.style.border = 'none';
               button1.style.padding = '5px 10px';
               button1.style.borderRadius = '4px';

               const button2 = document.createElement('button');
               button2.textContent = '';
               button2.style.cursor = 'pointer';
               button2.style.backgroundColor = '#ff0000';
               button2.style.color = 'white';
               button2.style.border = 'none';
               button2.style.padding = '5px 10px';
               button2.style.borderRadius = '4px';

               const button3 = document.createElement('button');
               button3.textContent = '';
               button3.style.cursor = 'pointer';
               button3.style.backgroundColor = '#f48a06';
               button3.style.color = 'white';
               button3.style.border = 'none';
               button3.style.padding = '5px 10px';
               button3.style.borderRadius = '4px';

               //
                button1.addEventListener('click',()=>{
                    //
                    this.updateReturn();
                });

                button2.addEventListener('click',()=>{
                    //
                    this.deleteReturn();
                });
                button3.addEventListener('click',()=>{
                    //
                    this.rollbackEdit(); 
                })

                // 
                const inputText = document.createElement('input');
                inputText.type = 'text';
                inputText.placeholder = '';
                inputText.style.padding = '5px';
                inputText.style.width = '250px';
                inputText.style.border = '1px solid #ccc';
                inputText.style.borderRadius = '4px';

                //    ag 
                inputText.addEventListener('input',(event)=>{
                    const value = event.target.value;
                    //console.log(" :", value);

                    //
                    this.inputListsearch = value;
                });

                // ,   
                container.appendChild(button1);
                container.appendChild(button2);
                container.appendChild(button3);
                container.appendChild(inputText);

                //  
                paginationPanel.insertBefore(container,paginationPanel.firstChild);
            }

        },
        // onClickedRr(col){
        //     if (col.colDef.field === 'return_reason') {
        //         this.openModal('returns');
        //     }
        // },
        //
        async updateReturn(){
            const selectedRows = this.gridApi.getSelectedRows();
            let returnlistNums = []
            let returnNums = []
            let returnCodes = []

            //console.log("",selectedRows);

            //     
            for (const row of selectedRows) {
                if (row.return_num > row.output_amount) {
                    this.$notify({
                        text: `    `,
                        type: 'error',
                    });
                    return; //   
                } else {
                    returnlistNums.push(row.returnlist_num);
                    returnNums.push(row.return_num);
                    returnCodes.push(row.return_code);
                }
            }

            //,    
            let updateReturnInfo = {
                returnlistNum : JSON.stringify(Number(returnlistNums)),
                returnNum : JSON.stringify(Number(returnNums)),
                returnCode : JSON.stringify(returnCodes)
            }

            let result = await axios.put(`${ajaxUrl}/returnUpdate/update`,updateReturnInfo)
                                    .catch(err => console.log(err));
                                    
                if(result.data.result === true){
                        this.$notify({
                        text: `   . `,
                        type: 'success',
                    });
                    window.location.reload(); 
                }else{
                    this.$notify({
                        text: `    . `,
                        type: 'error',
                    });
                }
        },
        // 
        async deleteReturn(){
            const selectedRows = this.gridApi.getSelectedRows();
            let returnlistNums = []
            selectedRows.forEach(row => {
                returnlistNums.push(row.returnlist_num);
            });

            let deleteInfo = {
                returnlistNum : JSON.stringify(returnlistNums)
            }

            //console.log(deleteInfo);

            let result = await axios.put(`${ajaxUrl}/return/delete`,deleteInfo)
                                    .catch(err=> console.log(err));
                if(result.data.result === true){
                        this.$notify({
                        text: `   . `,
                        type: 'success',
                    });
                    window.location.reload(); 
                }else{
                    this.$notify({
                        text: `    . `,
                        type: 'error',
                    });
                }
        },
      
        
    
    },//method
    
};
</script>
<style lang="scss" scoped>
.main-container{
    background-color:  #e9ecef;
    border-radius: 10px;
    
}
.d-flex {
  display: flex;
  justify-content: space-between;
  //gap: 5px;
  text-align: center;
}

.custom-cell {
  padding: 2px !important;
}

.text-center {
  text-align: center;
}

.button {
  margin: 0 10px; /*     */
}

/*  input   */
input {
  background-color: #ffffff; /*   */
  border: solid 1px #ced4da; /*   */
  color: #495057; /*   */
}
/* readonly  input   */
input:read-only {
  background-color: #ffffff; /*    */
  color: #495057; /*    */
  cursor: not-allowed; /*      */
}

/* input     */
input:focus {
  background-color: #ffffff; /*      */
  border-color: #86b7fe; /*       */
  outline: none; /*      */
}

/* readonly input     */
input:read-only:focus {
  background-color: #ffffff; /*    */
  border-color: #ced4da; /* readonly    */
  outline: none; /*    */
}
</style>
