<template>
    <div class="container-fluid py-4">
    <!--  -->
    <h3></h3>
    <div class= "main-container ps-4">
        <div class= "pt-5 pb-5">
                <!-- -->
                <div class="row align-items-center mb-3">
                    <label class="col-sm-2 col-form-label fw-bold" ></label>
                    <div class="col-sm-4">
                        <input 
                            id="clientSearch"  class="form-control border p-2"
                            v-model="searchCom.com_name" @click="openModal('client')"  autocomplete="off" placeholder="" />
                            <Modal
                                :isShowModal="isShowModal.client"
                                :modalTitle="''"
                                :noBtn="''"
                                :yesBtn="''"
                                @closeModal="closeModal('client')"
                                @confirm="confirm('client')"
                            >
                            <template v-slot:list>
                                <ComList v-show="isShowModal.client" @selectclient="selectclient"/>
                            </template>
                            </Modal>
                    </div>    
                </div>
                  <!-- -->
                  <div class="row align-items-center mb-3">
                    <label class="col-sm-2 col-form-label fw-bold" ></label>
                    <div class="col-sm-4">
                        <input type="text" class="form-control" id="productName" placeholder=""
                            v-model="productName" @click="openModal('product')"  autocomplete="off"  >
                        <Modal
                                :isShowModal="isShowModal.product"
                                :modalTitle="''"
                                :noBtn="''"
                                :yesBtn="''"
                                @closeModal="closeModal('product')"
                                @confirm="confirm('product')"
                            >
                            <template v-slot:list>
                                <proList v-show="isShowModal.product" @selectproduct="selectproduct" :indexNum="indexNum"/>
                            </template>
                        </Modal>
                    </div>    
                </div>
                <!--  -->
                <div  class="row align-items-center mb-3">
                    <label class="col-sm-2 col-form-label fw-bold"></label>
                    <div class="col-sm-6">
                        <input 
                        id="orderSearch" class="form-control border p-2"
                        v-model="orderName"  autocomplete="off" placeholder="" />
                    </div>
                </div>
                <!--  -->
                <div class="row align-items-center mb-5">
                    <label class="col-sm-2 col-form-label fw-bold"></label>
                    <div class="col-sm-4">
                        <input 
                        type="date" 
                        id="startDate" class="form-control border p-2"
                        v-model="startDate"  autocomplete="off"  />
                    </div>
                    <div class="col-sm-1 text-center">~</div>
                    <div class="col-sm-4">
                        <input 
                        type="date" 
                        id="endDate" class="form-control border p-2"
                        v-model="endDate"  autocomplete="off"   />
                    </div>
                </div>
            </div>
                    <!--  -->
            <div class="pb-3 text-center">
                <material-button  color="success" class="button" @click="searchOrderlists"></material-button>
                <material-button  color="warning" class="button" @click="resetSearch"></material-button>
            </div>
        </div>
    </div>
    <!-- <div class="grid-container" v-show="rowData.length != 0"> -->
    <div class="grid-container">
        <ag-grid-vue
        style ="height: 600px; "
        :rowData="rowData"
        :columnDefs="columnOrderlist"
        :theme="theme"
        @grid-ready="onReady"
        :quickFilterText="inputListsearch"
        :noRowsOverlayComponent="noRowsOverlayComponent"
        @rowClicked="onOrderRowClicked"
        rowSelection="multiple"
        :pagination="true"
        :paginationPageSize="10"
        :paginationPageSizeSelector="[10, 20, 50, 100]"
    />
    </div>
    <div style="display: none">
        <CustomNoRowsOverlay/>
    </div>    
</template>

<script>
import MaterialButton from "@/components/MaterialButton.vue";
import Modal from "@/views/natureBlendComponents/modal/Modal.vue";
import proList from "@/views/sales/Order/ProductModal.vue"
import ComList from "@/views/sales/Order/clientModal.vue";
import theme from "@/utils/agGridTheme";
import userDateUtils from '@/utils/useDates.js';
import CustomNoRowsOverlay from "@/views/natureBlendComponents/grid/noDataMsg.vue";
import axios from "axios";
import { ajaxUrl } from '@/utils/commons.js';

export default{
    name :"orderlistSearch",
    components:{
            MaterialButton,
            Modal,
            proList,
            ComList,
            CustomNoRowsOverlay,
            
    },
    data(){
        return{
            

            //  
            orderName:"", // 
            startDate:"", //  
            endDate:"", //  
            //  
            searchCom:"", //     
            selectedCom: "", //  

            // 
            selectedProName:"",//  
            productName:'', //   
            selectedProCode:"", //   
            productCode:'', //   

          

            //   
            noRowsOverlayComponent: 'CustomNoRowsOverlay',
            filteredOrderlist : [],
            theme : theme,
            rowData : [],
            columnOrderlist : [
            { headerName : "", field:'output_num',flex: 2,cellStyle: { textAlign: "right" }},
            { headerName : "", field:'orderlist_title' ,flex: 4,cellStyle: { textAlign: "left" }},
            { headerName : "",field:'com_name' ,flex: 3,cellStyle: { textAlign: "left" }},
            { headerName : "",field:'product_code' ,flex: 2,cellStyle: { textAlign: "center" }},
            { headerName : "",field:'product_name' ,flex: 3,cellStyle: { textAlign: "left" }},
            { headerName : "LOT",field:'product_lot' ,flex: 3,cellStyle: { textAlign: "center" }},
            { headerName : "",
            field:'output_amount' ,
            flex: 2,
            cellStyle: { textAlign: "right" }
            ,cellRenderer: params =>{
                    if(params.value){
                        const formattedValue = params.value.toLocaleString(); //   
                        return `<span>${formattedValue}</span>`;
                    }
            }},
            { headerName : "",field:'output_date' ,flex: 3,cellStyle: { textAlign: "center" }},
            ],
           
            //  ( )
            inputListsearch: "", // 1 ()
    

            

           
         

            


            isShowModal: {
            client: false, //  
            emp: false, //  
            product : false, //
            },

            
           

            
        }
    },
 

    methods:{
        selectclient(client){
            this.selectedCom = client; 
        },
        selectproduct(product){
            //console.log(product); 
            this.selectedProCode = product.product_code;
            this.selectedProName = product.product_name;
        },
        openModal(modalType){
            this.isShowModal[modalType] = true; 
            //console.log(`${modalType} modal open`);
        },
        
        confirm(modalType){
            if (modalType === 'client') {
                this.searchCom = this.selectedCom;
            } else if (modalType === 'product') {
                this.productName = this.selectedProName;
                this.productCode = this.selectedProCode;
            }
            this.closeModal(modalType); //  
        },
        closeModal(modalType) {
            this.isShowModal[modalType] = false;
        },
        // 
        resetSearch(){
            this.searchCom = ""; //      
            this.orderName = "";  //      
            this.startDate = "";  //   
            this.endDate = "";    //   
            this.productName = "";
            this.productCode = "";
            this.orderData = {};  //  
            this.rowData =[];
            //console.log(this.orderData);

            this.searchEmpName="";
        },
        
        async searchOrderlists(){
        this.filters  = {
            clientName : this.searchCom,
            productCode : this.productCode,
            orderName : this.orderName,
            startDate : this.startDate,
            endDate : this.endDate,
        }
        if( new Date(this.filters.startDate) > new Date(this.filters.endDate)){
          this.$notify({
                  text: `     . `,
                  type: 'error',
              });
          return;
        }
    

        //console.log(this.filters);

        let result = await axios.put(`${ajaxUrl}/outputList/search`,this.filters )
                                .catch(err => console.log(err));
        
        this.rowData = result.data;
        this.rowData = result.data.map((col) => ({
            ...col,
            output_date: this.dateFormat(col.output_date, "yyyy-MM-dd"),
            })
        );  
    },
    dateFormat(value, format) {
        return userDateUtils.dateFormat(value, format);
    },
            


    onReady(event){
            this.gridApi = event.api;
            //event.api.sizeColumnsToFit(); // api   
            //    
            const allPanels = document.querySelectorAll('.ag-paging-panel');
            const paginationPanel = allPanels[2];
            if (paginationPanel) {
               //  
               const container = document.createElement('div');
               container.style.display = 'flex';
               container.style.alignItems = 'center';
               container.style.gap = '5px'; //    

               
                // 
                const inputText = document.createElement('input');
                inputText.type = 'text';
                inputText.placeholder = '';
                inputText.style.padding = '5px';
                inputText.style.width = '250px';
                inputText.style.border = '1px solid #ccc';
                inputText.style.borderRadius = '4px';

                //    ag 
                inputText.addEventListener('input',(event)=>{
                    const value = event.target.value;
                    //console.log(" :", value);

                    //
                    this.inputListsearch = value;
                });

                // ,   
                
                container.appendChild(inputText);

                //  
                paginationPanel.insertBefore(container,paginationPanel.firstChild);
            }

           


        },
       

       
    
    },//method
    
};
</script>
<style lang="scss" scoped>
.main-container{
    background-color:  #e9ecef;
    border-radius: 10px;
    
}
.d-flex {
  display: flex;
  justify-content: space-between;
  //gap: 5px;
  text-align: center;
}

.custom-cell {
  padding: 2px !important;
}

.text-center {
  text-align: center;
}

.button {
  margin: 0 10px; /*     */
}

/*  input   */
input {
  background-color: #ffffff; /*   */
  border: solid 1px #ced4da; /*   */
  color: #495057; /*   */
}
/* readonly  input   */
input:read-only {
  background-color: #ffffff; /*    */
  color: #495057; /*    */
  cursor: not-allowed; /*      */
}

/* input     */
input:focus {
  background-color: #ffffff; /*      */
  border-color: #86b7fe; /*       */
  outline: none; /*      */
}

/* readonly input     */
input:read-only:focus {
  background-color: #ffffff; /*    */
  border-color: #ced4da; /* readonly    */
  outline: none; /*    */
}
</style>
