<!-- 
     : >>   
-->
<template>
   <div class="p-3">
      <h3>&nbsp;&nbsp;  </h3>
   </div>
<!--    -->
<div class="main-container">
   <div class="content" style="height: 250px;">
      <form class="row gx-3 gy-2 align-items-center">
         <!--  -->
         <div class="col-sm-2">
            <label class="col-form-label fw-bold" for="materialCode"></label>
            <input type="text" class="form-control" style="background-color: white; padding-left: 20px;" id="materialCode" v-model="materialCode" autocomplete="off" @keydown.enter="enterkey">
         </div>

         <!--  -->
         <div class="col-sm-2">
            <label class="col-form-label fw-bold" for="clientName"></label>
            <input type="text" class="form-control" style="background-color: white; padding-left: 20px;" id="clientName" v-model="clientName" autocomplete="off" @keydown.enter="enterkey">
         </div>

         <!--  -->
         <div class="col-sm-2">
            <label class="col-form-label fw-bold" for="POListCode"></label>
            <input type="text" class="form-control" style="background-color: white; padding-left: 20px;" id="POListCode" v-model="POListCode" autocomplete="off" @keydown.enter="enterkey">
         </div>
      </form>

      <div class="row gx-3 gy-2 mt-2">
         <!--  -->
         <div class="col-sm-2">
            <label class="col-form-label fw-bold" for="startDate">()</label>
            <div class="input-group">
               <input type="date" class="form-control" style="background-color: white; padding-left: 20px;" id="startDate" v-model="startDate" @keydown.enter="enterkey">
            </div>
         </div>
         <div class="col-sm-2">
            <label class="col-form-label fw-bold" for="endDate">()</label>
            <div class="input-group">
               <input type="date" class="form-control" style="background-color: white; padding-left: 20px;" id="endDate" v-model="endDate" @keydown.enter="enterkey">
            </div>
         </div>
      </div>

      <!--   -->
      <div class="col-auto mt-1 text-center">
         <button type="button" class="btn me-5" style="background-color: #4caf50; color: white;" @click="seachPoList"></button>
         <button type="button" class="btn me-5" style="background-color: #fb8c00; color: white;" @click="reSet"></button>
         <button type="button" class="btn" style="background-color: #0077ff; color: white;" @click="onExportClick"></button>
      </div>
   </div>
</div>
<!--     -->
<div class="grid-container" style="padding-top: 10px;">
   <ag-grid-vue
      ref="gridRef"
      :rowData="rowData"
      :columnDefs="columnDefs"
      :theme="theme"
      :pagination="true"
      :paginationPageSize="9"
      :paginationPageSizeSelector="[9, 20, 50, 100]"
      @grid-ready="onReady"
      style="height: 471px;"
      rowSelection="multiple"
      :noRowsOverlayComponent="CustomNoRowsOverlay"
   >
   </ag-grid-vue>
</div>
<div>
   <Modal :isShowModal="isShowModal" :newObject="newObject"  @closeModal="closeModal" @confirm="confirm"></Modal>
</div>
<div>
    <InspectionModal :isShowModal2="isShowModal2" :inspection_data="inspection_data" @closeModal2="closeModal2" @confirm2="confirm2">
    </InspectionModal>
</div>
</template>
<script setup>
import axios from 'axios';
import { ajaxUrl } from '@/utils/commons.js';
import userDateUtils from '@/utils/useDates.js';

import { useNotification } from "@kyvg/vue3-notification";  // 
const { notify } = useNotification();  //  

import Modal from "@/views/material/materialInputListModal.vue";
import InspectionModal from "@/views/material/inspection_com.vue";

import CustomNoRowsOverlay from "@/views/natureBlendComponents/grid/noDataMsg.vue";

import theme from "@/utils/agGridTheme";
import { ref, onBeforeMount } from 'vue'; //onBeforeMount

 //  
 import { useRouter } from 'vue-router';
const router = useRouter();
//   
import { useStore } from 'vuex'; // Vuex  
const store = useStore();
const loginfo = ref({});
const loginInfo = () => {
   loginfo.value = store.state.loginInfo;
   if(loginfo.value.job === '' || loginfo.value.position === ''){
      console.log(loginfo.value.job);
   }else{
      notify({
         //title: "",
         text: "    .",
         type: "error", // success, warn, error 
      });
      router.push({ name: 'MainPage' });
   }
};

const materialCode = ref();   //
const clientName = ref();  //
const POListCode = ref();  //
const startDate = ref();   //
const endDate = ref();  //
const seachcondition = ref({}); //  

// 
const reSet = () => {
  materialCode.value = '';
  clientName.value = '';
  POListCode.value = '';
  startDate.value = '';
  endDate.value = '';

  seachcondition.value = {
   materialCode: '',
   clientName: '',
   POListCode: '',
   startDate: '',
   endDate: ''
  }
  matrialInputList();   //   

  //this.$notify({ text: '   .', type: 'error' });

//   notify({
//       title: "",
//       text: "  .",
//       type: "success", // success, warn, error 
//    });
};


// AG Grid Vue  
const gridRef = ref(null);
// CSV  
const onExportClick = () => {
  if (gridRef.value) {
    const gridApi = gridRef.value.api; // AG Grid API
    gridApi.exportDataAsCsv({
      fileName: "__.csv",
    });
  }
};


//     
const inspection_data = ref({});
//   
const inspection_com = (data) => {
  //     
  inspection_data.value = {
    order_code: data.order_code,
    material_name: data.material_name,
    com_name: data.com_name,
    ord_qty: data.ord_qty,
    total_qnt: data.total_qty,
    pass_qnt: data.pass_qty,
    rjc_qnt: data.reject_qty,
  };
  console.log('  : ',inspection_data.value);
  // 
  isShowModal2.value = true;
};
//   
const isShowModal2 = ref(false);
 //  
 const closeModal2 = () => {
  isShowModal2.value = false;
};
 //  
 const confirm2 = () => {
  isShowModal2.value = false;
};



// 
const seachPoList = () => {
  console.log(materialCode.value);
  console.log(clientName.value);
  console.log(POListCode.value);
  console.log(startDate.value);
  console.log(endDate.value);

  seachcondition.value = {
   materialCode: materialCode.value,
   clientName: clientName.value,
   POListCode: POListCode.value,
   startDate: startDate.value,
   endDate: endDate.value
  }
  matrialInputList();
};

  // api   
const columnDefs = ref([
   { headerName: "No.", field: "input_num", width:80, cellStyle: { textAlign: "center" } },
   { headerName: "", field: "order_code", width:150, cellStyle: { textAlign: "center" } },
   { headerName: "", field: "material_name", cellStyle: { textAlign: "left" } },
   { headerName: "", field: "com_name", cellStyle: { textAlign: "left" } },
   { headerName: "", field: "ord_qty", width:120, cellStyle: { textAlign: "right" } },
   { headerName: "", field: "in_qty", width:120, cellStyle: { textAlign: "right" } },
   { headerName: "", field: "pass_qty", width:120, cellStyle: { textAlign: "right" } },
   { headerName: "", field: "pass_warehouse_name", width:110, cellStyle: { textAlign: "left" } },
   { headerName: "", field: "reject_qty", width:120, cellStyle: { textAlign: "right" } },
   { headerName: "", field: "reject_warehouse_name", width:110, cellStyle: { textAlign: "left" } },
   { headerName: "", field: "inset_lot_date", width:140, cellStyle: { textAlign: "center" } },
   { headerName: "", field: "name", width:130, cellStyle: { textAlign: "center" } },
   {  
      headerName: "", 
      field: "", 
      width:130,
      editable: false,
      cellRenderer: params => {
      const div = document.createElement('div');
      div.style.display = 'flex';
      div.style.justifyContent = 'center';
      div.style.alignItems = 'center';
      div.style.height = '100%';
         
      const button = document.createElement('button');
      button.innerText = '';
      button.style.cursor = 'pointer';
      button.style.backgroundColor = '#fb8c00';
      button.style.width = '60px';
      button.style.height = '30px';
      button.style.color = 'white';
      button.style.border = 'none';
      button.style.borderRadius = '4px';
      button.style.display = 'flex';
      button.style.justifyContent = 'center';
      button.style.alignItems = 'center';
      button.addEventListener('click', () => {
         console.log("  : ", JSON.stringify(params.data));
         inspection_com(params.data);
      });
      div.appendChild(button);
      return div;
      //return button;
      }
   },
   {  
      headerName: "LOT", 
      field: "LOT", 
      width:130,
      editable: false,
      cellRenderer: params => {
      const div = document.createElement('div');
      div.style.display = 'flex';
      div.style.justifyContent = 'center';
      div.style.alignItems = 'center';
      div.style.height = '100%';

      const button2 = document.createElement('button');
      button2.innerText = '';
      button2.style.cursor = 'pointer';
      button2.style.backgroundColor = '#4caf50';
      button2.style.width = '60px';
      button2.style.height = '30px';
      button2.style.color = 'white';
      button2.style.border = 'none';
      button2.style.borderRadius = '4px';
      button2.style.display = 'flex';
      button2.style.justifyContent = 'center';
      button2.style.alignItems = 'center';
      button2.addEventListener('click', () => {
         //console.log("  : ", JSON.stringify(params.data));
         //  
         lotinfo(params.data);
      });
      div.appendChild(button2);
      return div;
      //return button2;
      }
   },
]);

const lotinfodata = ref([]);

 //  
 const isShowModal = ref(false);

 //   
 const newObject = ref([]);

//   
const lotinfo = async (lotdata) =>{
   console.log(lotdata);
   lotinfodata.value = await lotInfoAxios(lotdata.input_num);

   //console.log(lotinfodata.value);
   newObject.value = {
      material_name: lotdata.material_name,
      lot_code: lotinfodata.value[0]['lot_code'],
      //pass_stok_qty: ( lotinfodata.value[0]['pass_stok_qty'] * 0.001 ) + lotdata.material_name.includes('') ? ' ' : ' kg',
      pass_stok_qty: lotdata.material_name.includes('') ? ( lotinfodata.value[0]['pass_stok_qty'] ).toLocaleString()+' ' : ( lotinfodata.value[0]['pass_stok_qty'] * 0.001 ).toLocaleString()+' kg',
      //reject_stok_qty: ( lotinfodata.value[0]['reject_stok_qty'] * 0.001 ) + ' kg',
      reject_stok_qty: lotdata.material_name.includes('') ? ( lotinfodata.value[0]['reject_stok_qty']  ).toLocaleString()+' ' : ( lotinfodata.value[0]['reject_stok_qty'] * 0.001 ).toLocaleString()+' kg',
   };
   console.log('newObject ',newObject);

   // 
   isShowModal.value = true;
};

// LOT   
const lotInfoAxios = async function(input_num){
   let result = await axios.get(`${ajaxUrl}/material/lotinfo/${input_num}`)
                      .catch(err=>console.log(err));
   return result.data;
};

//  
const confirm = () => {
  console.log("   ");
  isShowModal.value = false; //  
};

 //  
 const closeModal = () => {
  isShowModal.value = false;
};


//   
const enterkey = (event) => {
   event.preventDefault();
   seachPoList();
};

//   api 
const rowData = ref([]);

const onReady = (param) => {
   param.api.sizeColumnsToFit(); // api   
}

//   
onBeforeMount(()=>{
  matrialInputList();   //  
  loginInfo();
});


//    ( )
const matrialInputList = async function(){
  let result = await axios.put(`${ajaxUrl}/material/materialInputList`, seachcondition.value)
                      .catch(err=>console.log(err));
   console.log(result.data);
   rowData.value = result.data.map((col) => ({
      ...col,
      //ord_qty: (col.ord_qty * 0.001) + " kg",
      ord_qty: col.material_name.includes('') ? (Number(col.ord_qty)).toLocaleString() + " " : (Number(col.ord_qty) * 0.001).toLocaleString() + " kg",
      //in_qty: (col.in_qty * 0.001) + " kg",
      in_qty: col.material_name.includes('') ? (Number(col.in_qty)).toLocaleString() + " " : (Number(col.in_qty) * 0.001).toLocaleString() + " kg",
      //pass_qty: (col.pass_qty * 0.001) + " kg",
      pass_qty: col.material_name.includes('') ? (Number(col.pass_qty)).toLocaleString() + " " : (Number(col.pass_qty) * 0.001).toLocaleString() + " kg",
      //reject_qty: (col.reject_qty * 0.001) + " kg",
      reject_qty: col.material_name.includes('') ? (Number(col.reject_qty)).toLocaleString() + " " : (Number(col.reject_qty) * 0.001).toLocaleString() + " kg",
      inset_lot_date: userDateUtils.dateFormat(col.inset_lot_date, "yyyy-MM-dd")
    })
  );
}

</script>
<style lang="scss" scoped>
.main-container{
    background-color:  #e9ecef;
    margin-left: 20px;
    margin-right: 20px;
    margin-top: 0px;
    margin-bottom: 0px;
    border-radius: 10px;
}
.content{
   margin-left: 20px;
   margin-right: 20px;
   margin-top: 0px;
   margin-bottom: 0px;
   padding: 0px;
}
input {
      background-color: $white;
      border: solid 1px  ;
}
</style>
