<!--    -->
<template>
  <div class="partList container-fluid py-4">
    <div>
       <h3>&nbsp;&nbsp; </h3>
    </div>
 <!--    -->
 <div class="main-container">
    <div class="content" style="height: 130px; margin: 20px;">
       <form class="row gx-3 gy-2 align-items-center">
          <!--  -->
          <div class="col-sm-2">
             <label class="col-form-label fw-bold" for="processCode"></label>
             <input type="text" class="form-control" style="background-color: white; padding-left: 20px;" id="processCode" v-model="processCode" >
          </div>
 
          <!--  -->
          <div class="col-sm-2">
             <label class="col-form-label fw-bold" for="processName"></label>
             <input type="text" class="form-control" style="background-color: white; padding-left: 20px;" id="processName" v-model="processName" >
          </div>
 
          <!--  -->
          <div class="col-sm-2">
             <label class="col-form-label fw-bold" for="machineType"></label>
             <input type="text" class="form-control" style="background-color: white; padding-left: 20px;" id="machineType" v-model="machineType" >
          </div>
 
          <!--   -->
          <div class="col-sm-3">
              <button style="position:relative; top:29px;" type="button" class="btn btn-success me-5" @click="upin? input_update(2) : input_update(1)">/</button>
              <button style="position:relative; width:120px ; top:29px;" type="button" class="btn btn-warning me-5" @click="refresh"  ></button>
          </div>
       </form>
    </div>
 </div>
 <!--     -->
 <div class="grid-container" style="padding-top: 10px;">
    <ag-grid-vue
       :rowData="rowData"
       :columnDefs="columnDefs"
       :theme="theme"
       :pagination="true"
       :paginationPageSize="10"
       @grid-ready="onReady"
       style="height: 513px;"
       rowSelection="multiple"
       @cellClicked="onCellClicked"
    >
    </ag-grid-vue>
 </div>
 </div>
 </template>
 <script>
 import axios from 'axios';
 import { ajaxUrl } from '@/utils/commons.js';
 // import userDateUtils from '@/utils/useDates.js';
 import theme from "@/utils/agGridTheme";
 import { mapMutations } from "vuex";

 
 export default {
   data() {
     return {
       processCode: '',   // 
       processName: '',  // 
       machineType: '',  // 
       columnDefs: [
         { headerName: "", field: "process_code", width: 220 },
         { headerName: "", field: "process_name" },
         { headerName: "", field: "machine_type" },
         {
           headerName: "",
           field: "",
           cellStyle: { textAlign: 'center' },
           upin : '',
           editable: false,
           cellRenderer: (params) => {
             const button2 = document.createElement('button');
             button2.innerText = '';
             button2.style.marginRight = '10px';
             button2.style.cursor = 'pointer';
             button2.style.backgroundColor = '#f7b84d';
             button2.style.width = '60px';
             button2.style.height = '30px';
             button2.style.color = 'white';
             button2.style.border = 'none';
             button2.style.padding = '0';
             button2.style.borderRadius = '4px';
             button2.style.textAlign = 'center';
             button2.style.lineHeight = '30px';
             button2.addEventListener('click', () => {
               console.log(" [] : ", JSON.stringify(params.data));
               console.log("  : ", params.data.process_code);
               if(confirm(" ?")){
                    axios.delete(`${ajaxUrl}/processDelete/${params.data.process_code}`)
                    .then(res => {
                        if(res.data === ''){
                            this.$notify({ text: ' .', type: 'success' });
                            this.processList();
                        }else{
                            this.$notify({ text: '.', type: 'error' });
                        }
                    })
                    .catch(err => console.log(err));
               }
               //   
               // lotinfo(params.data);
             });
             return button2;
           }
         },
       ],
       newList: [], //     
       rowData: [], //    api 
       theme:theme,
     };
   },
   methods: {
    refresh(){
        this.processList();
        this.upin = '';
        this.processCode = '';
        this.processName = '';
        this.machineType = '';
    },
    ...mapMutations(["addLoginInfo"]),
      async checkLogin(){
          this.loginInfo = this.$store.state.loginInfo;
          console.log('',this.loginInfo);
          if(this.loginInfo.job === ''){
            console.log('');
          }else{
              this.$notify({ text: '  ', type: 'error' });
              this.$router.push({ name : 'MainPage' });
          }
      },
    async processList(){
        const result = await axios.get(`${ajaxUrl}/processList`)
                                          .catch(err => console.log(err));
        console.log(result.data);
        this.rowData = result.data;
        console.log(this.rowData);
    },
    onReady(param) {
       param.api.sizeColumnsToFit(); //  api      
    },
    onCellClicked(params) {
        if(params.colDef.field !== ''){
            console.log(" [] : ", JSON.stringify(params.data));
            this.processCode = params.data.process_code;
            this.processName = params.data.process_name;
            this.machineType = params.data.machine_type;
            this.upin = 'update';
        }
    },
     async input_update(number) {
       console.log('    ');
        console.log(this.processCode);
        console.log(this.processName);
        console.log(this.machineType);
        if(number === 1){
            if(this.processCode === '' || this.processName === '' || this.machineType === ''){
                this.$notify({ text: ' .', type: 'error' });
                return;
            }else{
                console.log('');
                let dataresult = 'ok';
                // 
                for(let i = 0; i < this.rowData.length; i++){
                    if(this.rowData[i].process_code === this.processCode){
                        this.$notify({  text: ' .', type: 'error' });
                        dataresult = 'no';
                        return;
                    }
                }
                if(dataresult === 'ok'){
                    this.newList = {
                        process_code: this.processCode,
                        process_name: this.processName,
                        machine_type: this.machineType,
                    }
                    console.log(this.newList);
                    this.processInsert(this.newList);
                return
                }
            }
        }else if(number === 2){
            console.log('');
            this.newList = {
                process_code: this.processCode,
                process_name: this.processName,
                machine_type: this.machineType,
            }
            this.processUpdate(this.newList);
        }
     },
     async processInsert(newList){
        const result = await axios.post(`${ajaxUrl}/processInsert`, newList)
                                    .catch(err => console.log(err));
            if(result.data === ''){
                this.$notify({ text: ' .', type: 'success' });
                this.processList();
            }else{
                this.$notify({ text: '.', type: 'error' });
            }
     },
        async processUpdate(newList){
            const result = await axios.post(`${ajaxUrl}/processUpdate/${this.processCode}`, newList)
                if(result.data === ''){
                    this.$notify({ text: ' .', type: 'success' });
                    this.processList();
                    this.upin = '';
                    this.processCode = '';
                    this.processName = '';
                    this.machineType = '';
                }else{
                    this.$notify({ text: '.', type: 'error' });
                }
        },
   },
   //   
   mounted() {
    this.checkLogin();
    this.processList();
     //   
   },
 };
 </script>
 
 <style lang="scss" scoped>
 .main-container{
     background-color:  #e9ecef;
     margin-left: 20px;
     margin-right: 20px;
     margin-top: 0px;
     margin-bottom: 0px;
     border-radius: 10px;
 }
 .content{
    margin-left: 20px;
    margin-right: 20px;
    margin-top: 0px;
    margin-bottom: 0px;
    padding: 0px;
 }
 input {
       background-color: $white;
       border: solid 1px  ;
 }
 input:focus {
  background-color: #ffffff; /*      */
  border-color: #86b7fe; /*       */
  outline: none; /*      */
}
 </style>