<template>
    <input type="text" v-model="inputListsearch" class="text-center styled-input" placeholder=" " />
    <div class="grid-container">
        <ag-grid-vue
            :rowData="rowData"
            :columnDefs="columnMateriallist"
            :theme="theme"
            @grid-ready="onReady"
            :quickFilterText="inputListsearch"
            :noRowsOverlayComponent="noRowsOverlayComponent"
            @rowClicked="selectMaterial"
            rowSelection="single"
        />
    </div>
    <div style="display: none">
        <CustomNoRowsOverlay/>
    </div>
</template>
<script>
import axios from "axios";
import { ajaxUrl } from '@/utils/commons.js';
import theme from "@/utils/agGridTheme";

export default{
    name : "MaterialList",

    data(){
        return{
            rowData:[], //  
            theme : theme,
            selectedCom : "", //   
            columnMateriallist : [
                {headerName : "", field:"material_code",resizable: true, sortable: true, flex: 2, cellStyle: { textAlign: "center" }},
                {headerName : "", field:"material_name",resizable: true, sortable: true, flex: 3, cellStyle: { textAlign: "left" }},
                {headerName : "", field:"safety_inventory",resizable: true, sortable: true, flex: 2, 
                    cellStyle: { textAlign: "right" },
                    cellRenderer: params => {
                        if (params.value) {
                            if(params.data.material_name.includes('')){
                                const formatted_qty = params.value.toLocaleString()+' ';
                                return `<span style="text-align: right;">${formatted_qty}</span>`;
                            }else{
                                const formatted_qty = params.value.toLocaleString()+' kg';
                                return `<span style="text-align: right;">${formatted_qty}</span>`;
                            }
                        } else {
                            return `<span style="text-align: right;"></span>`;
                        }
                    },

                },
                {headerName : "", field:"stok_qty",resizable: true, sortable: true, flex: 2, 
                    cellStyle: { textAlign: "right" },
                    cellRenderer: params => {
                        if (params.value) {
                            if(params.data.material_name.includes('')){
                                const formatted_t_qty = params.value.toLocaleString()+' ';
                                return `<span style="text-align: right;">${formatted_t_qty}</span>`;
                            }else{
                                const formatted_t_qty = params.value.toLocaleString()+' kg';
                                return `<span style="text-align: right;">${formatted_t_qty}</span>`;
                            }
                        } else {
                            return `<span style="text-align: right;"></span>`;
                        }
                    },
                },
            ],
            //  ( )
            inputListsearch: "", // 1 ()
        }
    },
    created(){
        this.getMaterialList()
    },
    methods: {
        async getMaterialList(){
            let result = await axios.get(`${ajaxUrl}/material/material_list`) //
                                    .catch(err=> console.log(err));
            //this.rowData = result.data; // 
            // this.rowData = result.data.map((col) => ({
            //     ...col,
            //     stok_qty: col.material.includes('') ? Number(col.stok_qty).toLocaleString() : (col.stok_qty * 0.001).toLocaleString(),
            //     safety_inventory: col.material.includes('') ? Number(col.safety_inventory).toLocaleString() : (col.safety_inventory * 0.001).toLocaleString(),
            // }));
            this.rowData = result.data.map((col) => ({
                ...col,
                stok_qty: col.material_name && col.material_name.includes('')
                    ? Number(col.stok_qty).toLocaleString()
                    : (col.stok_qty * 0.001).toLocaleString(),
                safety_inventory: col.material_name && col.material_name.includes('')
                    ? Number(col.safety_inventory).toLocaleString()
                    : (col.safety_inventory * 0.001).toLocaleString(),
            }));


        },

        selectMaterial(event){
            const Material = event.data;
            //console.log(Material);
            this.$emit('selectmaterial',Material);
        },
        onReady(event){
            this.gridApi = event.api;
            event.api.sizeColumnsToFit(); // api   
            //    
            const allPanels = document.querySelectorAll('.ag-paging-panel');
            const paginationPanel = allPanels[0];
            if (paginationPanel) {
               //  
               const container = document.createElement('div');
               container.style.display = 'flex';
               container.style.alignItems = 'center';
               container.style.gap = '5px'; //    

               
                // 
                const inputText = document.createElement('input');
                inputText.type = 'text';
                inputText.placeholder = '';
                inputText.style.padding = '5px';
                inputText.style.width = '250px';
                inputText.style.border = '1px solid #ccc';
                inputText.style.borderRadius = '4px';

                //    ag 
                inputText.addEventListener('input',(event)=>{
                    const value = event.target.value;
                    //console.log(" :", value);

                    //
                    this.inputListsearch = value;
                });

                // ,   
                container.appendChild(inputText);
                
            }
        },
    }
}
    
    
</script>
<style scoped lang="scss">
.card {
  box-shadow: none;
}
.emp:hover {
  background-color: $main3;
  cursor: pointer;
  transition: all .1s ease-in-out;
  h6, p {
    color: $white;
  }

}
.selected {
  background-color: $main1;
  h6, p {
    color: $white;
  }
}

.styled-input {
    display: block; /*      */
    margin: 0 auto; /*      */
    margin-top: 10px;
    padding: 10px; /*   */
    width: 250px; /*    */
    text-align: center; /*    */
    border: 1px solid #ccc; /*  */
    border-radius: 20px; /*   */
    outline: none; /*      */
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); /*   */
    transition: box-shadow 0.3s ease; /*    */
}

/*      */
.styled-input:focus,
.styled-input:hover {
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2); /*    */
    border-color: #007bff; /*      */
}
</style>