                    <th class="align-middle text-center text-xxs font-weight-bolder opacity-7">
                      <label><input type="checkbox" v-model="allSelected2" @click="click_all2"/></label>
                    </th>
                    <th class="align-middle text-center text-xxs font-weight-bolder opacity-7">
                      
