<!--  -->
<template>
   <div class="partList container-fluid py-4">
    <div>
       <h3>&nbsp;&nbsp; </h3>
    </div>
 <!--    -->
 <div class="main-container">
    <div class="content" style="height: 130px; margin: 20px;">
       <form class="row gx-3 gy-2 align-items-center">
          <!--  -->
          <div class="col-sm-2">
             <label class="col-form-label fw-bold" for="returnCode"></label>
             <input type="text" class="form-control" style="background-color: white; padding-left: 20px;" id="returnCode" v-model="returnCode" >
          </div>
 
          <!--  -->
          <div class="col-sm-2">
             <label class="col-form-label fw-bold" for="returnReason"></label>
             <input type="text" class="form-control" style="background-color: white; padding-left: 20px;" id="returnReason" v-model="returnReason" >
          </div>

          <!--   -->
          <div class="col-sm-3">
             <button style="position:relative; top:29px;" type="button" class="btn btn-success me-5" @click="upin? input_update(2) : input_update(1)">/</button>
             <button style="position:relative; width:120px ; top:29px;" type="button" class="btn btn-warning me-5" @click="refresh"  ></button>
          </div>
       </form>
    </div>
 </div>
 <!--     -->
 <div class="grid-container" style="padding-top: 10px;">
    <ag-grid-vue
       :rowData="rowData"
       :columnDefs="columnDefs"
       :theme="theme"
       :pagination="true"
       :paginationPageSize="10"
       @grid-ready="onReady"
       style="height: 513px;"
       rowSelection="multiple"
       @cellClicked="onCellClicked"
    >
    </ag-grid-vue>
 </div>
 </div>
 </template>
 <script>
 import axios from 'axios';
 import { ajaxUrl } from '@/utils/commons.js';
 // import userDateUtils from '@/utils/useDates.js';
 import theme from "@/utils/agGridTheme";
 import { mapMutations } from "vuex";
 
 export default {
   data() {
     return {
       returnCode: '',   // 
       returnReason: '',  // 
       columnDefs: [
         { headerName: "", field: "return_code", width: 220 },
         { headerName: "", field: "return_reason" },
         {
           headerName: "",
           field: "",
           cellStyle: { textAlign: 'center' },
           editable: false,
           cellRenderer: (params) => {
             const button2 = document.createElement('button');
             button2.innerText = '';
             button2.style.marginRight = '10px';
             button2.style.cursor = 'pointer';
             button2.style.backgroundColor = '#f7b84d';
             button2.style.width = '60px';
             button2.style.height = '30px';
             button2.style.color = 'white';
             button2.style.border = 'none';
             button2.style.padding = '0';
             button2.style.borderRadius = '4px';
             button2.style.textAlign = 'center';
             button2.style.lineHeight = '30px';
             button2.addEventListener('click', () => {
               console.log(" [] : ", JSON.stringify(params.data));
               if(confirm(" ?")){
                    axios.delete(`${ajaxUrl}/returnDelete/${params.data.return_code}`)
                    .then(res => {
                        if(res.data === ''){
                           this.$notify({ text: ' .', type: 'success' });
                            this.returnList();
                        }else{
                           this.$notify({ text: '.', type: 'error' });
                        }
                    })
                    .catch(err => console.log(err));
               }
               //   
               // lotinfo(params.data);
             });
             return button2;
           }
         },
       ],
       newList: [],
       rowData: [], //    api 
       theme:theme,
     };
   },
   methods: {
      refresh(){
         this.returnList();
         this.upin = '';
         this.returnCode = '';
         this.returnReason = '';
      },
      ...mapMutations(["addLoginInfo"]),
      async checkLogin(){
          this.loginInfo = this.$store.state.loginInfo;
          console.log('',this.loginInfo);
          if(this.loginInfo.job === ''){
            console.log('');
          }else{
              this.$notify({ text: '  ', type: 'error' });
              this.$router.push({ name : 'MainPage' });
          }
      },
    async returnList(){ // 
        const result = await axios.get(`${ajaxUrl}/returnList`)
                                    .catch(err => console.log(err));
        this.rowData = result.data;
    },
     onReady(param) {
       param.api.sizeColumnsToFit(); //  api      
     },
     onCellClicked(params){
        if(params.colDef.field !== ''){
            console.log(" [] : " , JSON.stringify(params.data));
            this.returnCode = params.data.return_code;
            this.returnReason = params.data.return_reason;
            this.upin = 'update';
        }
     },
     async returnInsert(newList){
        const result = await axios.post(`${ajaxUrl}/returnInsert`, newList)
                                  .catch(err => console.log(err));
                                  console.log(result.data);
            this.returnList();

     },
     input_update() {
       console.log('    ');
       if(this.returnCode === '' || this.returnReason === ''){
         this.$notify({ text: ' .', type: 'error' });
           return;
       }
       this.newList = {
        return_code : this.returnCode,
        return_reason : this.returnReason
       };
       if(this.upin === 'update'){
            this.$notify({ text: '.', type: 'success' });
            this.returnInsert(this.newList);
         } else {
            for(let i = 0; i < this.rowData.length; i++){
               if(this.rowData[i].return_code === this.returnCode){
                  this.$notify({ text: '  .', type: 'error' });
                  return;
               }
            }
            this.$notify({ text: '.', type: 'success' });
            this.returnInsert(this.newList);
         }
      //  this.returnInsert(this.newList);
       this.returnList();
     },
   },
   //   
   mounted() {
     //   
     this.checkLogin();
     this.returnList();
   },
 };
 </script>
 
 <style lang="scss" scoped>
 .main-container{
     background-color:  #e9ecef;
     margin-left: 20px;
     margin-right: 20px;
     margin-top: 0px;
     margin-bottom: 0px;
     border-radius: 10px;
 }
 .content{
    margin-left: 20px;
    margin-right: 20px;
    margin-top: 0px;
    margin-bottom: 0px;
    padding: 0px;
 }
 input {
       background-color: $white;
       border: solid 1px  ;
 }
 input:focus {
  background-color: #ffffff; /*      */
  border-color: #86b7fe; /*       */
  outline: none; /*      */
}
 </style>