<template>
    <div class="container-fluid py-4">
    <!--  -->
        <h3></h3>
        <div class= "main-container ps-4">
            <div class= "pt-5 pb-5">
                    <!-- -->
                    <div class="row align-items-center mb-3">
                        <label class="col-sm-2 col-form-label fw-bold" ></label>
                        <div class="col-sm-4">
                            <input type="text" class="form-control" id="productName" 
                                v-model="productName" @click="openModal('product')"  autocomplete="off" placeholder=" " >
                                <Modal
                                        :isShowModal="isShowModal.product"
                                        :modalTitle="''"
                                        :noBtn="''"
                                        :yesBtn="''"
                                        @closeModal="closeModal('product')"
                                        @confirm="confirm('product')"
                                    >
                                    <template v-slot:list>
                                        <proList v-show="isShowModal.product" @selectproduct="selectproduct"/>
                                    </template>
                                </Modal>
                        </div>    
                    </div>
                    <!--  -->
                    <div class="row align-items-center mb-5">
                        <label class="col-sm-2 col-form-label fw-bold"> </label>
                        <div class="col-sm-4">
                            <input 
                            type="date" 
                            id="startDate" class="form-control border p-2"
                            v-model="startDate"  autocomplete="off"  />
                        </div>
                        <div class="col-sm-1 text-center">~</div>
                        <div class="col-sm-4">
                            <input 
                            type="date" 
                            id="endDate" class="form-control border p-2"
                            v-model="endDate"  autocomplete="off"   />
                        </div>
                    </div>
                </div>
                        <!--  -->
                <div class="pb-3 text-center">
                    <material-button  color="success" class="button" @click="searchInputlist" ></material-button>
                    <material-button color="warning" class="button" @click="resetSearch"></material-button>
                </div>
            </div>
        </div>



        <div class="container-fluid py-4">
            <div class="">
                <!--    -->
                <div class="grid-container">
                    <ag-grid-vue
                    style ="height: 500px;"
                    :rowData="inputData"
                    :columnDefs="columnInputData"
                    :theme="theme"
                    @grid-ready="onReady"
                    :quickFilterText="inputListsearch"
                    :noRowsOverlayComponent="noRowsOverlayComponent"
                    @cellClicked="onCellClicked" 
                    rowSelection="multiple"
                    :pagination="true"
                    :paginationPageSize="10"
                    :paginationPageSizeSelector="[10, 20, 50, 100]"
                />
                <!--@cellClicked="onClickedWh"       cellClicked 
                 :quickFilterText="inputListsearch"     
                  rowSelection="multiple"     -->
                </div>
                <div style="display: none">
                    <CustomNoRowsOverlay/>
                </div>
        </div>
    </div>
    <div>
        <Modal
            :isShowModal="isShowModal.warehouse"
            :modalTitle="''"
            :noBtn="''"
            :yesBtn="''"
            @closeModal="closeModal('warehouse')"
            @confirm="confirm('warehouse')"
        >
        <template v-slot:list>
            <WarList v-show="isShowModal.warehouse" @selectwarehouse="selectwarehouse"/>
        </template>
        </Modal>
    </div>
        
    
</template>

<script>
import MaterialButton from "@/components/MaterialButton.vue";
import Modal from "@/views/natureBlendComponents/modal/Modal.vue";
import proList from "@/views/sales/Order/ProductModal.vue"
import WarList from "@/views/sales/ProductInput/warehouseModal.vue";
import theme from "@/utils/agGridTheme";
import userDateUtils from '@/utils/useDates.js';
import CustomNoRowsOverlay from "@/views/natureBlendComponents/grid/noDataMsg.vue";
import axios from "axios";
import { ajaxUrl } from '@/utils/commons.js';

export default{
    name :"inputList",
    components:{
            MaterialButton,
            Modal,
            proList,
            WarList,
            CustomNoRowsOverlay,
            
    },
    data(){
        return{
            //  
            startDate:"", //  
            endDate:"", //  
            // 
            selectedProName:"",//  
            productName:'', //   
            selectedProCode:"", //   
            productCode:'', //   

            //  ( )
            inputListsearch: "", //

            //
            originWarehouseName : null,

          

            //    
            noRowsOverlayComponent: 'CustomNoRowsOverlay',
            theme : theme,
            inputData : [],
            columnInputData : [
            { headerName: "",
              headerCheckboxSelection: true,
              field: "check",
              resizable: false,
              editable: true,
              sortable: false,
              checkboxSelection: true,
              flex: 1,
              cellStyle: { textAlign: "center" },

            },
            { headerName: "", field: "product_code", resizable: true, sortable: true ,flex: 2, cellStyle: { textAlign: "center" } },
            { headerName: " ", field: "product_name", resizable: true, sortable: true ,flex: 2, cellStyle: { textAlign: "left" } },
            { headerName: "LOT ", field: "product_lot", resizable: true, sortable: true ,flex: 3, cellStyle: { textAlign: "center" }},
            { headerName: " ", 
            field: "input_amount", 
            resizable: true, 
            sortable: true ,
            flex: 2, 
            cellStyle: { textAlign: "right" }
            ,cellRenderer: params =>{
                    if(params.value){
                        const formattedValue = params.value.toLocaleString(); //   
                        return `<span>${formattedValue}</span>`;
                    }
            }},
            { headerName: " ", 
            field: "warehouse_name", 
            editable: true, 
            sortable: true ,
            flex: 2, 
            cellStyle: { textAlign: "right",
                        //backgroundColor: "#fff", //  
                        //border: "0.5px dashed #fb8c00", //  
                        cursor: "text", //  
             }
             ,cellRenderer: params =>{
                    if(params.value){
                        const formattedValue = params.value.toLocaleString(); //   
                        return `
                                    
                                    <span style="display: flex; align-items: center; justify-content: text-start;">
                                        <span style="flex-grow: 1; text-align: left;">${formattedValue}</span>
                                        <i class="fas fa-edit" style="color:#6c757d88" title="  "></i>
                                    </span>
                                `;
                    }else{
                         //       
                        return `<span><i class="fas fa-edit" style="color:#6c757d88" title="  "></i></span>`;
                    }
             }},
            { headerName: " ", field: "input_date", resizable: true, sortable: true ,flex: 2, cellStyle: { textAlign: "center" } },
            
        ],

           
             

            //   () 
            warehouseCode : "", //  
            selectedWarehouseCode:"", //   
            warehouseName:"", //   
            selectedWarehouseName : "", //    


            isShowModal: {
            client: false, //  
            warehouse:false, // 
            },

            
           

            
        }
    },
   
 

    methods:{
        selectproduct(product){
            //console.log(product); 
            this.selectedProCode = product.product_code;
            this.selectedProName = product.product_name;
        },
        selectwarehouse(warehouse){
            this.selectedWarehouseCode = warehouse.warehouse_code;
            this.selectedWarehouseName = warehouse.warehouse_name;
        },
        openModal(modalType){
            this.isShowModal[modalType] = true; 
            //console.log(`${modalType} modal open`);

            // warehouse_name   
            if (modalType === 'warehouse') {
                // ag-Grid    
                const selectedNodes = this.gridApi.getSelectedNodes();
                if (selectedNodes.length > 0) {
                    this.selectedCol = selectedNodes[0];  //     .
                    //console.log(" :", this.selectedCol);
                    //  
                    this.originWarehouseName  = this.selectedCol.data.warehouse_name;
                    //console.log(this.originWarehouseName)
                } else {
                    //console.log("  .");
                }
            }
        },
        
        confirm(modalType){
            if (modalType === 'product') {
                this.productName = this.selectedProName;
                this.productCode = this.selectedProCode;
            } else if (modalType === 'warehouse'){
                this.warehouseCode = this.selectedWarehouseCode;
                this.warehouseName = this.selectedWarehouseName; 
                //console.log(":",this.warehouseName);
                
                this.selectedCol.data.warehouse_name = this.warehouseName;
                //console.log(" warehouse_name:", this.selectedCol.data.warehouse_name);

                // ag-Grid 
                this.gridApi.applyTransaction({
                    update: [this.selectedCol.data] //   
                });
                
                
            }
            this.closeModal(modalType); //  
        },
        closeModal(modalType) {
            this.isShowModal[modalType] = false;
        },
        
        dateFormat(value, format) {
          return userDateUtils.dateFormat(value, format);
        },
            



        onReady(event){
            this.gridApi = event.api;
            // event.api.sizeColumnsToFit(); // api   
            //    
            const allPanels = document.querySelectorAll('.ag-paging-panel');
            const paginationPanel = allPanels[1];
            if (paginationPanel) {
               //  
               const container = document.createElement('div');
               container.style.display = 'flex';
               container.style.alignItems = 'center';
               container.style.gap = '5px'; //    

               //  
               const button1 = document.createElement('button');
               button1.textContent = '';
               button1.style.cursor = 'pointer';
               button1.style.backgroundColor = '#4caf50';
               button1.style.color = 'white';
               button1.style.border = 'none';
               button1.style.padding = '5px 10px';
               button1.style.borderRadius = '4px';

               const button2 = document.createElement('button');
               button2.textContent = '';
               button2.style.cursor = 'pointer';
               button2.style.backgroundColor = '#ff0000';
               button2.style.color = 'white';
               button2.style.border = 'none';
               button2.style.padding = '5px 10px';
               button2.style.borderRadius = '4px';

               const button3 = document.createElement('button');
               button3.textContent = '';
               button3.style.cursor = 'pointer';
               button3.style.backgroundColor = '#f48a06';
               button3.style.color = 'white';
               button3.style.border = 'none';
               button3.style.padding = '5px 10px';
               button3.style.borderRadius = '4px';

               //
                button1.addEventListener('click',()=>{
                    //
                    this.updateInput();
                });

                button2.addEventListener('click',()=>{
                    //
                    this.deleteInput();
                });
                button3.addEventListener('click',()=>{
                    //
                    this.resetUpdateInfo(); 
                })

                // 
                const inputText = document.createElement('input');
                inputText.type = 'text';
                inputText.placeholder = '';
                inputText.style.padding = '5px';
                inputText.style.width = '250px';
                inputText.style.border = '1px solid #ccc';
                inputText.style.borderRadius = '4px';

                //    ag 
                inputText.addEventListener('input',(event)=>{
                    const value = event.target.value;
                    //console.log(" :", value);

                    //
                    this.inputListsearch = value;
                });

                // ,   
                container.appendChild(button1);
                container.appendChild(button2);
                container.appendChild(button3);
                container.appendChild(inputText);

                //  
                paginationPanel.insertBefore(container,paginationPanel.firstChild);
            }

        },

        async searchInputlist(){
            if( new Date(this.startDate) > new Date(this.endDate)){
                this.$notify({
                        text: `     . `,
                        type: 'error',
                    });
                return;
            }
            this.filters  = {
                productCode : this.productCode,
                startDate : this.startDate,
                endDate : this.endDate,
            }

            //console.log(this.filters);
            let result = await axios.put(`${ajaxUrl}/input/inputlist`,this.filters )
                                    .catch(err => console.log(err));
            //console.log(result.data);
            this.inputData = result.data;
            this.inputData = result.data.map((col) => ({
                ...col,
                input_date: this.dateFormat(col.input_date, "yyyy-MM-dd"),
                })
            );  

        }, 
        resetSearch(){
            // 
             //  
             this.startDate = "", //  
            this.endDate = "", //  
            // 
            this.selectedProName = "",//  
            this.productName = '', //   
            this.selectedProCode = "", //   
            this.productCode='',//   
            this.inputData = []


        },
        onCellClicked(col) {
            if (col.colDef.field === 'warehouse_name') {
                //console.log("");
                // warehouse_name     
                this.openModal('warehouse');
           
            }
        },
        // onClickedWh(col){
        //     if (col.colDef.field === 'warehouse_name') {
        //         console.log("");
        //         // warehouse_name     
        //         this.openModal('warehouse');
           
        //     }else{
        //         console.log("");
        //     }

        // },
        //         
        rollbackCellChange(col) {
            let nodeId = col.node.id;
            let colId = col.column.colId;
            if (this.originalValues && this.originalValues[nodeId] && this.originalValues[nodeId][colId] !== undefined) {
                let originalValue = this.originalValues[nodeId][colId];
                col.node.setDataValue(col.column,originalValue); //   
            }
        },
        //
        resetUpdateInfo(){
            if (this.selectedCol) {
            //  
            this.selectedCol.data.warehouse_name = this.originWarehouseName;

        // ag-Grid 
            this.gridApi.applyTransaction({
                update: [this.selectedCol.data] //   
            });

            //console.log("  :", this.originWarehouseName);
            }
            this.gridApi.deselectAll();
            this.originWarehouseName = null;
        },

        async updateInput(){
            const selectedRows = this.gridApi.getSelectedRows(); 
            let productLots = []
            //let inputAmounts = []
            let warehouseNames = []
            selectedRows.forEach(row => {
                //console.log("row:",row.product_lot);
                productLots.push(row.product_lot);
                //inputAmounts.push(row.input_amount);
                warehouseNames.push(row.warehouse_name);
            });

        //         =>       
            // let checkLotOutput = {
            //     productLot : JSON.stringify(productLots)
            // }
            // let result = await axios.put(`${ajaxUrl}/inputUpdate/check`,checkLotOutput)
            //                         .catch(err => console.log(err));

            // console.log(result.data);
            // let lotOutputNum = result.data[0][0]
            // console.log(lotOutputNum.total_output_count);

            // if(lotOutputNum.total_output_count == 0){
                 //   input         
            //     let updateInputInfo = {
            //         productLot : JSON.stringify(productLots),
            //         inputAmount : JSON.stringify(inputAmounts),
            //         warehouse : JSON.stringify(warehouseNames)
            //     }
            //     console.log("",updateInputInfo);
            //         let result = await axios.put(`${ajaxUrl}/inputUpdate/update`,updateInputInfo)
            //                                 .catch(err => console.log(err));
            //                     console.log(result.data.result);
                    // ===      (true,false boolean   '' .)
            //         if(result.data.result === true){
            //                 this.$notify({
            //                 text: `   . `,
            //                 type: 'success',
            //             });
            //         }else{
            //             this.$notify({
            //                 text: `    . `,
            //                 type: 'error',
            //             });
            //         }

            // }else{
            //     this.$notify({
            //             text: `     .`,
            //             type: 'error',
            //         });
            // }
            //    
                let updateInputInfo = {
                    productLot : JSON.stringify(productLots),
                    warehouse : JSON.stringify(warehouseNames)
                }
                let result = await axios.put(`${ajaxUrl}/inputUpdate/update`,updateInputInfo)
                                        .catch(err => console.log(err));
                //console.log(result.data.result);
                // ===      (true,false boolean   '' .)
                if(result.data.result === true){
                        this.$notify({
                        text: `   . `,
                        type: 'success',
                    });
                    window.location.reload(); 
                    
                }else{
                    this.$notify({
                        text: `    . `,
                        type: 'error',
                    });
                }

            },
            
       //  (input_flag 1  )
        async deleteInput(){
            const selectedRows = this.gridApi.getSelectedRows(); 
            let productLots = []
            selectedRows.forEach(row => {
                //console.log("row:",row.product_lot);
                productLots.push(row.product_lot);
            });

            let deleteInfo = {
                productLot : JSON.stringify(productLots)
            }
            //console.log(":",deleteInfo);

            let result = 
                await axios.put(`${ajaxUrl}/input/delete`,deleteInfo)
                            .catch(err => console.log(err)); 

                //console.log(result);
             // ===      (true,false boolean   '' .)
            if(result.data.result === true){
                        this.$notify({
                        text: `   . `,
                        type: 'success',
                    });
                    window.location.reload(); 
                }else{
                    this.$notify({
                        text: `    . `,
                        type: 'error',
                    });
                }                  
        },
     

    },// method  
};
</script>
<style lang="scss" scoped>
.main-container{
    background-color:  #e9ecef;
    border-radius: 10px;
    height: 300px;
    
}



.text-center {
  text-align: center;
}

.button {
  margin: 0 10px; /*     */
}

/*  input   */
input {
  background-color: #ffffff; /*   */
  border: solid 1px #ced4da; /*   */
  color: #495057; /*   */
}
/* readonly  input   */
input:read-only {
  background-color: #ffffff; /*    */
  color: #495057; /*    */
  cursor: not-allowed; /*      */
}

/* input     */
input:focus {
  background-color: #ffffff; /*      */
  border-color: #86b7fe; /*       */
  outline: none; /*      */
}

/* readonly input     */
input:read-only:focus {
  background-color: #ffffff; /*    */
  border-color: #ced4da; /* readonly    */
  outline: none; /*    */
}
</style>
