<!--  -->
<template>
   <div class="partList container-fluid py-4">
    <div>
       <h3>&nbsp;&nbsp; </h3>
    </div>
 <!--    -->
 <div class="main-container">
    <div class="content" style="height: auto;  margin: 20px; padding-bottom: 30px;">
       <form class="row gx-3 gy-2 align-items-center">
         <div class="d-flex">
            <div class="p-2 flex-fill">
               <!--  -->
               <div class="col-sm-8">
                  <label class="col-form-label fw-bold" for="warehouseCode"></label>
                  <input type="text" class="form-control" style="background-color: white; padding-left: 20px;" id="warehouseCode" v-model="warehouseCode" >
               </div>
               <!--  -->
               <div class="col-sm-8">
                  <label class="col-form-label fw-bold" for="empName"></label>
                  <input type="text" class="form-control" style="background-color: white; padding-left: 20px;" id="empName" v-model="empName" >
               </div>
            </div>
            <div class="p-2 flex-fill">
               <!--  -->
               <div class="col-sm-8">
                  <label class="col-form-label fw-bold" for="warehouseName"></label>
                  <input type="text" class="form-control" style="background-color: white; padding-left: 20px;" id="warehouseName" v-model="warehouseName" >
               </div>
               <!--  -->
               <div class="col-sm-8">
                  <label class="col-form-label fw-bold" for="empTel"></label>
                  <input type="text" class="form-control" style="background-color: white; padding-left: 20px;" id="empTel" v-model="empTel" >
               </div>
            </div>
            <div class="p-2 flex-fill">
               <!--  -->
               <div class="col-sm-8">
                  <label class="col-form-label fw-bold" for="storage"></label>
                  <input type="text" class="form-control" style="background-color: white; padding-left: 20px;" id="storage" v-model="storage" >
               </div>
               <!--  -->
               <div class="col-sm-8">
                  <label class="col-form-label fw-bold" for="warehouseArea"></label>
                  <input type="text" class="form-control" style="background-color: white; padding-left: 20px;" id="warehouseArea" v-model="warehouseArea" >
               </div>
            </div>
            <div class="p-2 flex-fill">
               <!--  -->
               <div class="col-sm-8">
                  <label class="col-form-label fw-bold" for="warehouseLocation"></label>
                  <input type="text" class="form-control" style="background-color: white; padding-left: 20px;" id="warehouseLocation" v-model="warehouseLocation" >
               </div>
               <!--   -->
               <div class="col-sm-11">
                  <button style="position:relative;  top:45px;" type="button" class="btn btn-success me-5" @click="upin? input_update(2) : input_update(1)">/</button>
                  <button style="position:relative; width:120px ; top:45px;" type="button" class="btn btn-warning me-5" @click="refresh"  ></button>
               </div>
            </div>
         </div>
      </form>
    </div>
 </div>
 
 <!--     -->
 <div class="grid-container" style="padding-top: 10px;">
    <ag-grid-vue
       :rowData="rowData"
       :columnDefs="columnDefs"
       :theme="theme"
       :pagination="true"
       :paginationPageSize="10"
       @grid-ready="onReady"
       style="height: 513px;"
       rowSelection="multiple"
       @cellClicked="onCellClicked"

    > 
    </ag-grid-vue>
 </div>
 <deleteModal
      :showModal="showDeleteModal"
      @deleteConfirmed="onDeleteConfirmed"
      @deleteCancelled="onDeleteCancelled"
      style="z-index : '9999';"
 />
 </div>
 </template>
 
 <script>
 import axios from 'axios';
 import { ajaxUrl } from '@/utils/commons.js';
 // import userDateUtils from '@/utils/useDates.js';
 import theme from "@/utils/agGridTheme";
 import { mapMutations } from "vuex";
 import deleteModal from './deleteModal.vue';
 
 export default {
   components: {
      deleteModal, //   
   },
   data() {
     return {
        warehouseCode:'',
        warehouseName:'',
        storage:'',
        warehouseLocation:'',
        empName:'',
        empTel:'',
        warehouseArea:'',
        showDeleteModal: false, //    
        selectedWarehouse: null, //   
       columnDefs: [
         { headerName: "", field: "warehouse_code", width: 220 },
         { headerName: "", field: "warehouse_name" },
         { headerName: "", field: "storage" },
         { headerName: "", field: "warehouse_location" },
         { headerName: "", field: "emp_name" },
         { headerName: "", field: "emp_tel" ,cellStyle: { textAlign: 'center' } },
         { headerName: "", field: "warehouse_area" ,cellStyle: { textAlign: 'right' }},
         {
           headerName: "",
           field: "",
           upin : '',
           cellStyle: { textAlign: 'center' },
           editable: false,
           cellRenderer: (params) => {
             const button2 = document.createElement('button');
             button2.innerText = '';
             button2.style.marginRight = '10px';
             button2.style.cursor = 'pointer';
             button2.style.backgroundColor = '#f7b84d';
             button2.style.width = '60px';
             button2.style.height = '30px';
             button2.style.color = 'white';
             button2.style.border = 'none';
             button2.style.padding = '0';
             button2.style.borderRadius = '4px';
             button2.style.textAlign = 'center';
             button2.style.lineHeight = '30px';
             button2.addEventListener('click', () => {
               console.log(" [] : ", JSON.stringify(params.data));
               console.log("  : ", params.data.warehouse_code);
               this.selectedWarehouse = params.data; //   
               this.showDeleteModal = true; //  
             });
             return button2;
           }
         },
       ],
       newList: [],
       rowData: [], //    api 
       theme:theme,
     };
   },
   methods: {
      refresh(){
         this.warehouseList();
         this.upin = '';
         this.warehouseCode = '';
         this.warehouseName = '';
         this.storage = '';
         this.warehouseLocation = '';
         this.empName = '';
         this.empTel = '';
         this.warehouseArea = '';
      },
      onDeleteCancelled() {
         this.showDeleteModal = false; //  
      },
      async onDeleteConfirmed() {
      if (this.selectedWarehouse) {
        try {
          const res = await axios.delete(`${ajaxUrl}/warehouseDelete/${this.selectedWarehouse.warehouse_code}`);
          if (res.data === '') {
            this.$notify({ text: ' .', type: 'success' });
            this.warehouseList(); //    
          } else {
            this.$notify({ text: ' .', type: 'error' });
          }
        } catch (error) {
          console.log(error);
          this.$notify({ text: '   ', type: 'error' });
        }
      }
      this.showDeleteModal = false; //  
    },
      ...mapMutations(["addLoginInfo"]),
      async checkLogin(){
          this.loginInfo = this.$store.state.loginInfo;
          console.log('',this.loginInfo);
          if(this.loginInfo.job === ''){
            console.log('');
          }else{
              this.$notify({ text: '  ', type: 'error' });
              this.$router.push({ name : 'MainPage' });
          }
      },

    async warehouseList(){ // 
        const result = await axios.get(`${ajaxUrl}/warehouseList`)
                                        .catch(err => console.log(err));
        this.rowData = result.data;
    },
     onReady(param) {
       param.api.sizeColumnsToFit(); //  api      
     },
     onCellClicked(params) {
        if(params.colDef.field !== ''){
            console.log(" [] : ", JSON.stringify(params.data));
            this.warehouseCode = params.data.warehouse_code;
            this.warehouseName = params.data.warehouse_name;
            this.storage = params.data.storage;
            this.warehouseLocation = params.data.warehouse_location;
            this.empName = params.data.emp_name;
            this.empTel = params.data.emp_tel;
            this.warehouseArea = params.data.warehouse_area;
            this.upin = 'update';
        }
    },
    async warehouseInsert(newList){
           const result = await axios.post(`${ajaxUrl}/warehouseInsert`, newList)
                                       .catch(err => console.log(err));
           console.log(result.data);
           this.warehouseList();
    },
     input_update() {
       console.log('    ');
       if(this.warehouseCode === '' || this.warehouseName === '' || this.storage === '' || this.warehouseLocation === '' || this.empName === '' || this.empTel === '' || this.warehouseArea === ''){
         this.$notify({ text: ' .', type: 'error' });
           return;
       }
       this.newList = { warehouse_code : this.warehouseCode,
                        warehouse_name : this.warehouseName,
                        storage : this.storage,
                        warehouse_location : this.warehouseLocation,
                        emp_name : this.empName,
                        emp_tel : this.empTel,
                        warehouse_area : this.warehouseArea
       };
       if(this.upin === 'update'){
            this.$notify({ text: '.', type: 'success' });
            this.warehouseInsert(this.newList);
         } else {
            this.$notify({ text: '.', type: 'success' });
            this.warehouseInsert(this.newList);
         }
      //  this.warehouseInsert(this.newList);
       this.warehouseList();
     },
   },
   //   
   mounted() {
     //   
     this.checkLogin();
     this.warehouseList();
   },
 };
 </script>
 
 <style lang="scss" scoped>
 .main-container{
     background-color:  #e9ecef;
     margin-left: 20px;
     margin-right: 20px;
     margin-top: 0px;
     margin-bottom: 0px;
     border-radius: 10px;
 }
 .content{
    margin-left: 20px;
    margin-right: 20px;
    margin-top: 0px;
    margin-bottom: 0px;
    padding: 0px;
 }
 input {
       background-color: $white;
       border: solid 1px  ;
 }
 input:focus {
  background-color: #ffffff; /*      */
  border-color: #86b7fe; /*       */
  outline: none; /*      */
}
 </style>