<template>
    <div class="container-fluid py-4">
        <!--  -->
        <h3> </h3>
            <div class= "main-container ps-4">
                <div class="pt-2 pb-3">
                        <!--   -->
                        <div class="mb-3">
                            <label class="col-sm-2 col-form-label fw-bold"></label>
                            <div>
                                <label class="me-3" v-for="status in statusList" :key="status">
                                    {{ status }}
                                <input 
                                    class="form-check-input"
                                    :value="status"
                                    type="checkbox"
                                    v-model="pickedStatus"
                                >
                                </label>
                            </div>

                        </div>

                    <!-- -->
                    <div class="row align-items-center mb-3">
                        <label class="col-sm-2 col-form-label fw-bold" ></label>
                        <div class="col-sm-4">
                            <input 
                                id="clientSearch"  class="form-control border p-2"
                                v-model="searchCom.com_name" @click="openModal"  autocomplete="off" placeholder="" />
                                <Modal
                                    :isShowModal="isShowModal"
                                    :modalTitle="''"
                                    :noBtn="''"
                                    :yesBtn="''"
                                    @closeModal="closeModal()"
                                    @confirm="confirm()"
                                >
                                <template v-slot:list>
                                    <ComList v-show="isShowModal" @selectclient="selectclient"/>
                                </template>
                                </Modal>
                        </div>    
                    </div>
                    <!--  -->
                    <div  class="row align-items-center mb-3">
                        <label class="col-sm-2 col-form-label fw-bold" ></label>
                        <div class="col-sm-6">
                            <input 
                            id="orderSearch" class="form-control border p-2"
                            v-model="orderName"  autocomplete="off" placeholder="" />
                        </div>
                    </div>
                    <!--  -->
                    <div class="row align-items-center mb-5">
                        <label class="col-sm-2 col-form-label fw-bold"></label>
                        <div class="col-sm-4">
                            <input 
                            type="date" 
                            id="startDate" class="form-control border p-2"
                            v-model="startDate"  autocomplete="off"  />
                        </div>
                        <div class="col-sm-1 text-center">~</div>
                        <div class="col-sm-4">
                            <input 
                            type="date" 
                            id="endDate" class="form-control border p-2"
                            v-model="endDate"  autocomplete="off"   />
                        </div>
                    </div>
                </div>
                 <!--  -->
                <div class="mb-3 pt-2 pb-5 text-center">
                    <material-button  color="success" class="button" @click="searchOrderlists"></material-button>
                    <material-button color="warning" class="button" @click="resetSearch"></material-button>
                </div>
            </div>
            <div style="display: none">
                <CustomNoRowsOverlay/>
            </div>
    </div>
    <!--orderList   :     -->
<orderList :filters="filters"/>


</template>

<script>
import MaterialButton from "@/components/MaterialButton.vue";
import Modal from "@/views/natureBlendComponents/modal/Modal.vue";
import ComList from "@/views/sales/Order/clientModal.vue";
import orderList from "./orderList.vue";
import CustomNoRowsOverlay from "@/views/natureBlendComponents/grid/noDataMsg.vue";


 

export default{
    name :"orderlistSearch",
    components:{
            MaterialButton,
            Modal,
            ComList,
            orderList,
            CustomNoRowsOverlay,
    },
    data(){
        return{
            noRowsOverlayComponent:'CustomNoRowsOverlay',
            isShowModal : false,

            //    
            statusList:["","",""], // 
            pickedStatus:[], //   

            //  
            orderName:"", // 
            startDate:"", //  
            endDate:"", //  

            searchCom:"", //     
            selectedCom: "", //  

            

            filters: [],
            
        };
    },
    

    methods:{
        selectclient(client){
            this.selectedCom = client; 
        },
        async openModal(){
            //console.log("modal ")
            this.isShowModal = !this.isShowModal; //  
        },
        
        confirm(){
            this.searchCom = this.selectedCom;
            this.closeModal();
        },
        closeModal() {
            this.isShowModal = false
        },
        //
        resetSearch(){
            this.pickedStatus = [];
            this.clientName = ""; //      
            this.orderName = "";  //      
            this.startDate = "";  //   
            this.endDate = "";    //   
            this.orderData = {};  //  
            //console.log(this.orderData);
        },
        searchOrderlists(){
            const statusMap ={
                "":"update",
                "":"continue",
                "":"done"
            }
            
            const dbStatus = this.pickedStatus.map(status=>statusMap[status]);
            this.filters = {
                orderStatus : dbStatus,
                clientName : this.searchCom,
                orderName : this.orderName,
                startDate : this.startDate,
                endDate : this.endDate,
            };
            //console.log(this.filters);
        },
        // addOrder(){
        //     this.orderData = {};
        // },
        
    },
    
};
</script>
<style lang="scss" scoped>
.main-container{
    background-color:  #e9ecef;
    border-radius: 10px;
}

.text-center {
  text-align: center;
}

.button {
  margin: 0 10px; /*     */
}


/*  input   */
input {
  background-color: #ffffff; /*   */
  border: solid 1px #ced4da; /*   */
  color: #495057; /*   */
}

/* input     */
input:focus {
  background-color: #ffffff; /*      */
  border-color: #86b7fe; /*       */
  outline: none; /*      */
}


</style>
