<template>
   <div>
     <h4 v-show="isVisible2" style="margin-bottom: 0px;">&nbsp;&nbsp;&nbsp;&nbsp;[{{ material_name }}]  </h4>
     <h4 v-show="!isVisible2" style="margin-bottom: 0px;">&nbsp;&nbsp;&nbsp;&nbsp; </h4>
   </div>
  <div class="grid-container" style="width: 700px; padding-top: 0px; padding-bottom: 10px;">
        <ag-grid-vue
            :rowData="clientdate"
            :columnDefs="clientcolumnDefs"
            :theme="theme"
            :pagination="true"
            :noRowsOverlayComponent="noRowsOverlayComponent"
            :quickFilterText="clientNamesearch"
            :paginationPageSize="4"
            :paginationPageSizeSelector="[4, 10, 20, 40]"
            @grid-ready="onReady"
            style="height: 261px;"
            rowSelection="multiple"
        >
        </ag-grid-vue>
    </div>
   <div style="display: none">
       <CustomNoRowsOverlay/>
   </div>
</template>
<script>
import theme from "@/utils/agGridTheme";
import CustomNoRowsOverlay from "@/views/natureBlendComponents/grid/noDataMsg.vue";
import axios from 'axios';
import { ajaxUrl } from '@/utils/commons.js';

export default {
    // 
    components: {
        CustomNoRowsOverlay,
    },
    //   
    props: {
      material_code: {
         type: String,  // 
         required: true, // 
      },
      material_name: {
         type: String,
         required: true,
      },
    },
    //   
    watch: {
      material_code: {
         handler: "fullClient",  //  
         //   1   
         immediate: true, 
      },
      material_name: {
         immediate: true, 
      },
    },
    //   
    data(){
        return{
            noRowsOverlayComponent: 'CustomNoRowsOverlay',
            clientNamesearch: "", //
            isVisible2:false,
            clientcolumnDefs: [
                { 
                    headerCheckboxSelection: true,
                    checkboxSelection: true,
                    headerName: "",
                    width:45,
                },
                { headerName: "NO.", field: "client_num", width:100, cellStyle: { textAlign: "center" } },
                { headerName: "", field: "com_name", cellStyle: { textAlign: "left" } },
                { headerName: "", field: "emp_name", width:130, cellStyle: { textAlign: "left" } },
                { headerName: "", field: "emp_tel", cellStyle: { textAlign: "center" } },
            ],
            clientdate: [],
            theme : theme,
        };
    },
    methods: {
        onReady(event) {
            this.gridApi = event.api;
            event.api.sizeColumnsToFit(); // api   

            //    
            const allPanels = document.querySelectorAll('.ag-paging-panel');
            const paginationPanel2 = allPanels[1];
            if (paginationPanel2) {
               //  
               const container = document.createElement('div');
               container.style.display = 'flex';
               container.style.alignItems = 'center';
               container.style.gap = '5px'; //    
               container.style.position = 'absolute';
               container.style.left = '10px';

               //  
               const button3 = document.createElement('button');
               button3.textContent = '';
               button3.style.cursor = 'pointer';
               button3.style.backgroundColor = '#4caf50';
               button3.style.color = 'white';
               button3.style.border = 'none';
               button3.style.width = '90px';
               button3.style.padding = '5px 10px';
               button3.style.borderRadius = '4px';

               //   
               button3.addEventListener('click', () => {
                  //alert('  ');
                  this.allInput();
               });

               //   
               const inputText = document.createElement('input');
               inputText.type = 'text';
               inputText.placeholder = '';
               inputText.style.padding = '5px';
               inputText.style.width = '60px';
               inputText.style.border = '1px solid #ccc';
               inputText.style.borderRadius = '4px';

               //     ag  
               inputText.addEventListener('input', (event) => {
                     const value = event.target.value;
                     //console.log(' :', value);
                     //    
                     this.clientNamesearch = value;
               });

               //     
               container.appendChild(button3);
               container.appendChild(inputText);

               //    
               paginationPanel2.insertBefore(container, paginationPanel2.firstChild);
            }
        },
        async allInput(){
         const selectedRows = this.gridApi.getSelectedRows(); //    
         if (selectedRows.length > 0) {
             this.selectedRows = selectedRows.map((col) => ({
                ...col,
                material: this.material_name,
                material_code: this.material_code,
             }));
             //  
             //console.log(this.selectedRows);
             this.$emit('planAndClientList', this.selectedRows);
             this.clientdate = [];
             this.isVisible2 = !this.isVisible2;
             this.$notify({ text: '   .', type: 'success' });    // success, warn, error 
         }else{
            this.$notify({ text: '  .', type: 'error' }); // title:'', 
         }
        },
        async fullClient(){
            if(this.material_code){
               this.isVisible2 = !this.isVisible2;
               let result = await axios.get(`${ajaxUrl}/material/fullClient`)
               .catch(err => console.log(err));
               this.clientdate = result.data;
               console.log(result.data);
            }
         },
    },
};
</script>