<template>
   <div class="pt-3" style="padding-left: 16px;">
      <h3>&nbsp;&nbsp; </h3>
   </div>
    <div class="grid-container pt-0">
      <ag-grid-vue
        :rowData="rowData"
        :columnDefs="columnDefs"
        :theme="theme"
        :pagination="true"
        :paginationPageSize="10"
        :paginationPageSizeSelector="[10, 20, 50, 100]"
        @grid-ready="onReady"
        style="height: 700px;"
        :quickFilterText="clientNamesearch"
        rowSelection="multiple"
        :noRowsOverlayComponent="CustomNoRowsOverlay"
        >
    </ag-grid-vue>
   </div>
   <div>
    <Modal :isShowModal="isShowModal" :testStr="testStr" v-model:nuwList="nuwList" :whList="whList" @closeModal="closeModal" @confirm="confirm">
    </Modal>
  </div>
  <div>
    <InspectionModal :isShowModal2="isShowModal2" :inspection_data="inspection_data" @closeModal2="closeModal2" @confirm2="confirm2">
    </InspectionModal>
  </div>
  <div v-if="showprogress" style="width: 100%; height: 100%; background-color: #0005; position: fixed; top: 0px; left: 0px; z-index: 1000;">
      <div style="position: fixed; top: 50%; left: 30%;  width: 50%;">
          <material-progress color="success" :percentage="number2" />
      </div>
  </div>
 </template>
<script setup>
import axios from 'axios';
import { ajaxUrl } from '@/utils/commons.js';
import userDateUtils from '@/utils/useDates.js';

import Modal from "@/views/material/materialInputModal.vue";
import InspectionModal from "@/views/material/inspection_com.vue";

import CustomNoRowsOverlay from "@/views/natureBlendComponents/grid/noDataMsg.vue";

 import theme from "@/utils/agGridTheme";
 import { ref, onBeforeMount } from 'vue';

import { useNotification } from "@kyvg/vue3-notification";  // 
const { notify } = useNotification();  //  

 //import { ref, shallowRef, computed, onBeforeMount } from 'vue';
 //import { useRouter } from 'vue-router';

 import MaterialProgress from "@/components/MaterialProgress.vue";

 //  
import { useRouter } from 'vue-router';
const router = useRouter();
//   
import { useStore } from 'vuex'; // Vuex  
const store = useStore();
const loginfo = ref({});
const loginInfo = () => {
   loginfo.value = store.state.loginInfo;
   if(loginfo.value.job === '' || loginfo.value.position === ''){
      console.log(loginfo.value.job);
   }else{
      notify({
         //title: "",
         text: "    .",
         type: "error", // success, warn, error 
      });
      router.push({ name: 'MainPage' });
   }
};

 //  
 const number2 = ref(0);
 const showprogress = ref(false);

 //    
 const rowData = ref([]);
 // {all_ckeck: false,body_num: 1,order_code: "A1001", material_name: "", com_name: "", ord_qty: 100, total_qnt: 90, pass_qnt: 85, rjc_qnt: 5, unit_price: 1000, total_price: 90000, inspec_end: "2024-06-12", : "" }

 const testStr = ref("test");

 // api   
 const columnDefs = ref([
        { 
          headerCheckboxSelection: true,
          checkboxSelection: true,
          headerName: "",
          width:45,
        },
        { headerName: "No.", field: "body_num", width:100, cellStyle: { textAlign: "center" } },
        { headerName: "", field: "order_code", cellStyle: { textAlign: "center" } },
        { headerName: "", field: "material_name" },
        { headerName: "", field: "com_name" },
        { headerName: "", field: "ord_qty", cellStyle: { textAlign: "right" } },
        { headerName: "", field: "total_qnt", cellStyle: { textAlign: "right" } },
        { headerName: "", field: "pass_qnt", cellStyle: { textAlign: "right" } },
        { headerName: "", field: "rjc_qnt", cellStyle: { textAlign: "right" } },
        { headerName: "", field: "unit_price", cellStyle: { textAlign: "right" } },
        { headerName: "", field: "total_price", cellStyle: { textAlign: "right" } },
        { headerName: "", field: "inspec_end", cellStyle: { textAlign: "center" } },
        {  
          headerName: "", 
          field: "", 
          width:150,
          editable: false,
          cellStyle: { textAlign: "center" },
          cellRenderer: params => {
            const div = document.createElement('div');
            div.style.display = 'flex';
            div.style.justifyContent = 'center';
            div.style.alignItems = 'center';
            div.style.height = '100%';

            const button = document.createElement('button');
            button.innerText = '';
            button.style.cursor = 'pointer';
            button.style.backgroundColor = '#fb8c00';
            button.style.width = '60px';
            button.style.height = '30px';
            button.style.color = 'white';
            button.style.border = 'none';
            button.style.borderRadius = '4px';
            button.style.display = 'flex';
            button.style.justifyContent = 'center';
            button.style.alignItems = 'center';
            button.addEventListener('click', () => {
              console.log("  : ", JSON.stringify(params.data));
              inspection_com(params.data);
            });
            div.appendChild(button);
            return div;
            //return button;
          }
        },
        {  
          headerName: "", 
          field: "", 
          width: 145,
          cellStyle: { textAlign: "center" },
          editable: false,
          cellRenderer: params => {
            const div = document.createElement('div');
            div.style.display = 'flex';
            div.style.justifyContent = 'center';
            div.style.alignItems = 'center';
            div.style.height = '100%';

            const button2 = document.createElement('button');
            button2.innerText = '';
            button2.style.cursor = 'pointer';
            button2.style.backgroundColor = '#4caf50';
            button2.style.width = '60px';
            button2.style.height = '30px';
            button2.style.color = 'white';
            button2.style.border = 'none';
            button2.style.borderRadius = '4px';
            button2.style.display = 'flex';
            button2.style.justifyContent = 'center';
            button2.style.alignItems = 'center';
            button2.addEventListener('click', () => {
              console.log("  : ", JSON.stringify(params.data));
              //  1   ( )
              allInput(params.data);
            });
            div.appendChild(button2);
            return div;
            //return button2;
          }
        }
      ]);


//     
const inspection_data = ref({});
//   
const inspection_com = (data) => {
  //     
  inspection_data.value = {
    order_code: data.order_code,
    material_name: data.material_name,
    com_name: data.com_name,
    ord_qty: data.ord_qty,
    total_qnt: data.total_qnt,
    pass_qnt: data.pass_qnt,
    rjc_qnt: data.rjc_qnt,
  };
  console.log(inspection_data.value);
  // 
  isShowModal2.value = true;
};
//   
const isShowModal2 = ref(false);
 //  
 const closeModal2 = () => {
  isShowModal2.value = false;
};
 //  
 const confirm2 = () => {
  isShowModal2.value = false;
};



//       
const matrialQcInput = async function(){
  let result = await axios.get(`${ajaxUrl}/material/miql`)
                      .catch(err=>console.log(err));
  //console.log(result.data);  //  
  rowData.value = result.data;
  //console.log(rowData.value[0]['inspec_end']);
  rowData.value = result.data.map(col => ({
    ...col,
    inspec_end: userDateUtils.dateFormat(col.inspec_endm, "yyyy-MM-dd"),
    //ord_qty: (col.ord_qty * 0.001) + " kg",
    ord_qty: col.material_name.includes('') ? (Number(col.ord_qty)).toLocaleString() + " " : (Number(col.ord_qty) * 0.001).toLocaleString() + " kg",
    //total_qnt: (col.total_qnt * 0.001) + " kg",
    total_qnt: col.material_name.includes('') ? (Number(col.total_qnt)).toLocaleString() + " " : (Number(col.total_qnt) * 0.001).toLocaleString() + " kg",
    //pass_qnt: (col.pass_qnt * 0.001) + " kg",
    pass_qnt: col.material_name.includes('') ? (Number(col.pass_qnt)).toLocaleString() + " " : (Number(col.pass_qnt) * 0.001).toLocaleString() + " kg",
    //rjc_qnt: (col.rjc_qnt * 0.001) + " kg",
    rjc_qnt: col.material_name.includes('') ? (Number(col.rjc_qnt)).toLocaleString() + " " : (Number(col.rjc_qnt) * 0.001).toLocaleString() + " kg",
    //unit_price: Number(col.unit_price*0.001).toLocaleString() + " ",
    unit_price: col.material_name.includes('') ? Number(col.unit_price).toLocaleString() + " " : Number(col.unit_price*0.001).toLocaleString() + " ",
    //total_price: Number(col.total_price*0.000001).toLocaleString() + " ",
    total_price: col.material_name.includes('') ? Number(col.total_price).toLocaleString() + " " : Number(col.total_price*0.000001).toLocaleString() + " ",
  }));
}

//  
const whList = ref([]);

//  
const warehouseInfo = async function(){
  let result = await axios.get(`${ajaxUrl}/material/warehouse`)
                      .catch(err=>console.log(err));
  //console.log(' : ',result.data);
  whList.value = result.data;
}

//  
const clientNamesearch = ref('');

//ag-grid  API    
const onReady = (param) => {
  param.api.sizeColumnsToFit(); // api   
  allInputData.value = param.api;  // 

  //    
  const paginationPanel = document.querySelector('.ag-paging-panel');
  if (paginationPanel) {

    //  
    const container = document.createElement('div');
    container.style.display = 'flex';
    container.style.alignItems = 'center';
    container.style.gap = '5px'; //    

    //  
    const button = document.createElement('button');
    button.textContent = '';
    button.style.marginRight = '10px';
    button.style.cursor = 'pointer';
    button.style.backgroundColor = '#4caf50';
    button.style.color = 'white';
    button.style.border = 'none';
    button.style.padding = '5px 10px';
    button.style.borderRadius = '4px';
    button.style.position = 'absolute';
    button.style.left = '10px';

    //   
    button.addEventListener('click', () => {
      allInput();
    });

    //   
    const inputText = document.createElement('input');
    inputText.type = 'text';
    inputText.placeholder = ' ';
    inputText.style.padding = '5px';
    inputText.style.width = '200px';
    inputText.style.border = '1px solid #ccc';
    inputText.style.borderRadius = '4px';
    inputText.style.position = 'absolute';
    inputText.style.left = '110px';

    //     ag  
    inputText.addEventListener('input', (event) => {
          const value = event.target.value;
          //console.log(' :', value);
          //    
          clientNamesearch.value = value;
    });

    //     
    container.appendChild(button);
    container.appendChild(inputText);

    //      
    paginationPanel.insertBefore(container, paginationPanel.firstChild);
  }

}

 //     
 const allInputData = ref(null);
 //  
 const isShowModal = ref(false);

 //   
 const nuwList = ref([]);
 const selectedRows = ref([]);
 
// allInput   
const allInput = (data = null) => {
  //const selectedRows = data ? [data] : allInputData.value.getSelectedRows();   //    
  if (data && data.isTrusted) {
    selectedRows.value = allInputData.value.getSelectedRows();
  } else if (data) {
    selectedRows.value = [data];
  } else {
    selectedRows.value = allInputData.value.getSelectedRows();
  }
  console.log('selectedRows ', selectedRows.value);
  //const selectedRows = allInputData.value.getSelectedRows(); 
  console.log('data :', data);

  //  
  nuwList.value = selectedRows.value.map((val) => ({
      order_code: val.order_code,
      material_name: val.material_name,
      warehouse1: '',
      pass_qnt: val.pass_qnt,
      rjc_qnt: val.rjc_qnt,
      warehouse2: 'W003',
    })
  );

  //console.log('nuwList.value :', nuwList.value);

  if (selectedRows.value.length > 0) {
    //console.log(JSON.stringify(selectedRows.value, null, 2));   //  json  null= , 2=2
  } else {
    console.log("  .");
  }

  warehouseInfo();  //  
  // 
  isShowModal.value = true;
}

 //  
const closeModal = () => {
  isShowModal.value = false;
};


/*
0: {order_code: 'PO241213005', material_name: '', warehouse1: 'W001', pass_qnt: '90 kg', rjc_qnt: '8 kg', }
1: {order_code: 'PO241218003', material_name: '', warehouse1: 'W002', pass_qnt: '100 kg', rjc_qnt: '0 kg', }
2: {order_code: 'PO241218004', material_name: '', warehouse1: 'W002', pass_qnt: '999 kg', rjc_qnt: '1 kg', }
*/

const materialObj = ref([]);
const prefix = ref([]);
const numberPart = ref([]);

const lotMaking = async function(){
  let result = await axios.get(`${ajaxUrl}/material/lotNum`)
                      .catch(err=>console.log(err));
  //console.log(' : ',result.data);
  //console.log(' : ', nuwList.value);

  const lotNum = result.data[0]['LOTNUM'];
  //console.log(lotNum);
  if(lotNum){
    prefix.value = lotNum.slice(0, 5);
    numberPart.value = parseInt(lotNum.slice(5), 10);
    //console.log( (numberPart.value + 1).toString().padStart(3, '0') );
  }

  
  let percentNum = 100/nuwList.value.length;
  showprogress.value = true;
  for(let i=0; i<nuwList.value.length; i++){
    materialObj.value = {
      lot_code: prefix.value + ( (numberPart.value + i).toString().padStart(3, '0') ),
      order_code: nuwList.value[i].order_code,
      material_name: nuwList.value[i].material_name,
      pass_qnt: nuwList.value[i].material_name.includes('') ? ( Number( parseFloat(nuwList.value[i].pass_qnt.replace(/,/g, '')) ) ) : ( Number( parseFloat(nuwList.value[i].pass_qnt.replace(/,/g, '')) ) * 1000 ),
      warehouse1: nuwList.value[i].warehouse1,
      rjc_qnt:  nuwList.value[i].material_name.includes('') ? ( Number( parseFloat(nuwList.value[i].rjc_qnt.replace(/,/g, '')) ) ) : ( Number( parseFloat(nuwList.value[i].rjc_qnt.replace(/,/g, '')) ) * 1000 ),
      warehouse2: nuwList.value[i].warehouse2,
      emp_num: loginfo.value.emp_num,   //    
    };
    
    console.log(materialObj.value);
    //  
    number2.value = number2.value + percentNum;
    await inputMaterial(materialObj.value);
    // await delay(3000);   //3  
  }
  matrialQcInput(); //    DB
  notify({
      //title: "",
      text: "  .",
      type: "success", // success, warn, error 
   });
   showprogress.value = false;
   number2.value = 0;
  //location.reload();  // 
}

//3 for  
//const delay = (ms) => new Promise(resolve => setTimeout(resolve, ms));

//const materialObjInput = ref([]);
const inputMaterial = async function(materialObj){
  console.log(materialObj);
  let result = await axios.post(`${ajaxUrl}/material/inputMaterial`, materialObj)
                             .catch(err=>console.log(err));
  console.log(result.data);
  //        ...
}


//  
const confirm = () => {
  //console.log(' : ', nuwList.value);
  for(let i=0; i<nuwList.value.length; i++){
    // console.log(' : ', nuwList.value[i]['warehouse1']);
    // console.log(' : ', nuwList.value[i]['warehouse2']);
    if(!nuwList.value[i]['warehouse1'] || !nuwList.value[i]['warehouse2']){
      notify({
          title: "",
          text: "   .",
          type: "error", // success, warn, error 
      });
      break;
    }
    if(i == nuwList.value.length-1){
      console.log(i);
      lotMaking();
    }
  }
  console.log("   ");
  isShowModal.value = false; //  
};


//   
onBeforeMount(()=>{
  matrialQcInput();   //  
  loginInfo();
});

</script>