<template>
    <div class="container-fluid py-4">
    <!--  -->
    <h3> </h3>
    <h4 class="pt-2"> </h4>
        <div class= "main-container">
            <div class= "pt-5 pb-5">
                    <!-- -->
                    <div class="row align-items-center ms-3 mb-3">
                        <label class="col-sm-2 col-form-label fw-bold" ></label>
                        <div class="col-sm-6 d-flex align-items-center">
                            <div class="col-sm-4">
                            <input 
                                id="clientSearch"  class="form-control"
                                v-model="searchCom.com_name" @click="openModal('client')"  autocomplete="off"  placeholder=" "/>
                                <Modal
                                    :isShowModal="isShowModal.client"
                                    :modalTitle="''"
                                    :noBtn="''"
                                    :yesBtn="''"
                                    @closeModal="closeModal('client')"
                                    @confirm="confirm('client')"
                                >
                                <template v-slot:list>
                                    <ComList v-show="isShowModal.client" @selectclient="selectclient"/>
                                </template>
                                </Modal>
                            </div> 
                                    <!--  -->
                            <div class="col-sm-4 text-end">
                                <material-button size="md" color="success" class="me-2" @click="searchOrderlists"></material-button>
                                <material-button size="md" color="warning"  @click="resetSearch"></material-button>
                            </div> 

                        </div>
                         
                    </div>
             
            </div>
        </div>
    </div>
   <!---->
    <!-- <div class="grid-container" v-show="rowData.length != 0"> -->
    <div class="grid-container">
        <ag-grid-vue
        
        :rowData="rowData"
        :columnDefs="columnOrderlist"
        :theme="theme"
        @grid-ready="onReady"
        :noRowsOverlayComponent="noRowsOverlayComponent"
        @rowClicked="onOrderRowClicked"
        :quickFilterText="inputListsearch"
        rowSelection="multiple"
        :pagination="true"
        :paginationPageSize="5"
        :paginationPageSizeSelector="[5, 10, 20, 50, 100]"
    />
    </div>
    <div style="display: none">
        <CustomNoRowsOverlay/>
    </div>
    <div class="container-fluid py-4">
        <h4>    </h4>
        <div class="d-flex">
            <div class="grid-container">
            <ag-grid-vue
            style ="width:900px; height: 500px;"
            :rowData="rowDataOutputOrder"
            :columnDefs="columnOutputOrder"
            :theme="theme"
            @grid-ready="onReady1"
            :quickFilterText="inputListsearch1"
            :noRowsOverlayComponent="noRowsOverlayComponent"
            @rowClicked="onOutputReturn"
            rowSelection="multiple"
            :pagination="true"
            :paginationPageSize="10"
            :paginationPageSizeSelector="[10, 20, 50, 100]"
            />
            </div>
            <div style="display: none">
                <CustomNoRowsOverlay/>
            </div>
               
            <div class="select-container" v-show="rowDataOutputOrder.length != 0">
                <div class= "form-section">
                    <!--  -->
                    <div class="d-flex flex-column justify-content-center ps-5 pt-5">
                        <div class="row align-items-center mb-3">
                            <label class="col-sm-3  col-form-label fw-bold" ><span> *</span></label>
                                <div class="col-sm-9 d-flex align-items-center">
                                    <input 
                                    id="EmpName"  class="form-control border p-2" 
                                    v-model="searchEmpName" @click="openModal('emp')"  autocomplete="off"  />
                                    <Modal
                                        :isShowModal="isShowModal.emp"
                                        :modalTitle="''"
                                        :noBtn="''"
                                        :yesBtn="''"
                                        @closeModal="closeModal('emp')"
                                        @confirm="confirm('emp')"
                                    >
                                    <template v-slot:list>
                                        <EmpList v-show="isShowModal.emp" @selectemp="selectemp"/>
                                    </template>
                                    </Modal>
                                </div>
                        </div>
                        <div class="row align-items-center mb-3">
                            <!---->
                            <div class="form-section mb-4">
                                <label class="col-sm-3  col-form-label fw-bold" ><span> *</span></label>
                                <div class="col-sm-9 d-flex align-items-center">
                                    <input 
                                        id="returnNum"  class="form-control border p-2" autocomplete="off"
                                        v-model="returnNum"  placeholder=" "/>
                                </div>
                            </div>
                        </div>
                        <div class="row align-items-center mb-3">
                             <!---->
                             <div class="form-section">
                                <label class="col-sm-3  col-form-label fw-bold" ><span> *</span></label>
                                    <div class="col-sm-9  d-flex align-items-center">
                                        <input 
                                        id="ReturnReason"  class="form-control border p-2" 
                                        v-model="searchReturnReason" @click="openModal('returns')"  autocomplete="off"  placeholder="" />
                                        <Modal
                                            :isShowModal="isShowModal.returns"
                                            :modalTitle="''"
                                            :noBtn="''"
                                            :yesBtn="''"
                                            @closeModal="closeModal('returns')"
                                            @confirm="confirm('returns')"
                                        >
                                        <template v-slot:list>
                                            <ReturnList v-show="isShowModal.returns" @selectreturn="selectreturn"/>
                                        </template>
                                        </Modal>
                                    </div>
                            </div>
                        </div>     
                    </div>
                </div> 
                <!--  -->
                <div class=" text-center">
                    <material-button  color="success" class=" me-3" @click="returninstall"></material-button>
                    <material-button color="warning"  @click="resetReturn"></material-button>
                </div>
            </div>        
        </div>
    </div>
   
    
</template>

<script>
import MaterialButton from "@/components/MaterialButton.vue";
import Modal from "@/views/natureBlendComponents/modal/Modal.vue";
import ComList from "@/views/sales/Order/clientModal.vue";
import EmpList from "@/views/sales/Order/EmpModal.vue";
import ReturnList from "@/views/sales/ProductReturn/ReturnModal.vue"
import theme from "@/utils/agGridTheme";
import userDateUtils from '@/utils/useDates.js';
import CustomNoRowsOverlay from "@/views/natureBlendComponents/grid/noDataMsg.vue";
import axios from "axios";
import { ajaxUrl } from '@/utils/commons.js';
import { mapMutations } from "vuex";

export default{
    name :"orderlistSearch",
    components:{
            MaterialButton,
            Modal,
            ComList,
            EmpList,
            ReturnList,
            CustomNoRowsOverlay,
            
    },
    data(){
        return{
            
            testing: {},

            //  
            //  
            searchCom:"", //     
            selectedCom: "", //  

            //  ( )
            inputListsearch: "", // 1 ()
            inputListsearch1:"", //2 (lot)

          

            //   
            statusMap: {         // DB    
                "update": "",
                "continue": "",
                "done": "",
            },
            noRowsOverlayComponent: 'CustomNoRowsOverlay',
            filteredOrderlist : [],
            theme : theme,
            rowData : [],
            columnOrderlist : [
            { headerName : "", field:'orderlist_num',flex: 2,cellStyle: { textAlign: "right" }},
            { headerName : "", field:'orderlist_title',flex: 4,cellStyle: { textAlign: "left" }},
            { headerName : "",field:'com_name',flex: 2,cellStyle: { textAlign: "left" }},
            { headerName : "",field:'name',flex: 3,cellStyle: { textAlign: "left" }},
            { headerName : "",field:'order_date',flex: 2,cellStyle: { textAlign: "center" }},
            { headerName : "",field:'due_date',flex: 2,cellStyle: { textAlign: "center" }},
            { headerName : "",field:'orderlist_status',flex: 2,cellStyle: { textAlign: "left" }},
            ],
            //
            
            rowDataOutputOrder : [],
            columnOutputOrder : [
                {headerName :"",field: 'order_num',flex: 2,cellStyle: { textAlign: "right" } },
                {headerName :"",field: 'output_num',flex: 2,cellStyle: { textAlign: "right" }}, //   as (alias)    .
                {headerName :"",field: 'product_code',flex: 2,cellStyle: { textAlign: "center" }},
                {headerName :"",field: 'product_name',flex: 3,cellStyle: { textAlign: "left" }},
                {headerName :""
                , field: 'order_amount'
                ,flex: 2
                ,cellStyle: { textAlign: "right" }
                ,cellRenderer: params =>{
                    if(params.value){
                        const formattedValue = params.value.toLocaleString(); //   
                        return `<span>${formattedValue}</span>`;
                    }
                }},
                {headerName :""
                , field: 'output_amount'
                ,flex: 2
                ,cellStyle: { textAlign: "right" }
                ,cellRenderer: params =>{
                    if(params.value){
                        const formattedValue = params.value.toLocaleString(); //   
                        return `<span>${formattedValue}</span>`;
                    }
                }},
                {headerName :""
                ,field: 'per_price'
                ,flex: 2
                ,cellStyle: { textAlign: "right" }
                ,cellRenderer: params =>{
                    if(params.value){
                        const formattedValue = params.value.toLocaleString(); //   
                        return `<span>${formattedValue}</span>`;
                    }
                }},
                {headerName :"",field: 'output_date',flex: 2,cellStyle: { textAlign: "center" }}
            ],

            //    
             //  
            searchEmpName:"", //    
            selectedEmpName:"", //  
            searchEmpNum:"", //  
            selectedEmpNum:"", //    

            selectedReturnCode: "", //    
            searchReturnCode : "", //   
            selectedReturnReason:"", //   
            searchReturnReason : "", //    

            // 
            productCode:"",
            outputNum:"",
            outputAmount:"",
            clientNum:"",
           

            


            isShowModal: {
            client: false, //  
            emp: false, //  
            },

            
           

            
        }
    },
    mounted() {
      this.test();
    },
 

    methods:{
        ...mapMutations(["addLoginInfo"]),
        test(){
        this.testing = this.$store.state.loginInfo;
        //console.log('ddd', this.$store.state.loginInfo);
        this.searchEmpName = this.$store.state.loginInfo.name;
        this.searchEmpNum = this.$store.state.loginInfo.emp_num;
        },
        selectclient(client){
            this.selectedCom = client; 
        },
        selectemp(emp){
        this.selectedEmpName = emp.name;
        this.selectedEmpNum = emp.emp_num;
        },
        selectreturn(returns){
        this.selectedReturnCode = returns.return_code;
        this.selectedReturnReason = returns.return_reason;
        },
        openModal(modalType){
            this.isShowModal[modalType] = true; 
            //console.log(`${modalType} modal open`);
        },
        
        confirm(modalType){
            if (modalType === 'client') {
                this.searchCom = this.selectedCom;
            } else if (modalType === 'emp') {
                this.searchEmpName = this.selectedEmpName;
                this.searchEmpNum = this.selectedEmpNum;
            }else if (modalType === 'returns') {
                this.searchReturnCode = this.selectedReturnCode;
                this.searchReturnReason = this.selectedReturnReason;
            }
            this.closeModal(modalType); //  
        },
        closeModal(modalType) {
            this.isShowModal[modalType] = false;
        },
        // 
        resetSearch(){
            this.searchCom = ""; //      
            
            
            this.rowData =[];
            this.rowDataOutputOrder =[],
         

            this.searchEmpName="";
            this.searchEmpNum = "";
            this.returnNum ="";
            this.searchReturnReason ="";
            this.searchReturnCode ="";
        },
        //;
        async searchOrderlists(){
        this.filters  = {
            clientName : this.searchCom,
            
        }
        //console.log(this.filters);

        let result = await axios.put(`${ajaxUrl}/orderlist/search`,this.filters )
                                .catch(err => console.log(err));
        
        this.rowData = result.data;
        
        this.rowData = result.data.map((col) => ({
            ...col,
            order_date: this.dateFormat(col.order_date, "yyyy-MM-dd"),
            due_date: this.dateFormat(col.due_date, "yyyy-MM-dd"),
            orderlist_status : this.statusMap[col.orderlist_status],
            })
        );  
    },
    dateFormat(value, format) {
        return userDateUtils.dateFormat(value, format);
    },
            


    onReady(event){
            this.gridApi = event.api;
            // event.api.sizeColumnsToFit(); // api   
            //    
            const allPanels = document.querySelectorAll('.ag-paging-panel');
            const paginationPanel = allPanels[1];
            if (paginationPanel) {
               //  
               const container = document.createElement('div');
               container.style.display = 'flex';
               container.style.alignItems = 'center';
               container.style.gap = '5px'; //    

               
                // 
                const inputText = document.createElement('input');
                inputText.type = 'text';
                inputText.placeholder = '';
                inputText.style.padding = '5px';
                inputText.style.width = '250px';
                inputText.style.border = '1px solid #ccc';
                inputText.style.borderRadius = '4px';

                //    ag 
                inputText.addEventListener('input',(event)=>{
                    const value = event.target.value;
                    //console.log(" :", value);

                    //
                    this.inputListsearch = value;
                });

                // ,   
                
                container.appendChild(inputText);

                //  
                paginationPanel.insertBefore(container,paginationPanel.firstChild);
            }

           


    },
    onReady1(event){
        this.gridApi = event.api;
        event.api.sizeColumnsToFit(); // api   
        //    
        const allPanels = document.querySelectorAll('.ag-paging-panel');

            //lot
            const paginationPanel1 = allPanels[2];
        if (paginationPanel1) {
            //  
            const container1 = document.createElement('div');
            container1.style.display = 'flex';
            container1.style.alignItems = 'center';
            container1.style.gap = '5px'; //    

            
            // 
            const inputText1 = document.createElement('input');
            inputText1.type = 'text';
            inputText1.placeholder = '';
            inputText1.style.padding = '5px';
            inputText1.style.width = '250px';
            inputText1.style.border = '1px solid #ccc';
            inputText1.style.borderRadius = '4px';

            //    ag 
            inputText1.addEventListener('input',(event)=>{
                const value = event.target.value;
                //console.log(" :", value);

                //
                this.inputListsearch1 = value;
            });

            // ,   
            
            container1.appendChild(inputText1);

            //  
            paginationPanel1.insertBefore(container1,paginationPanel1.firstChild);
        }
    },

    //        
    async onOrderRowClicked(row) {
        //console.log("row.data===",row.data);
        let order = row.data;
    

        let result = await axios.get(`${ajaxUrl}/shippedOrderInfo/${order.orderlist_num}`)
                                .catch(err => console.log(err));
        this.rowDataOutputOrder = result.data;
        
        this.rowDataOutputOrder = result.data.map((col) => ({
            ...col,
            output_date: this.dateFormat(col.output_date, "yyyy-MM-dd"),
            com_name : row.data.com_name,
            })
        );
        //console.log("rowDataOutputOrder:",this.rowDataOutputOrder);
    },

    //    
    onOutputReturn(row){
        let output = row.data;
        this.outputAmount = output.output_amount;
        this.productCode = output.product_code;
        this.outputNum = output.output_num;
        this.clientNum = output.client_num;
        //console.log(" ",output);
        
    },
    //
    async returninstall(){
        if(this.outputAmount < this.returnNum){
            this.$notify({
                    text: `  .`,
                    type: 'error',
                });
            return;
        }

        let returnInfo ={
            client_num : this.clientNum,
            product_code : this.productCode,
            return_num : Number(this.returnNum),
            return_code : this.searchReturnCode,
            emp_num : this.searchEmpNum,
            output_num : this.outputNum
        }

        let result = 
                    await axios.post(`${ajaxUrl}/return/insert`,returnInfo)
                               .catch(err => console.log(err));
        //console.log(result.data);
        if(result.statusText === 'OK'){
                    this.$notify({
                        text: ` .`,
                        type: 'success',
                    });
                    window.location.reload();  
                   
                }
    },
    //
    resetReturn(){
        this.searchEmpName = "";
        this.searchEmpNum = "";
        this.returnNum ="";
        this.searchReturnReason ="";
        this.searchReturnCode ="";

    },
        

  

            

      

        
    
    },//method
    
};
</script>
<style lang="scss" scoped>
.main-container{
    background-color:  #e9ecef;
    border-radius: 10px;
    
}
.grid-container {
  margin-bottom: 20px;
}
.form-section {
  margin-bottom: 20px;
}





.material-button {
    display: flex; /* Flex  */
    align-items: center; /*     */
    justify-content: center; /*    */
    padding: 8px 16px; /*    */
    margin: 0; /*    */
    cursor: pointer;
}


.select-container {
  background-color: #e9ecef;
  border-radius: 10px;
  width: 100%;
  max-width: 600px;
  height: 500px;
  margin-top: 20px;
}

.text-center {
  text-align: center;
}

.button {
  margin: 0 10px; /*     */
}

/*  input   */
input {
  background-color: #ffffff; /*   */
  border: solid 1px #ced4da; /*   */
  color: #495057; /*   */
}
/* readonly  input   */
input:read-only {
  background-color: #ffffff; /*    */
  color: #495057; /*    */
  cursor: not-allowed; /*      */
}

/* input     */
input:focus {
  background-color: #ffffff; /*      */
  border-color: #86b7fe; /*       */
  outline: none; /*      */
}

/* readonly input     */
input:read-only:focus {
  background-color: #ffffff; /*    */
  border-color: #ced4da; /* readonly    */
  outline: none; /*    */
}
</style>
