<template>
  <div class="container-fluid py-4">
    <h3> </h3>
    <!--   -->
    <div class="main-container">
      <div class="content overflow-hidden">

        <div class="row gx-3 align-items-center" style="margin: 0;">
          <!--   -->
          <div class="col-sm-auto">
            <label class="col-form-label fw-bold"> </label>
            <div>
              <label class="me-3" v-for="status in statusList" :key="status">
                {{ status }}
                <input
                  class="form-check-input"
                  :value="status"
                  type="radio"
                  v-model="pickedStatus"
                >
              </label>
            </div>
          </div>
          
          <!--   -->
          <div class="col-sm-auto">
            <label class="col-form-label fw-bold"> </label>
            <div>
              <label class="me-3" v-for="type in machineType" :key="type">
                {{ type }}
                <input
                  class="form-check-input"
                  :value="type"
                  type="checkbox"
                  v-model="pickedType"
                >
              </label>
            </div>
          </div>
        </div>
        
        <div class="row gx-3 align-items-center" style="margin: 10px 0;">
          <!--   -->
          <div class="col-auto">
            <select v-model="selectSearchType" class="form-select search">
              <option v-for="option in searchType" :key="option" :value="option">
                {{ option }}
              </option>
            </select>
          </div>
    
          <!--   -->
          <div class="col-5">
            <input
              type="text"
              v-model="searchData"
              placeholder="  "
              class="form-control search"
              @keydown.enter="enterkey"
            />
          </div>
        </div>

        <div class="row gx-3" style="margin: 20px 0 0; justify-content: center;">
          <!--     -->
          <div class="col-sm-auto">
            <button
              class="btn btn-success toast-btn"
              type="button"
              data-target="warningToast"
              @click="updateFilter"
            >
              
            </button>
          </div>
          <div class="col-sm-auto">
            <button
              class="btn btn-warning toast-btn"
              type="button"
              data-target="warningToast"
              @click="resetSearch"
            >
              
            </button>
          </div>
        </div>

      </div>

    </div>
    

    <!--   -->
    <div class="grid-container work" >
      <ag-grid-vue
        :rowData="rowData"
        :columnDefs="columnDefs"
        :theme="theme"
       	@grid-ready="onReady"
        :rowHeight="50"
        style="height: 493px;"
        :pagination="true"
        :paginationPageSize="8"
        :paginationPageSizeSelector="[8, 16, 40, 80]"
        @cellClicked="cellClickFnc"
        :noRowsOverlayComponent="noRowsOverlayComponent"
      ></ag-grid-vue>
    </div>
    <div style="display: none">
        <CustomNoRowsOverlay/>
    </div>

    <MachineManage
      :isShowModal="isShowMachineAdd"
      :isUpdate="false"
      @closeModal="closeMachineAdd"
      @confirm="confirmMachineAdd"
    />
    <InActAdd :isShowModal="isShowInActAdd" :machineNo="machineNo" @closeModal="closeInActAdd" @confirm="confirmInActAdd"/>
    
  </div>
</template>

<script>
import MachineManage from "./MachineManage.vue";
import InActAdd from "./InActAdd.vue";
import { ajaxUrl } from '@/utils/commons.js';
import axios from 'axios';
import theme from "@/utils/agGridTheme";
import userDateUtils from "@/utils/useDates.js";
import {shallowRef} from 'vue';

import { mapMutations } from "vuex";
import CustomNoRowsOverlay from "@/views/natureBlendComponents/grid/noDataMsg.vue";

export default {
  name: "machineList",
  setup() {
    const machineList = shallowRef([]);
    const rowData = shallowRef([]);
    const columnDefs = shallowRef([
      { headerName: '', field: 'machine_num', cellStyle: { textAlign: "center" }, flex: 2 },
      { headerName: '', field: 'process_name', flex: 4 },
      { headerName: '', field: 'model_num', flex: 4 },
      { headerName: '', field: 'machine_type', flex: 4 },
      { headerName: '', field: 'machine_name', flex: 4 },
      { headerName: '', field: 'machine_location', flex: 3 },
      { 
        headerName: '',
        field: 'machine_state',
        cellStyle: { textAlign: "center" },
        flex: 3,
        cellClass: (params) => {
          return params.value === '' ? 'green' : params.value === '' ? 'red' : ''
        }
      },
    ]);

    const getMachineList = async () => {
      let result = await axios.get(`${ajaxUrl}/machine/machineList`)
                              .catch(err => console.log(err));
      machineList.value = result.data;
      rowData.value = [...result.data];

      for(let i in rowData.value) {
        rowData.value[i].machine_state = rowData.value[i].machine_state == 'run' ? '' : '';
      }
    }

    return {
      machineList,
      rowData,
      columnDefs,
      getMachineList,
    }
  },
  components: {
    MachineManage,
    InActAdd,
    CustomNoRowsOverlay,
  },
  
  data() {
    return {
      //    
      checkJob: this.$store.state.loginInfo.job == '' ? true : this.$store.state.loginInfo.position == '' ? true : false,

      isShowMachineAdd: false,
      isShowInActAdd: false,
      theme: theme,
      inActClick: false,
      machineNo: 0,

      //  
      statusList: ["", "", ""], //   
      pickedStatus: "", //  
      machineType: ["", "", ""], //   
      pickedType: [], //   
      searchType: ["", "", "", ""], //  
      selectSearchType: "", //   
      searchData: "", //  

      filters: [],
      
      noRowsOverlayComponent: 'CustomNoRowsOverlay',

    };
  },
  beforeMount() {
    this.getMachineList();
  },
  methods: {
    //  
    ...mapMutations(["addLoginInfo"]),

    // ag-grid
    onReady() {
      // param.api.sizeColumnsToFit(); // column     => flex    

      //    
      const paginationPanel = document.querySelector('.ag-paging-panel');
      if (paginationPanel) {

        //  
        const container = document.createElement('div');
        container.style.display = 'flex';
        container.style.alignItems = 'center';
        container.style.gap = '5px'; //    

        //  
        const button = document.createElement('button');
        button.textContent = '';
        button.style.marginRight = '10px';
        button.style.cursor = 'pointer';
        button.style.color = 'white';
        button.style.border = 'none';
        button.style.padding = '5px 10px';
        button.style.borderRadius = '4px';
        button.style.position = 'absolute';
        button.style.left = '10px';
        button.className = 'btn-success';

        if(!this.checkJob) {
          button.setAttribute('disabled', true);
        }

        //   
        button.addEventListener('click', () => {
          this.machineAddOpen();
        });

        //     
        container.appendChild(button);
        //      
        paginationPanel.insertBefore(container, paginationPanel.firstChild);
      }
    },


    //    
    machineAddOpen() {
      this.isShowMachineAdd = !this.isShowMachineAdd;
      this.getMachineList();
    },
    //       
    confirmMachineAdd(check) {
      this.closeMachineAdd();
      if(check == true){
        this.getMachineList();
      }
    },
    //    
    closeMachineAdd() {
      this.isShowMachineAdd = false;
    },

    //    
    inActAddOpen() {
      this.isShowInActAdd = !this.isShowInActAdd;
    },
    //    
    confirmInActAdd(check) {
      this.closeInActAdd();
      if(check == true){
        this.searchMachines();
        // this.getMachineList();
      }
    },
    //    
    closeInActAdd() {
      this.isShowInActAdd = false;
    },

    //   :    ,      
    cellClickFnc(col) {
      switch(col.value) {
        case '': // run -> stop
          if(this.checkJob) {
            this.machineNo = col.data.machine_num;
            this.inActAddOpen();
          }
          break;
        case '': // stop -> run
          if(this.checkJob) {
            this.reStart(col.data.machine_num);
          }
          break;
        default: //  
          this.$router.push({name: 'machineInfo', params : {mno : col.data.machine_num}});
      }
    },

    //   
    async reStart(machineNo) {
      let obj = {
        inact_end_time: this.getToday(),
        inact_end_emp: this.$store.state.loginInfo.emp_num,
      }

      let result = await axios.put(`${ajaxUrl}/inActs/lastInAct/${machineNo}`, obj)
                              .catch(err => console.log(err));
      let updateRes = result.data;

      if(updateRes.result) {
        console.log('  ');
        this.runMachine(machineNo);
      } else {
        console.log('   ');
      }
    },
    //  
    async runMachine(machineNo) {
      let obj = {
        machine_state: 'run',
      }
      
      let result = await axios.put(`${ajaxUrl}/machine/machineUpdate/${machineNo}`, obj)
                              .catch(err => console.log(err));
      let updateRes = result.data;

      if(updateRes.result) {
        this.searchMachines();
        console.log('  ');
      } else {
        console.log('  ');
      }
    },

    //   
    resetSearch() {
      this.pickedStatus = "";
      this.pickedType = [];
      this.selectSearchType = "";
      this.searchData = "";
      this.filters = [];
      this.getMachineList();
    },
    updateFilter() {
      const typeMap = {
        "" : "p1",
        "" : "p2",
        "" : "p3",
      }
      const dbType = this.pickedType.map(type => typeMap[type]);
      this.filters = {
        pickedStatus : this.pickedStatus,
        pickedType : dbType,
        selectSearchType : this.selectSearchType,
        searchData : this.searchData,
      }
      switch(this.filters.pickedStatus) {
        case "":
          this.filters.pickedStatus = "";
          break;
        case "":
          this.filters.pickedStatus = "run";
          break;
        case "":
          this.filters.pickedStatus = "stop";
          break;
      }
      switch(this.filters.selectSearchType) {
        case "":
          this.filters.selectSearchType = "all";
          break;
        case "":
          this.filters.selectSearchType = "process_name";
          break;
        case "":
          this.filters.selectSearchType = "model_num";
          break;
        case "":
          this.filters.selectSearchType = "machine_name";
          break;
      }
      this.searchMachines();
    },

    //  
    async searchMachines() {
      let obj = {
        machine_state : this.filters.pickedStatus,
        process_code : this.filters.pickedType,
        selectSearchType : this.filters.selectSearchType,
        searchData : this.filters.searchData
      }

      let result = await axios.put(`${ajaxUrl}/machine/search`, obj)
                              .catch(err => console.log(err));
      this.machineList = result.data;
      this.rowData = result.data;

      for(let i in this.rowData) {
        this.rowData[i].machine_state = this.rowData[i].machine_state == 'run' ? '' : '';
      }
    },
    
    //   
    enterkey(event) {
      event.preventDefault();
      this.updateFilter();
    },

    //  
    getToday() {
      return this.dateFormat(null, 'yyyy-MM-dd hh:mm:ss');
    },
    dateFormat(value, format) {
      return userDateUtils.dateFormat(value, format);
    }
  },
};
</script>

<style lang="scss" scoped>
.main-container{
   background-color:  #e9ecef;
   margin: 0 20px;
   border-radius: 10px;
}
.content{
  margin: 10px 20px;
  padding: 20px 0;
}

select {
  background-color: white;
  padding-left: 20px;
  padding-right: 35px;
  background-color: #ffffff; /*      */
  border: 1px solid #ced4da;
}
select:focus {
  background-color: #ffffff; /*      */
  border-color: #86b7fe; /*       */
  outline: none; /*      */
}


/*  input   */
input {
  background-color: #ffffff; /*   */
  border: solid 1px #ced4da; /*   */
  color: #495057; /*   */
}
/* readonly  input   */
input:read-only {
  background-color: #ffffff; /*    */
  color: #495057; /*    */
}

/* input     */
input:focus {
  background-color: #ffffff; /*      */
  border-color: #86b7fe; /*       */
  outline: none; /*      */
}

/* readonly input     */
// input:read-only:focus {
//   background-color: #ffffff; /*    */
//   border-color: #ced4da; /* readonly    */
//   outline: none; /*    */
// }

</style>
