<template>
    <div class="container-fluid py-4">
        <h3></h3>
            <div class= "main-container">
            <div class="content">
                <div class="orderlistForm">
                    <form class="row gx-3 gy-2 align-items-center">
                        <div class="col-sm-2">
                            <label class="col-form-label fw-bold" for="orderlistNum"></label>
                            <input type="text" class="form-control" id="orderlistNum" v-model="orderlistNum" autocomplete="off" readonly>
                        </div>
                        <div class="sm-2">
                            <label class="col-form-label fw-bold" for="orderlistName"><span> *</span></label>
                            <input type="text" class="form-control" id="orderlistName"
                                 v-model="orderName"  autocomplete="off" placeholder=""  >
                        </div>
                        <div class="col-sm-4">
                            <label class="col-form-label fw-bold" for="clientName"><span> *</span></label>
                            <div class="input-group">
                            <input type="text" class="form-control" id="clientName" 
                                @click="openModal('client')" v-model="searchCom.com_name"  autocomplete="off" placeholder="" >
                        </div>
                        <Modal
                            :isShowModal="isShowModal.client"
                            :modalTitle="''"
                            :noBtn="''"
                            :yesBtn="''"
                            @closeModal="closeModal('client')"
                            @confirm="confirm('client')"
                        >
                            <template v-slot:list>
                                <ComList v-show="isShowModal.client" @selectclient="selectclient"/>
                            </template>
                        </Modal>
                        </div>
                        <div class="col-sm-2">
                            <label class="col-form-label fw-bold" for="EmpName"><span> *</span></label>
                            <div class="input-group">
                            <input type="text" v-if="searchEmpName.name" class="form-control" id="EmpName" @click="openModal('emp')" v-model="searchEmpName.name"  autocomplete="off"  >
                            <input type="text" v-else class="form-control" id="EmpName" @click="openModal('emp')" v-model="searchEmpName"  autocomplete="off"  >
                            </div>
                            <Modal
                                :isShowModal="isShowModal.emp"
                                :modalTitle="''"
                                :noBtn="''"
                                :yesBtn="''"
                                @closeModal="closeModal('emp')"
                                @confirm="confirm('emp')"
                            >
                            <template v-slot:list>
                                <EmpList v-show="isShowModal.emp" @selectemp="selectemp"/>
                            </template>
                            </Modal>
                        </div>
                        <div class="col-sm-4">
                            <label class="col-form-label fw-bold" for="dueDate"><span> *</span></label>
                            <div class="input-group">
                            <input type="date" class="form-control" id="dueDate" v-model="dueDate"  autocomplete="off"  >
                            </div>
                        </div>
                    </form>
                    <div class="mb-3 text-end">
                        <material-button size="sm" color="success" class="button"  @click="addMaterial"></material-button>
                    </div>

                </div>
                

                <div  v-for="(material, index) in materials" :key="index" class="row gx-3 gy-2 align-items-center plus-section">
                    <form class="row gx-3 gy-2 align-items-center mt-2">
                        <div class="col-sm-2">
                            <label class="col-form-label fw-bold" for="orderCode"></label>
                            <input type="text" class="form-control" id="orderCode" v-model="material.orderCode"  autocomplete="off" readonly >
                        </div>
                        <div class="col-sm-2">
                            <label class="col-form-label fw-bold" for="productCode"><span> *</span></label>
                            <input type="text" class="form-control" id="productCode" v-model="material.productCode"  autocomplete="off" readonly placeholder="" >
                        </div>
                        <div class="col-sm-2">
                            <label class="col-form-label fw-bold" for="productName"><span> *</span></label>
                            <input type="text" class="form-control" id="productName" v-model="material.productName" @click="openModal('product',index)"  autocomplete="off" placeholder="">
                        </div>
                        <Modal
                                :isShowModal="isShowModal.product"
                                :modalTitle="''"
                                :noBtn="''"
                                :yesBtn="''"
                                @closeModal="closeModal('product')"
                                @confirm="confirm('product')"
                            >
                            <template v-slot:list>
                                <proList v-show="isShowModal.product" @selectproduct="selectproduct" :indexNum="indexNum"/>
                            </template>
                        </Modal>
                        <div class="col-sm-2">
                            <label class="col-form-label fw-bold" for="orderNum"><span> *</span></label>
                            <div class="input-group">
                            <input type="number" class="form-control" id="orderNum" 
                                v-model="material.productNum" autocomplete="off" placeholder=""  >
                            </div>
                        </div>
                        <div class="col-sm-2">
                            <label class="col-form-label fw-bold"  for="perSale"><span> *</span></label>
                            <div class="input-group">
                            <input type="text" class="form-control" id="perSale" 
                                v-model="material.perPrice" autocomplete="off" placeholder="" >
                            </div>
                        </div>
                        <div class="col-sm-2">
                            <material-button size="sm" color="danger" type="button" class="mt-5"  @click="deleteMaterial(index)"></material-button>
                        </div>      
                    </form>
                </div>
               

                <div class="col-auto mt-5 text-center">
                        <material-button type="button" class="button me-4" color="success" @click="insertOrder"></material-button>
                        <material-button type="button" class="button" color="warning" @click="resetSearch"></material-button>
                </div>
            </div>
        </div>
    </div>
   
    
</template>

<script>
import MaterialButton from "@/components/MaterialButton.vue";
import ComList from "@/views/sales/Order/clientModal.vue";
import EmpList from "@/views/sales/Order/EmpModal.vue";
import proList from "@/views/sales/Order/ProductModal.vue"
import Modal from "@/views/natureBlendComponents/modal/Modal.vue";
import userDateUtils from '@/utils/useDates.js';
import axios from "axios";
import {ajaxUrl} from "@/utils/commons";
import { mapMutations } from "vuex";




export default{
    name:"orderForm",
    components:{
        MaterialButton,
        ComList,
        EmpList,
        proList,
        Modal,
       
    },
    data(){
        return{
            testing: {},

            orderlistNum :'',
            orderName : '',
            dueDate:'',

            orderCode:'' ,
            productCode:'',
            productName:'',
            productNum:'',
            perPrice:'',

            materials:[
            {
                orderCode: '', // 
                productCode: '', // 
                productName: '', // 
                productNum: '', // 
                perPrice: '' // 
            }
            ],
            indexNum: null, //  index 

            //  
            searchCom:"", //     
            selectedCom: "", //  

            //  
            searchEmpName:"", //    
            selectedEmpName:"", //   

            // ()
            selectedProCode:"",
            selectedProName:"",

            isShowModal: {
            client: false, //  
            emp: false, //  
            product:false //   
            },
        }
    },

    computed:{

    },
    mounted() {
      this.test();
    },

    methods:{
    
    ...mapMutations(["addLoginInfo"]),
    test(){
      this.testing = this.$store.state.loginInfo;
      //console.log('ddd', this.$store.state.loginInfo);
      this.searchEmpName = this.$store.state.loginInfo.name;
    },

    selectclient(client){
        this.selectedCom = client; 
    },
    selectemp(emp){
        this.selectedEmpName = emp;
    },
    selectproduct(product){
        //console.log(product); 
        this.selectedProCode = product.product_code;
        this.selectedProName = product.product_name;
    },
    // index  
    openModal(modalType,index) {
        this.isShowModal[modalType] = true; 
        this.indexNum = index; //  index
        //console.log(this.indexNum);
        console.log(`${modalType} modal open`);
    },
    confirm(modalType){
        if (modalType === 'client') {
        this.searchCom = this.selectedCom;
      } else if (modalType === 'emp') {
        this.searchEmpName = this.selectedEmpName;
      } else if(modalType === 'product'){
        this.productCode = this.selectedProCode;
        this.productName = this.selectedProName;
        
        //console.log(this.materials[this.indexNum])
        this.materials[this.indexNum]['productCode'] = this.productCode;
        this.materials[this.indexNum]['productName'] = this.productName;

        //    
        // for(let i=0; i<this.materials.length; i++){
        //     if(this.materials[i]['productCode'] == ''){
        //     this.materials[i]['productCode'] = this.productCode;
        //     this.materials[i]['productName'] = this.productName;
        //     break;
        //     }
        // }


        //console.log(this.materials);
        
      }

      this.closeModal(modalType); //  
    },
    closeModal(modalType) {
        this.isShowModal[modalType] = false;
    },

    dateFormat(value, format) {
          return userDateUtils.dateFormat(value, format);
    },
    addMaterial() {
        
    const newMaterial={
      orderCode: '',
      productCode : '',
      productName : '',
      productNum: '',
      perPrice: ''
    };
    // material  productCode   newMaterial    
        if(this.materials.length == 0){
            this.materials.push(newMaterial);
            //console.log(this.materials[this.materials.length]);
        }else if(this.materials[this.materials.length -1 ].productCode != ''){
            this.materials.push(newMaterial);
        }    
    },

    // materials   
    deleteMaterial(index){
        //console.log("deleteMaterial");
        // let index = parseInt(this.value);
        // const selectedNum = this.materials[index];
        this.materials = this.materials.filter((material, idx) => index != idx);
        //console.log(this.materials);
       
        
    },

    //
    resetSearch(){
        this.orderlistNum = "",
        this.orderName ="",
        this.dueDate="",

        this.orderCode="" ,
        this.productCode="",
        this.productName="",
        this.productNum="",
        this.perPrice="",
        this.materials= [],

        this.searchEmpName="",
        this.searchCom=""

    },
    //,   
    async insertOrder(){
        // for(let i=0; i<this.materials.length; i++){
        //     let productCode = this.materials[i]['productCode'];
        //     let productName = this.materials[i]['productName'];
        //     let productNum = this.materials[i]['productNum'];
        //     let perPrice = this.materials[i]['perPrice'];
        //     console.log(this.orderName,this.dueDate,this.searchEmpName,productCode,productName,productNum,perPrice);
            
        // }

        if (!this.orderName || !this.dueDate || !this.searchEmpName) {
        this.$notify({ text: '   .', type: 'error' });
        return;
        }
        if (this.materials.length === 0) {
            this.$notify({ text: '  .', type: 'error' });
            return;
        }
       
        if(new Date(this.dueDate) < new Date() ){
            this.$notify({ text: '  .', type: 'error' });
            return;
        }

        let productCodes = []
        let productNums = []
        let perPrices = []
        //    
        let invalidRows = [];
        this.materials.forEach((orderInfo)=>{
            if(!orderInfo.productCode || !orderInfo.productNum || !orderInfo.perPrice){
                invalidRows.push(orderInfo);
            }else{
                productCodes.push(orderInfo.productCode);
                productNums.push(orderInfo.productNum);
                perPrices.push(orderInfo.perPrice);
            } 
        });
         //       
         if (invalidRows.length > 0) {
            this.$notify({
                text: ',,  .',
                type: 'error',
            });
            return;
        }

        let orderInfo = {
            orderName : this.orderName,
            dueDate : this.dueDate,
            searchCom: this.searchCom.com_name,
            productCode: JSON.stringify(productCodes),
            productNum : JSON.stringify(productNums),
            perPrice : JSON.stringify(perPrices),
        }
         //   `name`   
         if (!this.searchEmpName.name) {
                orderInfo.searchEmpName = this.searchEmpName;
            } else {
                orderInfo.searchEmpName = this.searchEmpName.name;
            }

        //console.log(orderInfo);

        let result =
            await axios.post(`${ajaxUrl}/orderForm/insert`,orderInfo)
                        .catch(err => console.log(err));
                        //console.log(result);
        if(result.statusText === 'OK'){
        this.$notify({
            text: `${this.orderName} .`,
            type: 'success',
        });
        this.resetSearch();  //       
        }
    }
    
        
    }//end method
}
</script>

<style lang="scss" scoped>
.main-container {
//   background-color: #e9ecef;
  border: solid 5px #e9ecef;
  margin: 20px;
  border-radius: 10px;
}
.orderlistForm{
    background-color: #e9ecef;
    padding: 60px;
    padding-bottom: 20px;
    border-radius: 10px;
}
.content {
  margin: 20px;
  padding: 10px;
}
.plus-section{
    padding-left: 60px;
}

.client {
  padding: 10px;
  cursor: pointer;
  &:hover {
    background-color: #f8f9fa;
  }
}
.selected {
  background-color: #ffeeba;
  font-weight: bold;
}

/*  input   */
input {
  background-color: #ffffff; /*   */
  border: solid 1px #ced4da; /*   */
  color: #495057; /*   */
}
/* readonly  input   */
input:read-only {
  background-color: #ffffff; /*    */
  color: #495057; /*    */
  cursor: not-allowed; /*      */
}

/* input     */
input:focus {
  background-color: #ffffff; /*      */
  border-color: #86b7fe; /*       */
  outline: none; /*      */
}

/* readonly input     */
input:read-only:focus {
  background-color: #ffffff; /*    */
  border-color: #ced4da; /* readonly    */
  outline: none; /*    */
}
</style>
