<template>
        
    <div class="grid-container" v-show="rowData.length != 0">
    <ag-grid-vue
    
      :rowData="rowData"
      :columnDefs="columnDefs"
      :theme="theme"
      @grid-ready="onReady1"
      :noRowsOverlayComponent="noRowsOverlayComponent"
      rowSelection="multiple"
      @cellClicked="onCellClicked" 
      :quickFilterText="inputListsearch1"
      @rowClicked="onRowClicked"
      :pagination="true"
      :paginationPageSize="10"
      :paginationPageSizeSelector="[10, 20, 50, 100]"
  />
  
  </div>
  <div style="display: none">
      <CustomNoRowsOverlay/>
  </div>

    <Modal
            :isShowModal="isShowModal"
            :modalTitle="''"
            :noBtn="''"
            :yesBtn="''"
            @closeModal="closeModal()"
            @confirm="confirm()"
        >
        <template v-slot:list>
            <proList v-show="isShowModal" @selectproduct="selectproduct"/>
        </template>
    </Modal>

  <!--  -->
    <div class="mb-3 pt-2 text-center">
        <material-button  color="success" class="button me-5" @click="updateOrder"></material-button>
        <material-button  color="danger" class="button" @click="deleteOrder"></material-button>
    </div>
</template>

<script>
import MaterialButton from "@/components/MaterialButton.vue";
import theme from "@/utils/agGridTheme";
import CustomNoRowsOverlay from "@/views/natureBlendComponents/grid/noDataMsg.vue";
import axios from "axios";
import {ajaxUrl} from "@/utils/commons";
import proList from "@/views/sales/Order/ProductModal.vue"
import Modal from "@/views/natureBlendComponents/modal/Modal.vue";
import userDateUtils from '@/utils/useDates.js';

export default{
    name:"orderInfo",
    components:{
        CustomNoRowsOverlay,
        MaterialButton,
        proList,
        Modal,
        
    },
    props: {
        order: {
            type: Object,
            required: true,
        },
    },

    data(){
        return{
            noRowsOverlayComponent:'CustomNoRowsOverlay',
            Scol : 0,
            originalValue: null,  //    

            // orderNum : this.order.orderlist_num,
           //
           statusOrderMap: {         // DB    
                "preparing": "",
                "semiShipped": "",
                "shipped": "",
            },

            theme : theme,
            rowData : [],
            columnDefs : [
                { headerName: "",
                headerCheckboxSelection: true,//    
                field: "check",
                resizable: false,
                editable: true,
                sortable: false,
                checkboxSelection: true,
                cellStyle: { textAlign: "center" },flex: 1
                },
                { headerName : "", field:'order_num',resizable: true, sortable: true ,cellStyle: { textAlign: "right" },flex: 2},
                { headerName : "", field:'product_code',resizable: true, sortable: true ,cellStyle: { textAlign: "center" },flex: 3},
                { headerName : "", field:'product_name',resizable: true, sortable: true ,cellStyle: { textAlign: "left" },flex: 4},
                { headerName : ""
                , field:'order_amount'
                , editable: true
                , sortable: true 
                ,cellStyle: { 
                    textAlign: "right",
                    //backgroundColor: "#fff", //  
                    //border: "0.5px dashed #fb8c00", //  
                    cursor: "text", //  
                }
                ,flex: 2 
                ,cellRenderer: params => {
                    const orderStatus = params.data.order_status; //  
                    if (orderStatus === '') {
                        if (params.value) {
                            const formattedValue = params.value.toLocaleString(); //   
                            return `
                                <span style="display: flex; align-items: center; justify-content: flex-start;">
                                    <i class="fas fa-edit" style="color:#6c757d88" title="  "></i>
                                    <span style="flex-grow: 1; text-align: right;">${formattedValue}</span>
                                </span>
                            `;
                        } else {
                            return `<span><i class="fas fa-edit" style="color:#6c757d88" title="  "></i></span>`;
                        }
                    } else {
                        if (params.value) {
                            const formattedValue = params.value.toLocaleString(); //   
                            return `<span style="text-align: right;">${formattedValue}</span>`;
                        } else {
                            return `<span></span>`;
                        }
                    }
                }},
                { headerName : ""
                , field:'per_price' 
                ,editable: true
                , sortable: true 
                ,cellStyle: { 
                    textAlign: "right",
                    //backgroundColor: "#fff", //  
                    //border: "0.5px dashed #fb8c00", //  
                    cursor: "text", //  
                }
                ,flex: 2 
                ,cellRenderer: params => {
                    const orderStatus = params.data.order_status; //  
                    if (orderStatus === '') {
                        if (params.value) {
                            const formattedValue = params.value.toLocaleString(); //   
                            return `
                                <span style="display: flex; align-items: center; justify-content: flex-start;">
                                    <i class="fas fa-edit" style="color:#6c757d88" title="  "></i>
                                    <span style="flex-grow: 1; text-align: right;">${formattedValue}</span>
                                </span>
                            `;
                        } else {
                            return `<span><i class="fas fa-edit" style="color:#6c757d88" title="  "></i></span>`;
                        }
                    } else {
                        if (params.value) {
                            const formattedValue = params.value.toLocaleString(); //   
                            return `<span style="text-align: right;">${formattedValue}</span>`;
                        } else {
                            return `<span></span>`;
                        }
                    }
                }},
                { headerName : ""
                ,field:'total_price'
                ,resizable: true
                , sortable: true 
                ,cellStyle: { 
                    textAlign: "right",
                    }
                ,flex: 2
                ,cellRenderer: params =>{
                    if(params.value){
                        const formattedValue = params.value.toLocaleString(); //   
                        return `<span>${formattedValue}</span>`;
                    }
                }},
                { headerName : "", field:'order_status',resizable: true, sortable: true ,cellStyle: { textAlign: "left" },flex: 2},
                {
                    headerName : "",
                    field : "status",
                    editable : false,
                    flex: 1
                    ,cellStyle: { textAlign: "center" },
                    cellRenderer :params =>{
                        let orderNum = params.data.order_num;
                        if(orderNum === "" || orderNum === null){
                            const deleteButton = document.createElement('button');
                            deleteButton.innerText = '';
                            deleteButton.style.marginRight = '10px';
                            deleteButton.style.cursor = 'pointer';
                            deleteButton.style.backgroundColor = '#ff0000';
                            deleteButton.style.width = '60px';
                            deleteButton.style.height = '30px';
                            deleteButton.style.color = 'white';
                            deleteButton.style.border = 'none';
                            deleteButton.style.padding = '0';
                            deleteButton.style.borderRadius = '4px';
                            deleteButton.style.textAlign = 'center';
                            deleteButton.style.lineHeight = '30px';
                            deleteButton.addEventListener('click',()=>{
                                //    
                                this.renderButton(params);
                            })
                            return deleteButton;
                        }
                        //   (  )
                        return null;
                    }
                }
            ],          
            //  ( )
            inputListsearch1:"", //2

            //   
            selectedProCode : '',
            selectedProName : '',
            
            isShowModal: false,



        }
    },

  

    created(){
       
        this.getOrderInfo();
    }, 
    watch: {
        order:{
            handler(){
                this.getOrderInfo();
            },
            deep:true
        },
    },
   

    methods:{
        //         
    onCellClicked(params) {
        if (params.colDef.editable) {
            if (!this.originalValues) {
                this.originalValues = {};
            }
            this.originalValues[params.node.id] = this.originalValues[params.node.id] || {};
            this.originalValues[params.node.id][params.column.colId] = params.value; //     
            //console.log("==",params.value)
        }
        // 'product_name'     
        if (params.colDef.field === 'product_name') {
            this.Scol = params.node.rowIndex;  //   
            //console.log(this.Scol);
            this.openModal();  //  
        }
    },
    //         
    rollbackCellChange(params) {
        let nodeId = params.node.id;
        let colId = params.column.colId;
        if (this.originalValues && this.originalValues[nodeId] && this.originalValues[nodeId][colId] !== undefined) {
            let originalValue = this.originalValues[nodeId][colId];
            params.node.setDataValue(params.column,originalValue); //   
            this.originalValue = null;  //   
        }
    },

    rollbackEdit() {
        if (this.originalValues) {
            this.gridApi.forEachNode((node) => {
                //      
                Object.keys(this.originalValues[node.id] || {}).forEach(colId => {
                const originalValue = this.originalValues[node.id][colId];
                node.setDataValue(colId, originalValue); //     
                });
            });
             //  
            this.gridApi.deselectAll();
            this.originalValue = null; //     
        }
      
    },
        onReady1(event){
            this.gridApi = event.api;
            //event.api.sizeColumnsToFit(); // api   
            //    
            const allPanels = document.querySelectorAll('.ag-paging-panel');

             //lot
             const paginationPanel1 = allPanels[2];
            if (paginationPanel1) {
               //  
               const container1 = document.createElement('div');
               container1.style.display = 'flex';
               container1.style.alignItems = 'center';
               container1.style.gap = '5px'; //    

                //  
                const button1 = document.createElement('button');
               button1.textContent = '';
               button1.style.cursor = 'pointer';
               button1.style.backgroundColor = '#4caf50';
               button1.style.color = 'white';
               button1.style.border = 'none';
               button1.style.padding = '5px 10px';
               button1.style.borderRadius = '4px';
                //
                button1.addEventListener('click',()=>{
                //
                this.addMaterial();
                 });
                  //  
               const button2 = document.createElement('button');
               button2.textContent = '';
               button2.style.cursor = 'pointer';
               button2.style.backgroundColor = '#f44335';
               button2.style.color = 'white';
               button2.style.border = 'none';
               button2.style.padding = '5px 10px';
               button2.style.borderRadius = '4px';
                //
                button2.addEventListener('click',()=>{
                //
                this.deleteOrders();
                 });
                     //  
               const button3 = document.createElement('button');
               button3.textContent = '';
               button3.style.cursor = 'pointer';
               button3.style.backgroundColor = '#fb8c00';
               button3.style.color = 'white';
               button3.style.border = 'none';
               button3.style.padding = '5px 10px';
               button3.style.borderRadius = '4px';
                //
                button3.addEventListener('click',()=>{
                //
                this.rollbackEdit();
                 });

               
                // 
                const inputText1 = document.createElement('input');
                inputText1.type = 'text';
                inputText1.placeholder = '';
                inputText1.style.padding = '5px';
                inputText1.style.width = '250px';
                inputText1.style.border = '1px solid #ccc';
                inputText1.style.borderRadius = '4px';

             
                //    ag 
                inputText1.addEventListener('input',(event)=>{
                    const value = event.target.value;
                    //console.log(" :", value);

                    //
                    this.inputListsearch1 = value;
                });

                // ,   
                container1.appendChild(button1);
                container1.appendChild(button2);
                container1.appendChild(button3);
                container1.appendChild(inputText1);

                //  
                paginationPanel1.insertBefore(container1,paginationPanel1.firstChild);
            }
        },

        //   ()   order 
        async getOrderInfo() { 
            
            //console.log("",this.order);
            let result = await axios.get(`${ajaxUrl}/orderInfo/${this.order.orderlist_num}}`)
                                    .catch(err=> console.log(err));
                       
            this.rowData = result.data;
            this.rowData = result.data.map((col) => ({
            ...col,
            order_status : this.statusOrderMap[col.order_status],
            })
        );
        //console.log("rowData:",this.rowData);
            
            
        },
        //  
        addMaterial() {
        
            const newMaterial={
            order_num: null,
            product_code : '',
            product_name : '',
            order_amount: null,
            per_price: null
            };
            this.rowData.push(newMaterial);
            this.rowData = [ ...this.rowData];

            // material  productCode   newMaterial    
            // if(this.materials.length == 0){
            //     this.rowData.push(newMaterial);
            //     //console.log(this.materials[this.materials.length]);
            // }else if(this.materials[this.materials.length -1 ].newProductCode != ''){
            //     this.materials.push(newMaterial);
            // }   
        },

        openModal() {
            this.isShowModal = true; 
            //console.log("modalopen")
        },

        selectproduct(product){
        //console.log(product); 
        this.selectedProCode = product.product_code;
        this.selectedProName = product.product_name;
        //console.log("", this.selectedProCode,this.selectedProName);
        },

        confirm(){
            //console.log("====",this.Scol);
            // this.rowData[this.Scol] = this.rowData[this.Scol].map((col) => ({
            //     ...col,
            //     product_code : this.selectedProCode,
            //     product_name : this.selectedProName,
            //      })
            // );  
            //console.log(this.rowData[this.Scol].order_num ,this.rowData[this.Scol].order_status )

            if(this.rowData[this.Scol].order_status == ""){
                    this.rowData[this.Scol] ={
                    order_num : this.rowData[this.Scol].order_num,
                    product_code : this.selectedProCode,
                    product_name : this.selectedProName,
                    order_amount : this.rowData[this.Scol].order_amount,
                    per_price : this.rowData[this.Scol].per_price,
                    order_status : this.rowData[this.Scol].order_status,

                    }
            } else if (
                this.rowData[this.Scol].order_status === "" ||
                this.rowData[this.Scol].order_status === ""
            ) {
                //       
                this.$notify({
                    text: `      .`,
                    type: "warn",
                });
            }else {
                    this.rowData[this.Scol] = {
                    order_num: null,
                    product_code : this.selectedProCode,
                    product_name : this.selectedProName,
                    order_amount: null,
                    per_price: null
                    }
            }
                
            

           
            //        
            this.rowData = [... this.rowData]
            
            //console.log(":",this.rowData[this.Scol] )
        
            this.closeModal();
        },
        closeModal() {
        this.isShowModal = false;
    },

    // onClickedWh(col){

    //    this.Scol = col.node.rowIndex
    //    console.log(this.Scol);

    //     if(col.colDef.field === 'product_name'){
    //         this.openModal();
    //     }
    // },

    //   
    renderButton(params){
        let rowDelete = params.data;

        this.rowData = this.rowData.filter((row)=> row!=rowDelete);

    },


    dateFormat(value, format) {
        return userDateUtils.dateFormat(value, format);
    },
   

    //
    async updateOrder(){
    
        //  
        const selectedRows = this.gridApi.getSelectedRows(); 
        //console.log(selectedRows);
        //     ()
        let orderAmounts = []
        let perPrices = []
        let productCodes = []
        let orderCodes =[]

         // preparing     
        let hasInvalidStatus = false;

        selectedRows.forEach(row=>{
            //console.log(row.order_status);
            if(row.product_code === '' || row.product_code === null || row.order_amount === '' || row.order_amount === null || row.per_price === '' || row.per_price === null ){
                this.$notify({
                    text: ` ,    .`,
                    type: 'error',
                });
                return;
            }
            if(row.order_status === '' || row.order_num === '' || row.order_num === null){
                orderAmounts.push(Number(row.order_amount));
                productCodes.push(row.product_code);
                perPrices.push(row.per_price);
                orderCodes.push(row.order_num);
            }else {
            // preparing      
            hasInvalidStatus = true;
            }
        })
        if (hasInvalidStatus) {
        // preparing      
        this.$notify({
            text: `     . .`,
            type: 'warn',
        });
        return; //    
        }
        

        let updateOrderInfo = {
            orderCode: JSON.stringify(orderCodes),
            productCode :JSON.stringify(productCodes),
            orderAmount : JSON.stringify(orderAmounts),
            perPrice : JSON.stringify(perPrices),
        }
        //console.log(":",updateOrderInfo);
        //     
        this.$emit('updateOrder',updateOrderInfo);

       
      },

      //  ()
      async deleteOrder(){
        //console.log("  ", this.order);
        if(this.order.orderlist_status === ''){
            let result = await axios.delete(`${ajaxUrl}/orderlist/delete/${this.order.orderlist_num}`)
                                    .catch(err => console.log(err));
            if(result.data.result === 'success'){
                        this.$notify({
                            text: `${this.order.orderlist_title} .` ,
                            type: 'success',
                        });
                        window.location.reload();
                }else if (result.data.result === 'fail'){
                        this.$notify({
                            text: '  ',
                            type: 'error',
                        });
                }
        }else if (this.order.orderlist_status === '' || this.order.orderlist_status === ''){
            this.$notify({
                    text: '            .',
                    type: 'warn',
                });
        }
     
       
      }, 
      // 
      async deleteOrders(){
        const selectedRows = this.gridApi.getSelectedRows();
        //console.log("==",selectedRows);

        let errorMessage = null; //    

        let orderNums = []

        selectedRows.forEach(row=>{
            if(row.order_status == ""){
                orderNums.push(row.order_num);
            }else if(row.order_status == "" || row.order_status == ""){
                errorMessage = "        .";
            }else if (row.order_num == null){
                errorMessage = "     .";
            }
        });
         //      
        if (errorMessage) {
            this.$notify({
                text: errorMessage,
                type: 'error',
            });
            return;
        }
        //    (  )
        if (orderNums.length > 0) {
            let deleteOrder = {
                orderNum : JSON.stringify(orderNums)
            }

            //console.log("  ==",deleteOrder);

            let result = await axios.put(`${ajaxUrl}/order/delete`,deleteOrder)
                                    .catch(err => console.log(err));
            if(result.data.result === true){
                    this.$notify({
                    text: `   . `,
                    type: 'success',
                });
                window.location.reload(); 
            }else{
                    this.$notify({
                        text: `    . `,
                        type: 'error',
                    });
            }   
        }

      },
      
 
        
    }//end method
}
</script>

<style lang="scss" scoped>
.main-container {
  background-color: #e9ecef;
  margin: 20px;
  border-radius: 10px;
}
.content {
  margin: 20px;
  padding: 10px;
}
.client {
  padding: 10px;
  cursor: pointer;
  &:hover {
    background-color: #f8f9fa;
  }
}
.selected {
  background-color: #ffeeba;
  font-weight: bold;
}


/*  input   */
input {
  background-color: #ffffff; /*   */
  border: solid 1px #ced4da; /*   */
  color: #495057; /*   */
}
/* readonly  input   */
input:read-only {
  background-color: #ffffff; /*    */
  color: #495057; /*    */
  cursor: not-allowed; /*      */
}

/* input     */
input:focus {
  background-color: #ffffff; /*      */
  border-color: #86b7fe; /*       */
  outline: none; /*      */
}

/* readonly input     */
input:read-only:focus {
  background-color: #ffffff; /*    */
  border-color: #ced4da; /* readonly    */
  outline: none; /*    */
}
</style>
