<!--  -->
<template>
  <div class="partList container-fluid py-4">
    <div>
       <h3>&nbsp;&nbsp; </h3>
    </div>
 <!--    -->
 <div class="main-container">
    <div class="content" style="height: 130px; margin: 20px;">
       <form class="row gx-3 gy-2 align-items-center">
          <!--  -->
          <div class="col-sm-2">
             <label class="col-form-label fw-bold" for="productCode"></label>
             <input type="text" class="form-control" style="background-color: white; padding-left: 20px;" id="productCode" v-model="productCode" >
          </div>
 
          <!--  -->
          <div class="col-sm-2">
             <label class="col-form-label fw-bold" for="productName"></label>
             <input type="text" class="form-control" style="background-color: white; padding-left: 20px;" id="productName" v-model="productName" >
          </div>
 
          <!--  -->
          <div class="col-sm-2">
             <label class="col-form-label fw-bold" for="expirationDate">()</label>
             <input type="text" class="form-control" style="background-color: white; padding-left: 20px;" id="expirationDate" v-model="expirationDate" >
          </div>
 
          <!--  -->
          <div class="col-sm-2">
             <label class="col-form-label fw-bold" for="capacity">(mL)</label>
             <input type="text" class="form-control" style="background-color: white; padding-left: 20px;" id="capacity" v-model="capacity" >
          </div>
 
          <!--   -->
          <div class="col-sm-3">
            <div class="d-flex">
              <div class="p-2 flex-fill">
                <button style="position:relative; top:29px;" type="button" class="btn btn-success me-5" @click="upin? input_update(2) : input_update(1)">/</button>
                <button style="position:relative; width:120px ; top:29px;" type="button" class="btn btn-warning me-5" @click="refresh"  ></button>
              </div>
            </div>
          </div>
       </form>
    </div>
 </div>
 <!--     -->
 <div class="grid-container" style="padding-top: 10px;">
    <ag-grid-vue
       :rowData="rowData"
       :columnDefs="columnDefs"
       :theme="theme"
       :pagination="true"
       :paginationPageSize="10"
       @grid-ready="onReady"
       style="height: 513px;"
       rowSelection="multiple"
       @cellClicked="onCellClicked"
    >
    </ag-grid-vue>
 </div>
 </div>
 </template>
 <script>
import { ajaxUrl } from '@/utils/commons.js';
// import userDateUtils from '@/utils/useDates.js';
import theme from "@/utils/agGridTheme";
import axios from "axios";
import { mapMutations } from "vuex";

 
 export default {
   data() {
     return {
       productCode: '',   // 
       productName: '',  // 
       expirationDate: '',  // 
       capacity: '', // 
       columnDefs: [
         { headerName: "", field: "product_code", width: 220 },
         { headerName: "", field: "product_name" },
         { headerName: "()", field: "expiration_date" ,cellStyle: { textAlign: 'right' }},
         { headerName: "(mL)", field: "capacity" ,cellStyle: { textAlign: 'right' }},
         {
           headerName: "",
           field: "product_delete",
           upin : '',
           cellStyle: { textAlign: 'center' },
           editable: false,
           cellRenderer: (params) => {
             const button2 = document.createElement('button');
             button2.innerText = '';
             button2.style.marginRight = '10px'; // 
             button2.style.cursor = 'pointer';
             button2.style.backgroundColor = '#f7b84d';
             button2.style.width = '60px';
             button2.style.height = '30px';
             button2.style.color = 'white';
             button2.style.border = 'none';
             button2.style.padding = '0';
             button2.style.borderRadius = '4px';
             button2.style.textAlign = 'center';
             button2.style.lineHeight = '30px';
             button2.addEventListener('click', () => {
               console.log(" [] : ", JSON.stringify(params.data));
               console.log(params.data.product_code);
               if (confirm(' ?')) {
                 axios.delete(`${ajaxUrl}/productDelete/${params.data.product_code }`)
                   .then((res) => {
                     if (res.data === '') {
                      this.$notify({ text: ' .', type: 'success' });
                       this.productSelect();
                     } else {
                      this.$notify({ text: ' .', type: 'error' });
                     }
                   })
                   .catch((err) => {
                     console.log(err);
                   });
               }
               //   
               // lotinfo(params.data);
             });
             return button2;
           }
         },
       ],
       newList: [],
       rowData: [], //    api 
       theme:theme,
     };
   },
   methods: {
    refresh(){
      this.productSelect();
      this.upin = '';
      this.productCode = '';
      this.productName = '';
      this.expirationDate = '';
      this.capacity = '';
    },
    ...mapMutations(["addLoginInfo"]),
      async checkLogin(){
          this.loginInfo = this.$store.state.loginInfo;
          console.log('',this.loginInfo);
          if(this.loginInfo.job === ''){
            console.log('');
          }else{
              this.$notify({ text: '  ', type: 'error' });
              this.$router.push({ name : 'MainPage' });
          }
      },
    async productSelect() {
        const result = await axios.get(`${ajaxUrl}/bomproduct`)
                                .catch(err => console.log(err));
        this.rowData = result.data;
    },
    onReady(param) {
       param.api.sizeColumnsToFit(); //  api      
    },
    onCellClicked(params) {
        if (params.colDef.field !== 'product_delete') {
             console.log(" [] : ", JSON.stringify(params.data));
             this.productCode = params.data.product_code;
             this.productName = params.data.product_name;
             this.expirationDate = params.data.expiration_date;
             this.capacity = params.data.capacity;
             this.upin = 'update';
        }
    },
    async input_update(number) {
        console.log('    ');
        console.log(this.productCode);
        console.log(this.productName);
        console.log(this.expirationDate);
        console.log(this.capacity);
         if(number === 1){
             if(this.productCode === '' || this.productName === '' || this.expirationDate === '' || this.capacity === ''){
              this.$notify({ text: ' ', type: 'error' });
               return;
             }else{
              console.log(' ');
              let dataresult = 'ok';
              for(let i = 0; i < this.rowData.length; i++){
                if(this.rowData[i].product_code === this.productCode){
                  this.$notify({ text: '  .', type: 'error' });
                  dataresult = 'no';
                  return;
                }
              }
              if(dataresult === 'ok'){
                  this.newList = {
                      product_code: this.productCode,
                      product_name: this.productName,
                      expiration_date: this.expirationDate,
                      capacity: this.capacity,
                  }
                  console.log(this.newList);
                  this.productInsert(this.newList);
                return;
              }
             }
        }else if(number === 2){
        console.log('  ');
        this.newList = {
                      product_name: this.productName,
                      expiration_date: this.expirationDate,
                      capacity: this.capacity,
                  }
        this.productUpdate(this.newList);    
        }
     },
     async productUpdate(newList) {
        const result = await axios.post(`${ajaxUrl}/productUpdate/${this.productCode}`, newList)
                                .catch(err => console.log(err));
        if (result.data === ''){
            this.$notify({ text: ' .', type: 'success' });
            this.productSelect();
            this.upin = '';
            this.productCode = '';
            this.productName = '';
            this.expirationDate = '';
            this.capacity = '';
        }else{
            this.$notify({ text: ' .', type: 'error' });
        }
     },

     async productInsert(newList) {
        const result = await axios.post(`${ajaxUrl}/productInsert`, newList)
                                .catch(err => console.log(err));
        if (result.data === ''){
          this.$notify({ text: ' .', type: 'success' });
            this.productSelect();
        }else{
          this.$notify({ text: ' .', type: 'error' });
        }
     }
   },
   //   
   mounted() {
    this.checkLogin();
    this.productSelect();
    // bomproduct  
    //   
   },
 };
 </script>
 
 <style lang="scss" scoped>
 .main-container{
     background-color:  #e9ecef;
     margin-left: 20px;
     margin-right: 20px;
     margin-top: 0px;
     margin-bottom: 0px;
     border-radius: 10px;
 }
 .content{
    margin-left: 20px;
    margin-right: 20px;
    margin-top: 0px;
    margin-bottom: 0px;
    padding: 0px;
 }
 input {
       background-color: $white;
       border: solid 1px  ;
 }
 input:focus {
  background-color: #ffffff; /*      */
  border-color: #86b7fe; /*       */
  outline: none; /*      */
}
 </style>