<template>
    <div class="container-fluid py-4">
    <!--  -->
    <h3> </h3>
    <h4 class="pt-2"> </h4>
    <div class= "main-container ps-4">
        <div class= "pt-5 pb-5">
                <!-- -->
                <div class="row align-items-center mb-3">
                    <label class="col-sm-2 col-form-label fw-bold" ></label>
                    <div class="col-sm-4">
                        <input 
                            id="clientSearch"  class="form-control border p-2"
                            v-model="searchCom.com_name" @click="openModal('client')"  autocomplete="off" placeholder="" />
                            <Modal
                                :isShowModal="isShowModal.client"
                                :modalTitle="''"
                                :noBtn="''"
                                :yesBtn="''"
                                @closeModal="closeModal('client')"
                                @confirm="confirm('client')"
                            >
                            <template v-slot:list>
                                <ComList v-show="isShowModal.client" @selectclient="selectclient"/>
                            </template>
                            </Modal>
                    </div>    
                </div>
                <!--  -->
                <div  class="row align-items-center mb-3">
                    <label class="col-sm-2 col-form-label fw-bold"></label>
                    <div class="col-sm-6">
                        <input 
                        id="orderSearch" class="form-control border p-2"
                        v-model="orderName"  autocomplete="off" placeholder="" />
                    </div>
                </div>
                <!--  -->
                <div class="row align-items-center mb-5">
                    <label class="col-sm-2 col-form-label fw-bold"></label>
                    <div class="col-sm-4">
                        <input 
                        type="date" 
                        id="startDate" class="form-control border p-2"
                        v-model="startDate"  autocomplete="off"  />
                    </div>
                    <div class="col-sm-1 text-center">~</div>
                    <div class="col-sm-4">
                        <input 
                        type="date" 
                        id="endDate" class="form-control border p-2"
                        v-model="endDate"  autocomplete="off"   />
                    </div>
                </div>
            </div>
                    <!--  -->
            <div class="pb-3 text-center">
                <material-button  color="success" class="button" @click="searchOrderlists"></material-button>
                <material-button  color="warning" class="button" @click="resetSearch"></material-button>
            </div>
        </div>
    </div>
    <!-- <div class="grid-container" v-show="rowData.length != 0"> -->
    <div class="grid-container">
        <ag-grid-vue
        :rowData="rowData"
        :columnDefs="columnOrderlist"
        :theme="theme"
        @grid-ready="onGridReady"
        :noRowsOverlayComponent="noRowsOverlayComponent"
        @rowClicked="onOrderRowClicked"
        rowSelection="multiple"
        :pagination="true"
        :paginationPageSize="5"
        :paginationPageSizeSelector="[5, 10, 20, 50, 100]"
    />
    </div>
    <div style="display: none">
        <CustomNoRowsOverlay/>
    </div>
    <div class="container-fluid py-4">
        <h4 v-show="rowDataOrder.length != 0"></h4>
        <div class="d-flex">
            <div class="p-2" style="flex: 6;">
                <div class="grid-container" v-show="rowDataOrder.length != 0">
                <ag-grid-vue
                :rowData="rowDataOrder"
                :columnDefs="columnOrder"
                :theme="theme"
                @grid-ready="onGridReady"
                :noRowsOverlayComponent="noRowsOverlayComponent"
                @rowClicked="onDisoutputRowClicked"
                rowSelection="single"
                :pagination="true"
                :paginationPageSize="5"
                :paginationPageSizeSelector="[5, 10, 20, 50, 100]"
                />
                </div>
                <div style="display: none">
                    <CustomNoRowsOverlay/>
                </div>
            </div>
            <div class="p-2" style="flex: 4;">
                <div class="grid-container" v-show="rowDataOrder.length != 0">
                <ag-grid-vue
                :rowData="rowDataLot"
                :columnDefs="columnLot"
                :theme="theme"
                @grid-ready="onGridReady"
                :noRowsOverlayComponent="noRowsOverlayComponent"
                :editType="'fullRow'"
                rowSelection="multiple"
                :pagination="true"
                :paginationPageSize="5"
                :gridOptions="gridOptions"
                :paginationPageSizeSelector="[5, 10, 20, 50, 100]"
                />
                </div>
                <div style="display: none">
                    <CustomNoRowsOverlay/>
                </div>
                <!--  -->
                <div class="row align-items-center mb-3" v-show="rowDataOrder.length != 0">
                    <label class="col-sm-2 col-form-label fw-bold" ><span> *</span></label>
                    <div class="col-sm-4">
                        <input id="EmpName"  class="form-control border p-2" v-if="searchEmpName.name" v-model="searchEmpName.name" @click="openModal('emp')"  autocomplete="off"  />
                        <input type="text" v-else class="form-control" id="EmpName" @click="openModal('emp')" v-model="searchEmpName"  autocomplete="off"  >
                            <Modal
                                :isShowModal="isShowModal.emp"
                                :modalTitle="''"
                                :noBtn="''"
                                :yesBtn="''"
                                @closeModal="closeModal('emp')"
                                @confirm="confirm('emp')"
                            >
                            <template v-slot:list>
                                <EmpList v-show="isShowModal.emp" @selectemp="selectemp"/>
                            </template>
                            </Modal>
                    </div>    
                </div>
                
                <!--  -->
                <div class="mb-3 pt-2 text-center" v-show="rowDataOrder.length != 0">
                    <material-button  color="success" class="button" @click="processData"></material-button>
                    <material-button color="warning" class="button" @click="resetLot"></material-button>
                </div>
            </div>
       </div>
    </div>
        
    
</template>

<script>
import MaterialButton from "@/components/MaterialButton.vue";
import Modal from "@/views/natureBlendComponents/modal/Modal.vue";
import ComList from "@/views/sales/Order/clientModal.vue";
import EmpList from "@/views/sales/Order/EmpModal.vue";
import theme from "@/utils/agGridTheme";
import userDateUtils from '@/utils/useDates.js';
import CustomNoRowsOverlay from "@/views/natureBlendComponents/grid/noDataMsg.vue";
import axios from "axios";
import { ajaxUrl } from '@/utils/commons.js';
import { mapMutations } from "vuex";

export default{
    name :"orderlistSearch",
    components:{
            MaterialButton,
            Modal,
            ComList,
            EmpList,
            CustomNoRowsOverlay,
            
    },
    data(){
        return{
            
            testing: {},

            //  
            orderName:"", // 
            startDate:"", //  
            endDate:"", //  
            //  
            searchCom:"", //     
            selectedCom: "", //  

          

            //   
            noRowsOverlayComponent: 'CustomNoRowsOverlay',
            filteredOrderlist : [],
            theme : theme,
            rowData : [],
            columnOrderlist : [
            { headerName : "", field:'orderlist_num' ,flex: 2,cellStyle: { textAlign: "right" }},
            { headerName : "", field:'orderlist_title' ,flex: 4,cellStyle: { textAlign: "left" }},
            { headerName : "",field:'com_name' ,flex: 2,cellStyle: { textAlign: "left" }},
            { headerName : "",field:'name' ,flex: 2,cellStyle: { textAlign: "left" }},
            { headerName : "",field:'order_date' ,flex: 2,cellStyle: { textAlign: "center" }},
            { headerName : "",field:'due_date' ,flex: 2,cellStyle: { textAlign: "center" }},
            ],
           
            //
           statusOrderMap: {         // DB    
                "preparing": "",
                "semiShipped": "",
            },
            rowDataOrder : [],
            columnOrder : [
                {headerName :"",field: 'order_num',flex: 2,cellStyle: { textAlign: "right" } },
                {headerName :"",field: 'product_code',flex: 2,cellStyle: { textAlign: "center" }},
                {headerName :"",field: 'product_name',flex: 3,cellStyle: { textAlign: "left" }},
                {headerName :""
                ,field: 'order_amount'
                ,flex: 2
                ,cellStyle: { textAlign: "right" }
                ,cellRenderer: params =>{
                    if(params.value){
                        const formattedValue = params.value.toLocaleString(); //   
                        return `<span>${formattedValue}</span>`;
                    }
                }},
                {headerName :""
                ,field: 'disorder_amount'
                ,flex: 2
                ,cellStyle: { textAlign: "right" }
                ,cellRenderer: params =>{
                    if(params.value){
                        const formattedValue = params.value.toLocaleString(); //   
                        return `<span>${formattedValue}</span>`;
                    }
                }}, //   as (alias)    .
                {headerName :""
                ,field: 'output_amount'
                ,flex: 2
                ,cellStyle: { textAlign: "right" }
                ,cellRenderer: params =>{
                    if(params.value){
                        const formattedValue = params.value.toLocaleString(); //   
                        return `<span>${formattedValue}</span>`;
                    }
                }},
                {headerName :"",field: 'order_status',flex: 2,cellStyle: { textAlign: "left" }}
            ],

            //    
             //  
            searchEmpName:"", //    
            selectedEmpName:"", //   

            gridOptions: {
                rowSelection: {
                    checkboxes: true,  //    
                }
            },
           
            rowDataLot :[],
            columnLot :[
            {headerName: "",
            headerCheckboxSelection: true,
            field: "check",
            resizable: false,
            editable: true,
            sortable: false,
            checkboxSelection: true,
            flex: 1
            ,cellStyle: { textAlign: "center" }
            },
            {headerName :"LOT",field: 'product_lot' ,flex: 5,cellStyle: { textAlign: "center" }},
            { headerName: "",
            field: "output_num",
            resizable: false,
            editable: true,
            sortable: false,
            //      
            onCellValueChanged: this.onCellValueChanged
            ,flex: 3
            ,cellStyle: { 
                textAlign: "right",
                //backgroundColor: "#fff", //  
                //border: "0.5px dashed #fb8c00", //  
                cursor: "text", //  
            }
            ,cellRenderer: params => {
                if (params.value) {
                    const formattedValue = params.value.toLocaleString(); //   
                    return `<span  style="flex-grow: 1; text-align: right;">${formattedValue}</span>`;
                } else {
                    //       
                    return `
                                <span style="display: flex; align-items: center; justify-content: flex-start;">
                                    <i class="fas fa-edit pt-2" style="color:#6c757d88;" title="  "></i>
                                </span>
                            `;
                }
            }
            },
            {headerName :" "
            ,field: 'total_amount'
            ,flex: 4
            ,cellStyle: { textAlign: "right" }
            ,cellRenderer: params =>{
                    if(params.value){
                        const formattedValue = params.value.toLocaleString(); //   
                        return `<span>${formattedValue}</span>`;
                    }
             }},
            {headerName :" ",field: 'inspec_end',flex: 4,cellStyle: { textAlign: "center" }},

           
            ],

            


            isShowModal: {
            client: false, //  
            emp: false, //  
            },

            
           

            
        }
    },
    mounted() {
      this.test();
    },
 

    methods:{
        ...mapMutations(["addLoginInfo"]),
        test(){
        this.testing = this.$store.state.loginInfo;
        //console.log('ddd', this.$store.state.loginInfo);
        this.searchEmpName = this.$store.state.loginInfo.name;
        },
        
        selectclient(client){
            this.selectedCom = client; 
        },
        selectemp(emp){
        this.selectedEmpName = emp;
        },
        openModal(modalType){
            this.isShowModal[modalType] = true; 
            //console.log(`${modalType} modal open`);
        },
        
        confirm(modalType){
            if (modalType === 'client') {
                this.searchCom = this.selectedCom;
            } else if (modalType === 'emp') {
                this.searchEmpName = this.selectedEmpName;
            }
            this.closeModal(modalType); //  
        },
        closeModal(modalType) {
            this.isShowModal[modalType] = false;
        },
        // 
        resetSearch(){
            this.searchCom = ""; //      
            this.orderName = "";  //      
            this.startDate = "";  //   
            this.endDate = "";    //   
            this.orderData = {};  //  
            this.rowData =[];
            this.rowDataOrder = [];
            this.rowDataLot =[];
            //console.log(this.orderData);

            this.searchEmpName="";
        },
        
        async searchOrderlists(){
        this.filters  = {
            clientName : this.searchCom,
            orderName : this.orderName,
            startDate : this.startDate,
            endDate : this.endDate,
        }
        if( new Date(this.filters.startDate) > new Date(this.filters.endDate)){
          this.$notify({
                  text: `     . `,
                  type: 'error',
              });
          return;
        }
    

        //console.log(this.filters);

        let result = await axios.put(`${ajaxUrl}/output/search`,this.filters )
                                .catch(err => console.log(err));
        
        this.rowData = result.data;
        this.rowData = result.data.map((col) => ({
            ...col,
            order_date: this.dateFormat(col.order_date, "yyyy-MM-dd"),
            due_date: this.dateFormat(col.due_date, "yyyy-MM-dd"),
            })
        );  
    },
    dateFormat(value, format) {
        return userDateUtils.dateFormat(value, format);
    },
            


        onGridReady(params){
            this.gridApi = params.api;
            //this.gridApi.sizeColumnsToFit();
        },
        //        
        async onOrderRowClicked(row) {
            
            let order = row.data;
           
            let result = await axios.get(`${ajaxUrl}/output/disoutput/${order.orderlist_num}`)
                                    .catch(err => console.log(err));
            // this.rowDataOrder = result.data;
            this.rowDataOrder = result.data.map((col)=> {
                return {
                    ...col,
                    order_status : this.statusOrderMap[col.order_status],
                    com_name: row.data.com_name,
                };
            });

            //console.log("rowDataOrder:",this.rowDataOrder);
        },
        //    lot 
        async onDisoutputRowClicked(row){
            //console.log("rows:",row)
            //console.log("  ",row.data.order_num);
            let disorder = row.data;
            let result = await axios.get(`${ajaxUrl}/output/product/${disorder.product_code}`)
                                    .catch(err => console.log(err))

                                    this.rowDataLot = result.data;
            this.rowDataLot = result.data.map((col) => ({
                ...col,
                inspec_end: this.dateFormat(col.inspec_end, "yyyy-MM-dd"),
                com_name: row.data.com_name,
                order_num : row.data.order_num,
                order_amount : row.data.order_amount,
                disorder_amount : row.data.disorder_amount,
                }));
            this.currentDisorder = disorder;

            //console.log("rowDataLot:",this.rowDataLot)
            
           
        },
        // 
        async processData(){
            //   
            const selectedRows = this.gridApi.getSelectedRows();
            if (selectedRows.length === 0) {
                this.$notify({ text: '  .', type: 'error' });
                return;
            }  
            let clientName = selectedRows[0].com_name;
            let orderNum = selectedRows[0].order_num;
            //console.log(selectedRows[0].com_name, selectedRows[0].order_num)
            //console.log("disorder:",selectedRows[0].disorder_amount);
            

            let outputNums = []
            let productLots = []
          

         //     (     )
        let invalidRows = [];
        selectedRows.forEach(row =>{
            //console.log("==",row );
            if (Number(row.total_amount) < row.output_num) {
                //          
                invalidRows.push(row);
            }else{
                //    
                outputNums.push(Number(row.output_num));
                productLots.push(row.product_lot);
            }
                    
        });
         //       
        if (invalidRows.length > 0) {
            this.$notify({
                text: '     .',
                type: 'error',
            });
            return;
        }

        //   
        const totalOutput = outputNums.reduce((sum,value)=> sum + Number(value),0);
        //  
        if(selectedRows.length > 0 && Number(selectedRows[0].disorder_amount) < totalOutput){
            this.$notify({
                text: '    .',
                type: 'error',
            });
            return; 
        }

        //,   
        if(!this.searchEmpName || totalOutput == 0){
            this.$notify({ text: '     .', type: 'error' });
            return;
        }
        //   
        let outputInfo = {
            product_lot : JSON.stringify(productLots),
            output_amount : JSON.stringify(outputNums),
            order_num : orderNum,
            com_name : clientName,
        }
        //   `name`   
        if (!this.searchEmpName.name) {
            outputInfo.name = this.searchEmpName;
        } else {
            outputInfo.name = this.searchEmpName.name;
        }

        //console.log(outputInfo);
        let result =
            await axios.post(`${ajaxUrl}/output/insert`, outputInfo)
                        .catch(err => console.log(err));
            //console.log(result.data);
        if(result.statusText === 'OK'){
            this.$notify({
                text: ` .`,
                type: 'success',
            }); 
            window.location.reload();
            
        }

            
        },
        //LOT  
        resetLot(){
            //  
            this.gridApi.deselectAll();
             //   
            this.searchEmpName = ""; //  
            this.clientName = "";    //  

            //   
            const rows = this.gridApi.getRenderedNodes();
            rows.forEach(rowNode => {
                if (rowNode.data.output_num !== undefined) {
                    rowNode.data.output_num = ""; //   
                    this.gridApi.refreshCells({ rowNodes: [rowNode], columns: ['output_num'] });
                }
            });
            
        },

        // output_num      
        onCellValueChanged(event){
            if(event.colDef.field === 'output_num'){
                const updateOutput = event.newValue;
                const stock = event.data.input_amount;

                //    
                if(updateOutput > stock){
                    this.$notify({
                        text: `'    .'`,
                        type: 'error',
                    });

                     //    
                    event.node.setDataValue('output_num', event.oldValue);
                }
            }
        },
    
    },//method
    
};
</script>
<style lang="scss" scoped>
.main-container{
    background-color:  #e9ecef;
    border-radius: 10px;
    
}
.d-flex {
  display: flex;
  justify-content: space-between;
  //gap: 5px;
  text-align: center;
}

.custom-cell {
  padding: 2px !important;
}

.text-center {
  text-align: center;
}

.button {
  margin: 0 10px; /*     */
}

/*  input   */
input {
  background-color: #ffffff; /*   */
  border: solid 1px #ced4da; /*   */
  color: #495057; /*   */
}
/* readonly  input   */
input:read-only {
  background-color: #ffffff; /*    */
  color: #495057; /*    */
  cursor: not-allowed; /*      */
}

/* input     */
input:focus {
  background-color: #ffffff; /*      */
  border-color: #86b7fe; /*       */
  outline: none; /*      */
}

/* readonly input     */
input:read-only:focus {
  background-color: #ffffff; /*    */
  border-color: #ced4da; /* readonly    */
  outline: none; /*    */
}


</style>
