<template>
  <div class="px-4 py-4">
    <h3> - </h3>
    <hr>
    <!--    -->
    <div class="mb-4">
      <div class="d-flex align-items-center mb-3">
        <h4 class="me-3"> </h4>
      </div>

      <div class="row gx-3 p-4 rounded border shadow search-background">
        <!--   -->
        <div class="col-md-2 ps-5">
          <label for="startDate" class="form-label">()</label>
          <div class="d-flex gap-2">
            <input type="date" id="startDate" class="form-control border p-2 cursor-pointer"
              v-model="searchInfo.startDate" autocomplete="off" />
          </div>
        </div>
        <div class="col-md-2">
          <label for="endDate" class="form-label">()</label>
          <div class="d-flex gap-2">
            <input type="date" id="endDate" class="form-control border p-2 cursor-pointer" v-model="searchInfo.endDate"
              autocomplete="off" />
          </div>
        </div>

        <!--  -->
        <div class="col-md-3">
          <label for="mName" class="form-label"></label>
          <input type="search" id="mName" class="form-control border p-2 cursor-pointer" placeholder=""
            v-model="searchInfo.mName" autocomplete="off" />
        </div>

        <!--   -->
        <div class="col-md-2 d-flex align-items-end">
          <material-button size="md" v-on:click="searchOrder"></material-button>
          <material-button size="md" class="m-4" color="info" v-on:click="searchRequestAll"> </material-button>
        </div>

      </div>
    </div>
  </div>
  <!--    -->
  <hr>
  <!--   -->
  <div class="container-fluid py-4 ">
    <h4> </h4>
    <div class="ps-4">
      <p>         .</p>
    </div>
    <div class="col-2 ps-4">
      <material-button class="btn-success" size="md" color="primary" @click="addCheckedRows">  </material-button>
    </div>

    <div class="grid-container">
      <ag-grid-vue :rowData="rowData1" :columnDefs="columnDefs" :theme="theme" :defaultColDef="defaultColDef"
        @grid-ready="onReady" :pagination="true" :paginationPageSizeSelector="[10, 20, 50, 100]"
        :paginationPageSize="10" :noRowsOverlayComponent="noRowsOverlayComponent" rowSelection="multiple">
      </ag-grid-vue>

    </div>
  </div>
  <!--   -->

  <hr>
  <!--   -->
  <div class="container-fluid py-4">
    <h4>  </h4>
    <div class="ps-4">
      <p>           .</p>
    </div>
    <div class="grid-container">
      <ag-grid-vue :rowData="rowData2" :columnDefs="columnDefs2" :theme="theme" :defaultColDef="defaultColDef"
        @grid-ready="onGridReady" :pagination="true" @cell-clicked="onCellClicked"
        :paginationPageSizeSelector="[10, 20, 50, 100]" :paginationPageSize="10" style="height: 400px;"
        :noRowsOverlayComponent="noRowsOverlayComponent">
      </ag-grid-vue>


    </div>
    <!--   -->
    <hr>
    <!--   -->
    <div class="row justify-content-center">
      <div class="col-auto">
        <material-button class="btn btn-success" size="lg" @click="openModal"></material-button>
      </div>
      <div class="col-auto">
        <material-button class="btn btn-warning" size="lg" @click="resetAll"></material-button>
      </div>
    </div>
    <!--   -->
  </div>


  <Modal :isShowModal="isShowModalUpdate" @closeModal="closeModal"
    @confirm="updateQnt(selectedRow.orderCode, editedQuantity)">
    <template v-slot:title>
      <h1 class="modal-title fs-5" id="exampleModalLabel">  </h1>
    </template>
    <template v-slot:list>
      <p>     .</p>
      <div class="form-group">
        <label for="quantityInput"> </label>
        <div class="d-flex align-items-center">
          <input v-model="editedQuantity" type="number" id="quantityInput" class="form-control" style="width: auto;"
            autocomplete="off" />
          <p class="ms-2 mb-0">{{ materialType }}</p>
        </div>
      </div>
    </template>
  </Modal>

  <Modal :isShowModal="isShowModal" @closeModal="closeModal" @confirm="confirm">
    <template v-slot:title>
      <h1 class="modal-title fs-5" id="exampleModalLabel"> </h1>
    </template>
    <template v-slot:list>
      <b>  ?</b>
    </template>
  </Modal>

  <div style="display: none">
    <CustomNoRowsOverlay />
  </div>

</template>

<script>
import { toRaw } from 'vue';

import axios from 'axios';
import { ajaxUrl } from '@/utils/commons.js';
import userDateUtils from '@/utils/useDates.js';

import MaterialButton from "@/components/MaterialButton.vue";

import theme from "@/utils/agGridTheme";
import Modal from "@/views/natureBlendComponents/modal/ModalQc.vue";

import { AgGridVue } from "ag-grid-vue3";

import { useNotification } from "@kyvg/vue3-notification";  // 
const { notify } = useNotification();  //  

import CustomNoRowsOverlay from "@/views/natureBlendComponents/grid/noDataMsg.vue";


export default {
  name: "",
  components: { MaterialButton, Modal, AgGridVue, CustomNoRowsOverlay },

  data() {
    return {
      // 
      loginInfo: {},

      //   
      searchInfo: {
        mName: '',
        // :  
        startDate: this.dateFormat(new Date(Date.now() - 7 * 24 * 60 * 60 * 1000), 'yyyy-MM-dd'),
        endDate: this.dateFormat(new Date(), 'yyyy-MM-dd')
      },
      //db   
      orderList: [],

      // 
      isShowModal: false,
      isShowModalUpdate: false,

      editedQuantity: 0, //    
      selectedRow: {}, //   

      //ag grid 
      noRowsOverlayComponent: 'CustomNoRowsOverlay',

      theme: theme,
      rowData1: [], // (db       )
      columnDefs: [ //  
        {
          headerName: "",
          resizable: false,
          editable: true,
          sortable: false,
          cellStyle: { textAlign: "center" },
          flex: 0.0625,
          headerCheckboxSelection: true,
          checkboxSelection: true,
        },
        { headerName: "", field: "orderCode", resizable: false, cellStyle: { textAlign: "center" }, flex: 0.5 },
        { headerName: "", field: "mName", resizable: false, cellStyle: { textAlign: "left" }, flex: 1 },
        {
          headerName: "", field: "ordQty", resizable: false, cellStyle: { textAlign: "right" }, flex: 0.5,
          cellRenderer: params => {
            if (params.value) {
              if (params.data.mName.includes('')) {
                const formatted_t_qty = Number(params.value).toLocaleString() + ' ';
                return `<span style="text-align: right;">${formatted_t_qty}</span>`;
              } else {
                const formatted_t_qty = Number(params.value * 0.001).toLocaleString() + ' kg';
                return `<span style="text-align: right;">${formatted_t_qty}</span>`;
              }
            } else {
              return `<span style="text-align: right;"></span>`;
            }
          },
        },
        { headerName: "", field: "orderDate", resizable: false, cellStyle: { textAlign: "center" }, flex: 0.25 },
      ],
      rowData2: [],   // 
      columnDefs2: [
        { headerName: "", field: "orderCode", resizable: false, cellStyle: { textAlign: "center" }, flex: 0.5 },
        { headerName: "", field: "mName", resizable: false, cellStyle: { textAlign: "left" }, flex: 1 },
        {
          headerName: "", field: "ordQty", resizable: false, editable: true,
          cellStyle: {
            //border: "0.5px dashed #fb8c00", //  
            cursor: "text", //  
            textAlign: "right",
          },
          flex: 0.5,
          cellRenderer: params => {
            if (params.value != null) {
              const formatted_t_qty = (params.data.mName.includes('') ? (`${Number(params.value).toLocaleString()}`): (`${Number(params.value * 0.001).toLocaleString()} kg`));
              return `
                <div style="display: flex; align-items: center; justify-content: space-between;">
                  <i class="fas fa-edit" style=" color: gray; margin-right: 5px;"></i>
                  <span style="flex-grow: 1; text-align: right;">${formatted_t_qty}</span>
                </div>
              `;
            } else {
              return `<span style="text-align: right;"></span>`;
            }
          },
        },
        { headerName: "", field: "orderDate", resizable: false, cellStyle: { textAlign: "center" }, flex: 0.3 },
        { headerName: "", field: "empName", resizable: false, cellStyle: { textAlign: "left" }, flex: 0.3 },
        {
          cellRenderer: (params) => {
            return `
              <button class="btn btn-danger btn-sm delete-btn" data-id="${params.node.id}">
                
              </button>
            `;
          },
          editable: false,
          resizable: false,
          sortable: false,
          filter: false,
          cellStyle: { textAlign: "center" }
          , flex: 0.25
        }
      ],
      defaultColDef: {
        headerClass: "header-center"
      },
    };
  },



  methods: {
    bringIoginInfo() {
      this.loginInfo = this.$store.state.loginInfo;
    },
    // 
    async searchOrder() {
      if (new Date(this.searchInfo.startDate) > new Date(this.searchInfo.endDate)) {
        `${notify({
          text: "     .",
          type: "error", // success, warn, error 
        })}`;
        return;
      }

      const name = this.searchInfo.mName.replace(/\s+/g, "");
      const result = {
        mName: name.length != 0 ? name : "",
        startDate: this.searchInfo.startDate,
        endDate: this.searchInfo.endDate
      };
      let searchResult = await axios.post(`${ajaxUrl}/meterialOrderQC`, result)
        .catch(err => console.log(err));
      this.orderList = searchResult.data;

      // ag grid  
      this.rowData1 = []
      for (let i = 0; i < this.orderList.length; i++) {
        let col = {
          "orderCode": this.orderList[i].order_code, "mName": this.orderList[i].material_name,
          "ordQty": this.orderList[i].ord_qty, "orderDate": this.dateFormat(this.orderList[i].order_date, 'yyyy-MM-dd')
          , "empNum": this.loginInfo.emp_num, "empName": this.loginInfo.name
        };
        this.rowData1[i] = col;
      }
    },

    // 
    async searchOrderAll() {
      let searchResult = await axios.get(`${ajaxUrl}/meterialOrderQCAll`)
        .catch(err => console.log(err));
      this.orderList = searchResult.data;

      // ag grid  
      this.rowData1 = []
      for (let i = 0; i < this.orderList.length; i++) {
        let col = {
          "orderCode": this.orderList[i].order_code, "mName": this.orderList[i].material_name,
          "ordQty": this.orderList[i].ord_qty, "orderDate": this.dateFormat(this.orderList[i].order_date, 'yyyy-MM-dd')
          , "empNum": this.loginInfo.emp_num, "empName": this.loginInfo.name
        }
        this.rowData1[i] = col;
      }
    },
    //
    onReady(event) {
      this.gridApi1 = event.api;
    },
    //
    onGridReady(params) {
      this.gridApi = params.api;
      this.gridColumnApi = params.columnApi;

      //    
      document.removeEventListener("click", this.handleDeleteButtonClick);

      //      
      this.handleDeleteButtonClick = (event) => {
        if (event.target && event.target.classList.contains("delete-btn")) {
          const rowIndex = event.target.getAttribute("data-id");
          this.deleteRow(rowIndex);
        }
      };
      document.addEventListener("click", this.handleDeleteButtonClick);
    },

    //  
    //   rowData2() 
    addCheckedRows() {
      //    
      const selectedNodes = this.gridApi1.getSelectedNodes(); //  
      const checkedRows = selectedNodes.map(node => node.data); //   
      //  true  
      if (checkedRows.length == 0) {
        notify({
          text: "   .",
          type: "error", // success, warn, error 
        });
        return;
      }

      //      rowData2 
      const newRows = checkedRows.filter(row =>
        !this.rowData2.some(existingRow => existingRow["orderCode"] === row["orderCode"])
      );
      this.rowData2 = [...this.rowData2, ...newRows]; // rowData2 
      notify({
        text: `${newRows.length}  .`,
        type: "success", // success, warn, error 
      });
    },

    // 
    //    
    deleteRow(rowIndex) {
      const nodeToDelete = this.gridApi.getDisplayedRowAtIndex(rowIndex).data;
      //  
      this.rowData2 = this.rowData2.filter((row) => row.orderCode !== nodeToDelete.orderCode);
      notify({
        text: `  .`,
        type: "error", // success, warn, error 
      });

    },
    //
    resetAll() {
      if (this.rowData2.length != 0) {
        this.searchOrderAll();
        this.rowData2 = []; //   
        notify({
          text: `    .`,
          type: "warn", // success, warn, error 
        });
      }

    },


    // 
    //  YYYY-MM-DD  
    dateFormat(value, format) {
      return userDateUtils.dateFormat(value, format);
    },
    // 
    openModal() {
      if (this.rowData2.length == 0) {
        notify({
          text: "  .",
          type: "error", // success, warn, error 
        });
        return;
      }
      this.isShowModal = !this.isShowModal
    },
    //  
    async confirm() {
      let rawData = toRaw(this.rowData2);   //[{mName:"", ordQty:"100000", ...}, {...}, ...]


      let insertResult = await axios.post(`${ajaxUrl}/insertQCM`, rawData)
        .catch(err => console.log(err));
      notify({
        text: `${insertResult.data.successNum} `,
        type: "success", // success, warn, error 
      });
      this.searchOrderAll() //   
      this.rowData2 = []; //   

      this.closeModal();

    },
    //  
    //  , (,  ) 
    onCellClicked(event) {
      //   X
      if (event.event.target && event.event.target.classList.contains("delete-btn")) {
        return;
      }
      //       
      this.selectedRow = JSON.parse(JSON.stringify(event.data));
      if (this.selectedRow.mName.includes('')) {
        this.materialType = '';
        this.editedQuantity = this.selectedRow.ordQty;
      } else {
        this.materialType = 'kg';
        this.editedQuantity = this.selectedRow.ordQty * 0.001;

      }
      
      this.isShowModalUpdate = true;
    },

    updateQnt(orderCD, qty) {
      if (qty <= 0) {
        notify({
          text: "    .",
          type: "warn", // success, warn, error 
        });
        return;
      }
      else if (this.materialType === '' && !Number.isInteger(qty)) {
        notify({
          text: "      .",
          type: "warn", // success, warn, error 
        });
        return;
      }
      
      //   
      switch(this.materialType){
        case 'kg':
          qty= qty *1000;
          break;
        case '':
          break;
      }
      
      const targetIndex = this.rowData2.findIndex(row => row.orderCode === orderCD);
      if (targetIndex !== -1) {
        this.rowData2[targetIndex] = {
          ...this.rowData2[targetIndex],
          ordQty: qty, //  
        };
        notify({
          text: "  .",
          type: "success", // success, warn, error 
        });
      }
      this.rowData2 = [...this.rowData2,];
      this.closeModal();
    },
    // 
    closeModal() {
      this.isShowModal = false;
      this.isShowModalUpdate = false;
    },



  },
  mounted() {
    this.bringIoginInfo();
  },
  //
  created() {
    this.searchOrderAll();
  },
};
</script>


<style scoped lang="scss">
.container-fluid {
  min-height: 500px;

}

// 
.mb-4 {
  label {
    font-weight: bold;
    margin-bottom: 5px;
    color: #333;
  }
}

// 
.search-background {
  background-color: #e9ecef;




  input {
    background-color: #ffffff;
    border-radius: 5px;
    padding: 8px 12px;
    font-size: 16px;
    transition: border-color 0.3s;
  }

  input:focus {
    border-color: #007bff;
    outline: none;
    box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
  }
}

//  
.form-group {
  display: flex;
  flex-direction: column;
  /*   */
  margin-bottom: 15px;

  /*    */
  label {
    font-weight: bold;
    margin-bottom: 5px;
    color: #333;
  }

  input {
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 8px 12px;
    font-size: 16px;
    transition: border-color 0.3s;
  }

  input:focus {
    border-color: #007bff;
    outline: none;
    box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
  }
}
</style>