<template>
  <div class="p-3">
     <h3>&nbsp;&nbsp;  </h3>
  </div>
<!--    -->
<div class="main-container">
  <div class="content" style="height: 250px;">
     <form class="row gx-3 gy-2 align-items-center">
        <!--  -->
        <div class="col-sm-2">
           <label class="col-form-label fw-bold" for="materialCode"></label>
           <input type="text" class="form-control" style="background-color: white; padding-left: 20px;" id="materialCode" v-model="materialCode" @keydown.enter="enterkey" autocomplete="off">
        </div>

        <!--  -->
        <div class="col-sm-2">
           <label class="col-form-label fw-bold" for="clientName"></label>
           <input type="text" class="form-control" style="background-color: white; padding-left: 20px;" id="clientName" v-model="clientName" @keydown.enter="enterkey" autocomplete="off">
        </div>

        <!--  -->
        <div class="col-sm-2">
           <label class="col-form-label fw-bold" for="POListCode"></label>
           <input type="text" class="form-control" style="background-color: white; padding-left: 20px;" id="POListCode" v-model="POListCode" @keydown.enter="enterkey" autocomplete="off">
        </div>

         <!--   -->
         <div class="col-sm-2">
            <label class="col-form-label fw-bold" for="materialState"> </label>
            <div id="materialState" style="padding-left: 20px; display: inline-flex; align-items: center; gap: 15px; white-space: nowrap;">
               <label style="white-space: nowrap;"><input type="checkbox" class="form-check-input" value="a1" v-model="materialState" />&nbsp;&nbsp;</label>
               <label style="white-space: nowrap;"><input type="checkbox" class="form-check-input" value="a3" v-model="materialState" />&nbsp;&nbsp;</label>
               <label style="white-space: nowrap;"><input type="checkbox" class="form-check-input" value="a4" v-model="materialState" />&nbsp;&nbsp;</label>
            </div>
         </div>
     </form>

     <div class="row gx-3 gy-2 mt-2">
        <!--  -->
        <div class="col-sm-2">
           <label class="col-form-label fw-bold" for="startDate">()</label>
           <div class="input-group">
              <input type="date" class="form-control" style="background-color: white; padding-left: 20px;" id="startDate" v-model="startDate" @keydown.enter="enterkey">
           </div>
        </div>
        <div class="col-sm-2">
           <label class="col-form-label fw-bold" for="endDate">()</label>
           <div class="input-group">
              <input type="date" class="form-control" style="background-color: white; padding-left: 20px;" id="endDate" v-model="endDate" @keydown.enter="enterkey">
           </div>
        </div>
     </div>

     <!--   -->
     <div class="col-auto mt-1 text-center">
        <button type="button" class="btn me-5" style="background-color: #4caf50; color: white;" @click="seachPoList"></button>
        <button type="button" class="btn me-5" style="background-color: #fb8c00; color: white;" @click="reSet"></button>
        <button type="button" class="btn" style="background-color: #0077ff; color: white;" @click="onExportClick"></button>
     </div>
  </div>
</div>
<!--     -->
<div class="grid-container" style="padding-top: 10px;">
  <ag-grid-vue
      ref="gridRef"
     :rowData="rowData"
     :columnDefs="columnDefs"
     :theme="theme"
     :pagination="true"
     :paginationPageSize="9"
     :paginationPageSizeSelector="[9, 20, 50, 100]"
     @grid-ready="onReady"
     style="height: 471px;"
     rowSelection="multiple"
     :noRowsOverlayComponent="CustomNoRowsOverlay"
  ><!--513-->
  </ag-grid-vue>
</div>
<div>
   <Modal :isShowModal="isShowModal" :deleteList="deleteList" :totalcanceNO_display="totalcanceNO_display" @closeModal="closeModal" @confirm="confirm">
   </Modal>
</div>
</template>
<script setup>
import axios from 'axios';
import { ajaxUrl } from '@/utils/commons.js';
import userDateUtils from '@/utils/useDates.js';

import Modal from "@/views/material/materialOrderListModal.vue";
import CustomNoRowsOverlay from "@/views/natureBlendComponents/grid/noDataMsg.vue";

import theme from "@/utils/agGridTheme";
import { ref, onBeforeMount } from 'vue'; //onBeforeMount

import { useNotification } from "@kyvg/vue3-notification";  // 
const { notify } = useNotification();  //  

//  
import { useRouter } from 'vue-router';
const router = useRouter();
//   
import { useStore } from 'vuex'; // Vuex  
const store = useStore();
const loginfo = ref({});
const loginInfo = () => {
   loginfo.value = store.state.loginInfo;
   if(loginfo.value.job === '' || loginfo.value.position === ''){
      console.log(loginfo.value.job);
   }else{
      notify({
         //title: "",
         text: "    .",
         type: "error", // success, warn, error 
      });
      router.push({ name: 'MainPage' });
   }
};

const materialCode = ref('');   //
const clientName = ref('');  //
const POListCode = ref('');  //
const startDate = ref('');   //
const endDate = ref('');  //
const materialState = ref([]);   //  

const seachcondition = ref({}); //  

const deleteList = ref([]);   //  

// 
const reSet = () => {
  materialCode.value = '';
  clientName.value = '';
  POListCode.value = '';
  startDate.value = '';
  endDate.value = '';
  materialState.value = [];

  seachcondition.value = {
   materialCode: '',
   clientName: '',
   POListCode: '',
   startDate: '',
   endDate: '',
   materialState: [],
  }
  matrialOrderList2();   //   

  //this.$notify({ text: '   .', type: 'error' });

//   notify({
//       title: "",
//       text: "  .",
//       type: "success", // success, warn, error 
//    });

};

// AG Grid Vue  
const gridRef = ref(null);
// CSV  
const onExportClick = () => {
  if (gridRef.value) {
    const gridApi = gridRef.value.api; // AG Grid API
    gridApi.exportDataAsCsv({
      fileName: "__.csv",
    });
  }
};

// 
const seachPoList = () => {
  console.log(materialCode.value);
  console.log(clientName.value);
  console.log(POListCode.value);
  console.log(startDate.value);
  console.log(endDate.value);
  console.log(materialState.value);

  seachcondition.value = {
   materialCode: materialCode.value,
   clientName: clientName.value,
   POListCode: POListCode.value,
   startDate: startDate.value,
   endDate: endDate.value,
   materialState: materialState.value,
  }

  matrialOrderList2();
};

 // api   
const columnDefs = ref([
  { headerName: "No.", field: "body_num", width:75, cellStyle: { textAlign: "center" } },
  { headerName: "", field: "order_code", width:130, cellStyle: { textAlign: "center" } },
  { headerName: "", field: "material_name", cellStyle: { textAlign: "left" } },
  { headerName: "", field: "com_name", width:150, cellStyle: { textAlign: "left" } },
  { headerName: "", field: "ord_qty", width:110, cellStyle: { textAlign: "right" } },
  { headerName: "", field: "order_date", width:110, cellStyle: { textAlign: "center" } },
  { headerName: "", field: "limit_date", width:110, cellStyle: { textAlign: "center" } },
  { headerName: "", field: "unit_price", width:110, cellStyle: { textAlign: "right" } },
  { headerName: "", field: "total_price", width:110, cellStyle: { textAlign: "right" } },
  { headerName: "", field: "name", width:100, cellStyle: { textAlign: "center" } },
  { headerName: "", field: "material_state", width:100, 
   cellStyle: { textAlign: "center" },
   cellRenderer: params => {
      if (params.value) {
         if(params.data.material_state==='' && params.data.inspec_status===''){
            return '';
         }else if(params.data.material_state==='' && params.data.inspec_status===''){
            return '';
         }else{
            return params.value;
         }
      }else{
         return;
      }
   },
  },
  {
      headerName: "",
      field: "",
      width: 100,
      editable: false,
      cellRenderer: params => {
         const div = document.createElement('div');
         div.style.display = 'flex';
         div.style.justifyContent = 'center';
         div.style.alignItems = 'center';
         div.style.height = '100%';

         const button = document.createElement('button');
         button.innerText = '';
         button.style.cursor = 'pointer';
         button.style.backgroundColor = '#4caf50';
         button.style.width = '60px';
         button.style.height = '30px';
         button.style.color = 'white';
         button.style.border = 'none';
         button.style.borderRadius = '4px';
         button.style.display = 'flex';
         button.style.justifyContent = 'center';
         button.style.alignItems = 'center';

         button.addEventListener('click', () => {
            console.log(" : ", JSON.stringify(params.data));
            //  
            printRowData(params.data);
         });

         div.appendChild(button);
         return div;
      }
   },
   {
      headerName: "",
      field: "",
      width: 100,
      editable: false,
      cellRenderer: params => {
         if (params.data.material_state === "" && !params.data.inspec_status) {
            const div = document.createElement('div');
            div.style.display = 'flex';
            div.style.justifyContent = 'center';
            div.style.alignItems = 'center';
            div.style.height = '100%';

            const button2 = document.createElement('button');
            button2.innerText = '';
            button2.style.cursor = 'pointer';
            button2.style.backgroundColor = '#f44335';
            button2.style.width = '60px';
            button2.style.height = '30px';
            button2.style.color = 'white';
            button2.style.border = 'none';
            button2.style.borderRadius = '4px';
            //      
            button2.style.display = 'flex';
            button2.style.justifyContent = 'center';
            button2.style.alignItems = 'center';

            button2.addEventListener('click', () => {
               console.log(" : ", JSON.stringify(params.data));
               //   1   ( )
               console.log(params.data.order_code);
               const totalcanceNO = rowData.value.find( idx => idx.order_code === params.data.order_code && idx.inspec_status);
               if(totalcanceNO){
                  //console.log(totalcanceNO.body_num);
                  totalcanceNO_display.value = false;
               }
               deleteList.value = params.data;
               console.log(' ');
               isShowModal.value = true;
            });

            div.appendChild(button2);
            return div;
         }
      }
   },
]);

const totalcanceNO_display = ref(true);


// 
const client_info = async function(com_name){
   console.log(com_name);
   let result = await axios.get(`${ajaxUrl}/material/fullClientKeyWord/${com_name}`)
                              .catch(err=>console.log(err));
   return result.data;
};

//  
function formatBusinessNumber(businessNumber) {
    businessNumber = businessNumber.toString();
    const formattedNumber = `${businessNumber.slice(0, 3)}-${businessNumber.slice(3, 5)}-${businessNumber.slice(5)}`;
    return formattedNumber;
}

//    
const materialBodyList = async function(orderCode){
   let result = await axios.get(`${ajaxUrl}/material/matBodyList/${orderCode}`)
                              .catch(err=>console.log(err));
   return result.data;
};

//  
const printRowData = async (rowData) => {
   const client_data = await client_info(rowData.com_name);
   client_data.value = client_data[0];
   client_data.value.com_num = formatBusinessNumber(client_data.value.com_num);
   console.log(client_data.value.emp_name);

   const material_list = await materialBodyList(rowData.order_code);
   console.log(material_list);

   let materialRows = '';
   let tototal_price = 0;
   material_list.forEach((item, index) => {
      tototal_price += item.material_name.includes('') ? (item.total_price) : (item.total_price*0.001);
      materialRows += `
         <tr>
            <td style="text-align: center;">${index + 1}</td>
            <td style="text-align: center;">${item.material_code}</td>
            <td>${item.material_name}</td>
            <td style="text-align: right;">${ item.material_name.includes('') ? (item.ord_qty).toLocaleString() : (item.ord_qty*0.001).toLocaleString() }</td>
            <td style="text-align: center;">${ item.material_name.includes('') ? "" : "kg"  }</td>
            <td style="text-align: right;">${(item.material_name.includes('') ? item.unit_price.toLocaleString() : item.unit_price*0.001).toLocaleString() }</td>
            <td style="text-align: center;"></td>
            <td style="text-align: right;">${(item.material_name.includes('') ? item.total_price.toLocaleString() : item.total_price*0.001).toLocaleString()}</td>
            <td style="text-align: center;"></td>
         </tr>
      `;
   });



   const printContent = `
      <html>
      <head>
      <meta charset="UTF-8">
      <meta name="viewport" content="width=device-width, initial-scale=1.0">
      <title>  </title>
      <style>
         body {
            font-family: Arial, sans-serif;
            margin: 0;
            padding: 0;
            line-height: 1.5;
         }
         .container {
            width: 100%;
            max-width: 800px;
            margin: 0 auto;
            padding: 20px;
            border: 1px solid black;
         }
         table {
            width: 100%;
            border-collapse: collapse;
            margin-bottom: 20px;
         }
         th {
            border: 1px solid black;
            padding: 8px;
            text-align: center;
         }
         td {
            border: 1px solid black;
            padding: 8px;
            text-align: left;
         }
         th {
            background-color: #f4f4f4;
         }
         .note {
            margin: 10px 0;
            font-weight: bold;
            text-align: left;
         }
         .compact td {
            padding: 5px;
         }
         @media print {
            .no-print {
               display: none;
            }
         }
      </style>
      </head>
      <body>
      <div class="container">
         <button class="btn no-print" style="background-color: #4caf50; color: white;" onclick="window.print()"></button>
         <h1 style="text-align: center;">  </h1>
         <table>
            <tr>
            <th></th>
            <td>${rowData.order_date}</td>
            <th></th>
            <td>${rowData.limit_date}</td>
            </tr>
            <tr>
            <th></th>
            <td>${rowData.order_code}</td>
            <th></th>
            <td>   403 5( )</td>
            </tr>
            <tr>
            <th></th>
            <td>${rowData.com_name}</td>
            <th></th>
            <td>${client_data.value.com_num}</td>
            </tr>
            <tr>
            <th></th>
            <td>${client_data.value.emp_tel}</td>
            <th></th>
            <td>${client_data.value.emp_name}</td>
            </tr>
            <tr>
            <th></th>
            <td style="text-align: right;">${tototal_price.toLocaleString()} </td>
            <th></th>
            <td>${client_data.value.address}</td>
            </tr>
         </table>

         <table>
            <tr>
            <th>NO</th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            </tr>
            ${materialRows}
         </table>

         <div class="note">NOTE :     .</div>

         <table>
            <tr>
               <th class="merged-cell" rowspan="3"></th>
               <th></th>
               <td>${rowData.order_date}</td>
               <th class="merged-cell" rowspan="3"></th>
               <th></th>
               <th></th>
               <th></th>
            </tr>
            <tr>
               <th></th>
               <td>${rowData.order_code}</td>
               <td rowspan="2" style="text-align: center;">${rowData.name}</td>
               <td rowspan="2">&nbsp;</td>
               <td rowspan="2">&nbsp;</td>
            </tr>
            <tr>
               <th></th>
               <td>${rowData.name}</td>
            </tr>
         </table>
      </div>
      </body>
      </html>
   `;
   const printWindow = window.open('', '_blank', 'width=1000,height=1000');
   printWindow.document.open();
   printWindow.document.write(printContent);
   printWindow.document.close();
};







//  
const isShowModal = ref(false);
//  
const closeModal = () => {
   isShowModal.value = false;
   totalcanceNO_display.value = true;
   notify({
      //title: "",
      text: "  .",
      type: "error", // success, warn, error 
   });
};
//  
//const deleteNum = ref();
const confirm = (deleteNum) => {
   console.log("   ", deleteList.value);
   console.log("deleteNum : ", deleteNum);
   totalcanceNO_display.value = true;
   isShowModal.value = false; //  
   if(!deleteNum){
      notify({
         //title: "",
         text: "  .",
         type: "error", // success, warn, error 
      });
   }else{
      // 1= // 2= ( deleteNum )
      const materialObj = {
         deleteNum: deleteNum,
         body_num: deleteList.value.body_num,
         order_code: deleteList.value.order_code,
      }
      poList_delete(materialObj);
   }
};  

//   
const poList_delete = async function(materialObj){
  console.log(materialObj);
  let result = await axios.post(`${ajaxUrl}/material/poListDelete`, materialObj)
                             .catch(err=>console.log(err));
  console.log(result.data);
  if(result.data.affectedRows >= 1){
      notify({
         //title: " ",
         text: "  .",
         type: "success", // success, warn, error 
      });
      matrialOrderList2(); //  
  }
}

//   
const enterkey = (event) => {
   event.preventDefault();
   seachPoList();
};

const onReady = (param) => {
  param.api.sizeColumnsToFit(); // api   
}

//   api 
const rowData = ref([]);

//    ( )
const matrialOrderList2 = async function(){
  let result = await axios.put(`${ajaxUrl}/material/polistorder2`, seachcondition.value)
                      .catch(err=>console.log(err));
   console.log(result.data);
   rowData.value = result.data.map((col) => ({
      ...col,
      //ord_qty: (col.ord_qty * 0.001) + " kg",
      ord_qty: col.material_name.includes('') ? Number(col.ord_qty).toLocaleString() + " " : (Number(col.ord_qty) * 0.001).toLocaleString() + " kg",
      order_date: userDateUtils.dateFormat(col.order_date, "yyyy-MM-dd"),
      limit_date: userDateUtils.dateFormat(col.limit_date, "yyyy-MM-dd"),
      unit_price: col.material_name.includes('') ? Number(col.unit_price).toLocaleString() + " " : Number(col.unit_price*0.001).toLocaleString() + " ",
      total_price: col.material_name.includes('') ? Number(col.total_price).toLocaleString() + " " : Number(col.total_price*0.001).toLocaleString() + " "
    })
  );
}

//    (   )
// const matrialOrderList = async function(){
//   let result = await axios.get(`${ajaxUrl}/material/polistorder`)
//                       .catch(err=>console.log(err));
//   //console.log(' : ',result.data);
//   //rowData.value = result.data;
//   //{  ord_qty, order_date, limit_date, unit_price, total_price, material_state: "", }
//   rowData.value = result.data.map((col) => ({
//       ...col,
//       ord_qty: (col.ord_qty * 0.001) + " kg",
//       order_date: userDateUtils.dateFormat(col.order_date, "yyyy-MM-dd"),
//       limit_date: userDateUtils.dateFormat(col.limit_date, "yyyy-MM-dd"),
//       unit_price: Number(col.unit_price).toLocaleString() + " ",
//       total_price: Number(col.total_price*0.001).toLocaleString() + " "
//     })
//   );
// }


//   
onBeforeMount(()=>{
  matrialOrderList2();   //  
  loginInfo();
});

</script>
<style lang="scss" scoped>
.main-container{
   background-color:  #e9ecef;
   margin-left: 20px;
   margin-right: 20px;
   margin-top: 0px;
   margin-bottom: 0px;
   border-radius: 10px;
}
.content{
  margin-left: 20px;
  margin-right: 20px;
  margin-top: 0px;
  margin-bottom: 0px;
  padding: 0px;
}
input {
     background-color: $white;
     border: solid 1px  ;
}
</style>