<template>
    <div class="container-fluid py-4">
    <!--  -->
        <h3></h3>
        <div class= "main-container">
            <div class= "pt-5 pb-5">
                    <!-- -->
                    <div class="row align-items-center ms-3 mb-3">
                        <label class="col-sm-2 col-form-label fw-bold" ></label>
                        <div class="col-sm-6 d-flex align-items-center">
                            <input type="text" class="form-control me-2" id="productName" 
                                v-model="productName" @click="openModal()"  autocomplete="off"   placeholder=" ">
                                <Modal
                                        :isShowModal="isShowModal"
                                        :modalTitle="''"
                                        :noBtn="''"
                                        :yesBtn="''"
                                        @closeModal="closeModal()"
                                        @confirm="confirm()"
                                    >
                                    <template v-slot:list>
                                        <proList v-show="isShowModal" @selectproduct="selectproduct"/>
                                    </template>
                                </Modal>
                                    <!--  -->
                                <div class="col-sm-4 text-end">
                                    <material-button  size = "md" color="success" class="me-2" @click="searchProductNum"></material-button>
                                    <material-button  size = "md" color="warning" @click="resetSearch"></material-button>
                                </div>
                        </div>   
                    </div>
            </div>   
        </div>
    </div>
        <div class="container-fluid py-4">
            <div>
                <!--    -->
                <div class="grid-container">
                    <ag-grid-vue
                    style ="height: 350px;"
                    :rowData="productNum"
                    :columnDefs="columnProductNum"
                    :theme="theme"
                    @grid-ready="onReady"
                    @rowClicked="onProductLot"
                    :quickFilterText="inputListsearch"
                    :noRowsOverlayComponent="noRowsOverlayComponent"
                    rowSelection="multiple"
                    :pagination="true"
                    :paginationPageSize="5"
                    :paginationPageSizeSelector="[5, 10, 20, 50, 100]"
                />
                </div>
                <div style="display: none">
                    <CustomNoRowsOverlay/>
                </div>
            </div>
        </div>

        
        <div class="container-fluid py-4">
            
            <div>
                <!--  lot  -->
                <div class="grid-container">
                    <h4> LOT </h4>
                    <ag-grid-vue
                    style ="height: 350px;"
                    :rowData="LotNum"
                    :columnDefs="columnLotNum"
                    :theme="theme"
                    @grid-ready="onReady1"
                    :quickFilterText="inputListsearch1"
                    :noRowsOverlayComponent="noRowsOverlayComponent"
                    rowSelection="multiple"
                    :pagination="true"
                    :paginationPageSize="5"
                    :paginationPageSizeSelector="[5, 10, 20, 50, 100]"
                />
                </div>
                <div style="display: none">
                    <CustomNoRowsOverlay/>
                </div>
            </div>
        </div>
</template>

<script>
import MaterialButton from "@/components/MaterialButton.vue";
import Modal from "@/views/natureBlendComponents/modal/Modal.vue";
import proList from "@/views/sales/Order/ProductModal.vue"
import theme from "@/utils/agGridTheme";
import userDateUtils from '@/utils/useDates.js';
import CustomNoRowsOverlay from "@/views/natureBlendComponents/grid/noDataMsg.vue";
import axios from "axios";
import { ajaxUrl } from '@/utils/commons.js';



export default{
    name :"inputList",
    components:{
            MaterialButton,
            Modal,
            proList,
            CustomNoRowsOverlay,
            
    },
    data(){
        return{
            //  
            
            // 
            selectedProName:"",//  
            productName:'', //   
            selectedProCode:"", //   
            productCode:'', //   

            //  ( )
            inputListsearch: "", // 1 ()
            inputListsearch1:"", //2 (lot)

          

            //     
            noRowsOverlayComponent: 'CustomNoRowsOverlay',
            theme : theme,
            productNum : [],
            columnProductNum : [
            { headerName: " ", field: "product_code",flex: 1,cellStyle: { textAlign: "center" } },
            { headerName: "", field: "product_name",flex: 1,cellStyle: { textAlign: "left" } },
            { headerName: " "
            , field: "valid_input_amount"
            ,flex: 1
            ,cellStyle: { textAlign: "right" }
            ,cellRenderer: params =>{
                    if(params.value){
                        const formattedValue = params.value.toLocaleString(); //   
                        return `<span>${formattedValue}</span>`;
                    }
            }},
            { headerName: " "
            , field: "valid_output_amount"
            ,flex: 1
            ,cellStyle: { textAlign: "right" }
            ,cellRenderer: params =>{
                    if(params.value){
                        const formattedValue = params.value.toLocaleString(); //   
                        return `<span>${formattedValue}</span>`;
                    }
            }},
            { headerName: "  "
            , field: "canceled_amount"
            ,flex: 1
            ,cellStyle: { textAlign: "right" }
            ,cellRenderer: params =>{
                    if(params.value){
                        const formattedValue = params.value.toLocaleString(); //   
                        return `<span>${formattedValue}</span>`;
                    }
            }},
            { headerName: "  "
            , field: "disposed_amount"
            ,flex: 1
            ,cellStyle: { textAlign: "right" }
            ,cellRenderer: params =>{
                    if(params.value){
                        const formattedValue = params.value.toLocaleString(); //   
                        return `<span>${formattedValue}</span>`;
                    }
            }},
            { headerName: " "
            , field: "stock_amount"
            ,flex: 1
            ,cellStyle: { textAlign: "right" }
            ,cellRenderer: params =>{
                    if(params.value){
                        const formattedValue = params.value.toLocaleString(); //   
                        return `<span>${formattedValue}</span>`;
                    }
            }},
            ],

            // lot    
             //    
            statusList:["","",""], // 
            pickedStatus:[], //   

            LotNum :[],
            columnLotNum :[
            { headerName: "LOT ", field: "product_lot",flex: 1,cellStyle: { textAlign: "center" } },
            { headerName: "", field: "product_code",flex: 1,cellStyle: { textAlign: "center" } },
            { headerName: " ", field: "product_name",flex: 1,cellStyle: { textAlign: "left" } },
            { headerName: " "
            , field: "product_quantity"
            ,flex: 1
            ,cellStyle: { textAlign: "right" } 
            ,cellRenderer: params =>{
                    if(params.value){
                        const formattedValue = params.value.toLocaleString(); //   
                        return `<span>${formattedValue}</span>`;
                    }
             }},
            { headerName: " ", field: "warehouse_name",flex: 1,cellStyle: { textAlign: "left" } },
            { headerName: " ", field: "manufacturing_date",flex: 1,cellStyle: { textAlign: "center" } },
            { headerName: " ", field: "expire_date",flex: 1,cellStyle: { textAlign: "center" }},
            { headerName: "", field: "product_status",flex: 1,cellStyle: { textAlign: "left" } },  
        ],


            isShowModal: false,

            
           

            
        }
    },
   
 

    methods:{
        selectproduct(product){
            
            this.selectedProCode = product.product_code;
            this.selectedProName = product.product_name;
           // console.log(":",this.selectedProName);
        },
    
        openModal(){
            this.isShowModal = true; 
        },
        
        confirm(){
            
            this.productName = this.selectedProName;
            this.productCode = this.selectedProCode;
           // console.log(":",this.productName);

            this.closeModal(); //  
        },
        closeModal() {
            this.isShowModal = false;
        },
        
        
            



        onReady(event){
            this.gridApi = event.api;
            // event.api.sizeColumnsToFit(); // api   
            //    
            const allPanels = document.querySelectorAll('.ag-paging-panel');
            const paginationPanel = allPanels[1];
            if (paginationPanel) {
               //  
               const container = document.createElement('div');
               container.style.display = 'flex';
               container.style.alignItems = 'center';
               container.style.gap = '5px'; //    

               
                // 
                const inputText = document.createElement('input');
                inputText.type = 'text';
                inputText.placeholder = '';
                inputText.style.padding = '5px';
                inputText.style.width = '250px';
                inputText.style.border = '1px solid #ccc';
                inputText.style.borderRadius = '4px';

                //    ag 
                inputText.addEventListener('input',(event)=>{
                    const value = event.target.value;
                    //console.log(" :", value);

                    //
                    this.inputListsearch = value;
                });

                // ,   
                
                container.appendChild(inputText);

                //  
                paginationPanel.insertBefore(container,paginationPanel.firstChild);
            }

           


        },
        onReady1(event){
            this.gridApi = event.api;
            event.api.sizeColumnsToFit(); // api   
            //    
            const allPanels = document.querySelectorAll('.ag-paging-panel');

             //lot
             const paginationPanel1 = allPanels[2];
            if (paginationPanel1) {
               //  
               const container1 = document.createElement('div');
               container1.style.display = 'flex';
               container1.style.alignItems = 'center';
               container1.style.gap = '5px'; //    

               
                // 
                const inputText1 = document.createElement('input');
                inputText1.type = 'text';
                inputText1.placeholder = '';
                inputText1.style.padding = '5px';
                inputText1.style.width = '250px';
                inputText1.style.border = '1px solid #ccc';
                inputText1.style.borderRadius = '4px';

                //    ag 
                inputText1.addEventListener('input',(event)=>{
                    const value = event.target.value;
                    //console.log(" :", value);

                    //
                    this.inputListsearch1 = value;
                });

                // ,   
                
                container1.appendChild(inputText1);

                //  
                paginationPanel1.insertBefore(container1,paginationPanel1.firstChild);
            }
        },

        async searchProductNum(){
            this.filters = {
                productCode : this.productCode,
            }
            //console.log(this.filters);
            let result = await axios.put(`${ajaxUrl}/inventory/product`,this.filters)
                                    .catch(err => console.log(err));
            this.productNum = result.data;
            //console.log(this.productNum);

        },

      
        dateFormat(value, format) {
          return userDateUtils.dateFormat(value, format);
        }, 
        //    lot 
        async onProductLot(row){
            //console.log("1",row.data.product_code);
           

            let result = await axios.get(`${ajaxUrl}/inventory/productLot/${row.data.product_code}`)
                                    .catch(err => console.log(err))
            this.LotNum = result.data;
            this.LotNum = result.data.map((col) => ({
                ...col,
                manufacturing_date: this.dateFormat(col.manufacturing_date, "yyyy-MM-dd"),
                expire_date: this.dateFormat(col.expire_date, "yyyy-MM-dd"),
                })
            );  

        },

        resetSearch(){
            this.productName = "",
            this.productCode = "",
            this.productNum =[],
            this.LotNum =[]

        },

      

       


    },// method  
};
</script>
<style lang="scss" scoped>
.main-container{
    background-color:  #e9ecef;
    border-radius: 10px;
    //height: 150px;
    padding:15px;
    
}



.text-center {
  text-align: center;
}

.button {
  margin: 0 10px; /*     */
}
/* material-button   */
material-button {
  padding: 5px 20px; /*     */
  font-size: 14px; /*     */
}

/*      */
.row {
  align-items: center; /*   */
}
.text-end {
  text-align: right; /*   */
}
/*  input   */
input {
  background-color: #ffffff; /*   */
  border: solid 1px #ced4da; /*   */
  color: #495057; /*   */
}
/* readonly  input   */
input:read-only {
  background-color: #ffffff; /*    */
  color: #495057; /*    */
  cursor: not-allowed; /*      */
}

/* input     */
input:focus {
  background-color: #ffffff; /*      */
  border-color: #86b7fe; /*       */
  outline: none; /*      */
}

/* readonly input     */
input:read-only:focus {
  background-color: #ffffff; /*    */
  border-color: #ced4da; /* readonly    */
  outline: none; /*    */
}
</style>
