<template>
  <div class="px-4 py-4">
    <h3 class="mb-3"> -  </h3>
    <hr>
    <!--    -->
    <div class="mb-4">
      <div class="d-flex align-items-center mb-3">
        <h4 class="me-3"> </h4>
      </div>

      <div class="row gx-3 p-4 rounded border shadow search-background">
        <!--   -->
        <div class="col-md-2 ps-5">
          <label for="qcStat" class="form-label"> </label>
          <select class="form-select text-center border cursor-pointer" v-model="searchInfo.qcState"
            aria-label="  ">
            <option value="qcs1"></option>
            <option value="qcs2"> </option>
            <option value="qcs3"> </option>
          </select>
        </div>

        <!--   -->
        <div class="col-md-2">
          <label for="startDate" class="form-label">()</label>
          <div class="d-flex gap-2">
            <input type="date" id="startDate" class="form-control border p-2 cursor-pointer"
              v-model="searchInfo.startDate" autocomplete="off" />
          </div>
        </div>
        <div class="col-md-2">
          <label for="endDate" class="form-label">()</label>
          <div class="d-flex gap-2">
            <input type="date" id="endDate" class="form-control border p-2 cursor-pointer" v-model="searchInfo.endDate"
              autocomplete="off" />
          </div>
        </div>

        <!--  -->
        <div class="col-md-2">
          <label for="pName" class="form-label"></label>
          <input type="search" id="pName" class="form-control border p-2 cursor-pointer" placeholder=""
            v-model="searchInfo.pName" autocomplete="off" />
        </div>

        <!--   -->
        <div class="col-md-2 d-flex align-items-end">
          <material-button size="md" v-on:click="searchOrder"></material-button>
          <material-button size="md" class="m-4" color="info" v-on:click="searchRequestAll"> </material-button>
        </div>
      </div>
    </div>
  </div>
  <!--    -->

  <hr>
  <!--   -->
  <div class="container-fluid py-4">
    <h4>  </h4>

    <div class="grid-container">
      <ag-grid-vue :rowData="rowData1" :columnDefs="columnDefs" :theme="theme" :defaultColDef="defaultColDef"
        @grid-ready="onGridReady" :pagination="true" :paginationPageSizeSelector="[10, 20, 50, 100]"
        :paginationPageSize="10" style="height: 700px;" :noRowsOverlayComponent="noRowsOverlayComponent">
      </ag-grid-vue>
    </div>
  </div>
  <!--   -->

  <hr>


  <div style="display: none">
    <CustomNoRowsOverlay />
  </div>

</template>

<script>
import MaterialButton from "@/components/MaterialButton.vue";

import axios from 'axios';
import { ajaxUrl } from '@/utils/commons.js';
import userDateUtils from '@/utils/useDates.js';

import theme from "@/utils/agGridTheme";

import { useNotification } from "@kyvg/vue3-notification";  // 
const { notify } = useNotification();  //  

import CustomNoRowsOverlay from "@/views/natureBlendComponents/grid/noDataMsg.vue";

export default {
  name: "",
  components: { MaterialButton, CustomNoRowsOverlay },
  data() {
    return {
      searchInfo: {
        pName: '',
        // :  
        startDate: this.dateFormat(new Date(Date.now() - 7 * 24 * 60 * 60 * 1000), 'yyyy-MM-dd'),
        endDate: this.dateFormat(new Date(), 'yyyy-MM-dd'),
        qcState: 'qcs1',
      },

      searchList: [],

      //ag grid 
      noRowsOverlayComponent: 'CustomNoRowsOverlay',
      theme: theme,
      rowData1: [], // (db       )
      columnDefs: [ //  
        { headerName: "", field: "qcProcessId", resizable: false, cellStyle: { textAlign: "center" }, flex: 1.5 },
        { headerName: "", field: "processNum", resizable: false, cellStyle: { textAlign: "right" }, flex: 1.25 },
        { headerName: "", field: "productionOrderNum", resizable: false, cellStyle: { textAlign: "center" }, flex: 1.25 },
        { headerName: "", field: "pName", resizable: false, cellStyle: { textAlign: "left" }, flex: 1.5 },
        { headerName: "", field: "eName", resizable: false, cellStyle: { textAlign: "center" }, flex: 1.25 },
        {
          headerName: " ", field: "totalQnt", resizable: false, cellStyle: { textAlign: "right" }, flex: 1,
          cellRenderer: params => {
            if (params.value != null) {
              const formatted_t_qty = Number(params.value).toLocaleString() + ' ';
              return `<span style="text-align: right;">${formatted_t_qty}</span>`;
            } else {
              return `<span style="text-align: right;"></span>`;
            }
          },
        },
        {
          headerName: "", field: "passQnt", resizable: false, editable: true, cellStyle: { textAlign: "right" }, flex: 1,
          cellRenderer: params => {
            if (params.value != null) {
              const formatted_t_qty = Number(params.value).toLocaleString() + ' ';
              return `<span style="text-align: right;">${formatted_t_qty}</span>`;
            } else {
              return `<span style="text-align: right;"></span>`;
            }
          },
        },
        {
          headerName: "", field: "rjcQnt", resizable: false, editable: true, cellStyle: { textAlign: "right" }, flex: 1,
          cellRenderer: params => {
            if (params.value != null) {
              const formatted_t_qty = Number(params.value).toLocaleString() + ' ';
              return `<span style="text-align: right;">${formatted_t_qty}</span>`;
            } else {
              return `<span style="text-align: right;"></span>`;
            }
          },
        },
        { headerName: "", field: "inspecStart", resizable: false, cellStyle: { textAlign: "center" }, flex: 1.6 },
        { headerName: "", field: "inspecEnd", resizable: false, cellStyle: { textAlign: "center" }, flex: 1.6 },
        { headerName: "", field: "inspecStatus", resizable: false, cellStyle: { textAlign: "left" }, flex: 1 },

      ],

      defaultColDef: {
        headerClass: "header-center"
      },


    }

  },
  methods: {
    onGridReady(params) {
      this.gridApi = params.api;

    },
    //  YYYY-MM-DD  
    dateFormat(value, format) {
      return userDateUtils.dateFormat(value, format);
    },


    //   
    // 
    processSearchResults(searchList) {
      const processedData = [];
      for (let item of searchList) {
        processedData.push({
          "qcProcessId": item.qc_packing_id,
          "processNum": item.process_num,                   //()
          "productionOrderNum": item.production_order_num, //
          "pName": item.product_name,
          "eName": item.emp_name,
          "totalQnt": item.total_qnt,
          "passQnt": item.pass_qnt,
          "rjcQnt": item.rjc_qnt,
          "inspecStart": this.dateFormat(item.inspec_start, 'yyyy-MM-dd hh:mm:ss'),
          "inspecEnd": item.inspec_end === null
            ? "" : this.dateFormat(item.inspec_end, 'yyyy-MM-dd hh:mm:ss'),
          "inspecStatus": item.inspec_status
        });
      }
      return processedData;
    },
    async searchOrder() {
      if (new Date(this.searchInfo.startDate) > new Date(this.searchInfo.endDate)) {
        `${notify({
          text: "     .",
          type: "error", // success, warn, error 
        })}`;
        return;
      }

      let searchSelect = '';
      switch (this.searchInfo.qcState) {
        case 'qcs1':
          searchSelect = 'recordQCPPAll'
          break;
        case 'qcs2':
          searchSelect = 'recordQCPP'
          break;
        case 'qcs3':
          searchSelect = 'requestQCPP'
          break;
      }

      const name = this.searchInfo.pName.replace(/\s+/g, "");

      const result = {
        pName: name.length != 0 ? name : "",
        startDate: this.searchInfo.startDate,
        endDate: this.searchInfo.endDate,
      };

      let searchResult = await axios.post(`${ajaxUrl}/${searchSelect}`, result)
        .catch(err => console.log(err));
      this.searchList = searchResult.data;

      // ag grid  
      this.rowData1 = [];
      this.rowData1 = this.processSearchResults(this.searchList);
    },
    // 
    async searchRequestAll() {
      let searchResult = await axios.post(`${ajaxUrl}/recordQCPPAll`)
        .catch(err => console.log(err));
      this.searchList = searchResult.data;

      // ag grid  
      this.rowData1 = [];
      this.rowData1 = this.processSearchResults(this.searchList);
    },
  },
  created() {
    this.searchRequestAll();
  }


};
</script>




<style scoped lang="scss">
.pagination {
  display: flex;
  justify-content: center;
}

.container-fluid {
  min-height: 500px;

  .search {
    margin-top: 24px;
  }
}

// 
.mb-4 {
  label {
    font-weight: bold;
    margin-bottom: 5px;
    color: #333;
  }
}

// 
.search-background {
  background-color: #e9ecef;
  /*   */


  input,
  .form-select {
    background-color: #ffffff;
    border-radius: 5px;
    padding: 8px 12px;
    font-size: 16px;
    transition: border-color 0.3s;
  }

  input:focus {
    border-color: #007bff;
    outline: none;
    box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
  }
}
</style>
