<template>
    <div class="modal fade" @click.self="closeModal" :class="{ show: isShowModal }" id="exampleModal" tabindex="-1">
      <div class="modal-dialog modal-dialog-centered modal-dialog-scrollable">
        <div class="modal-content">
          <div class="modal-header" style ="background-color: #e9ecef;">
            <h1 class="modal-title fs-5" id="exampleModalLabel"> </h1>
          </div>
          <div class="modal-body">
            <slot name="list"></slot>
            <div class="mb-1">
              <label for="searchInput" class="form-label"></label>
              <input type="text" v-model="search" class="text-center styled-input" placeholder="" />
              <!--     -->
              <div class="grid-container" >
                <ag-grid-vue 
                  style ="width: 100%;  "
                  :rowData="rowData"
                  :columnDefs="columnDefs"
                  :theme="theme"
                  :pagination="true"
                  :paginationPageSize="10"
                  @grid-ready="onReady"
                  @rowClicked="onRowClicked"
                  :quickFilterText="search"
              >
              <!-- quickFilterText  -->
                </ag-grid-vue>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-bs-dismiss="modal" @click="closeModal"></button>
          </div>
        </div>
      </div>
    </div>
  </template>
  <script>
    import theme from "@/utils/agGridTheme";
    import { ajaxUrl } from '@/utils/commons.js';
    import axios from 'axios';

  export default {
    name: "Modal",
    props: {
      isShowModal: Boolean,
      modalTitle: String,
      // noBtn: String,
      // yesBtn: String,
      // modalData: Array,
    },
  
    data() {
      return {
        search:null,
        theme: theme,
        rowData:[], // []  {} 
        columnDefs:[
          { headerName:"",field:"material_code" },
          { headerName:"",field:"material_name" },
        ],
        quickFilterText: '',
        bomInput:[],
        // modalState: this.isShowModal,
      };
    },
    watch : {
      modalTitle(){
        console.log('');
        this.materialSelect();
      },
    },
    created(){
      this.materialSelect();
    },
    methods: {
      closeModal() {
        this.$emit('closeModal')
      },
      onRowClicked(row) {
        console.log('  : ', row.data);
        let bom = row.data;
        this.$emit( 'selectMaterial', { material_code : bom.material_code,
                                        material_name : bom.material_name
          }
        )
      },
      getSelectedProduct() {
          return this.rowData[0];
      },
      async materialSelect() {
        let result = await axios.get(`${ajaxUrl}/materialSelect`)
        if(result && result.data){
          console.log("");
          this.rowData = result.data;
        }else {
          console.log("");
        }
      },
    }
  }
  </script>
  
  
  
  <style scoped>
  .modal-dialog {
    max-width: 30%;
  }
  .modal-body{
    background-color: white;
  }
  .modal-body .form-control {
      width: 100px;
      max-width: 300px; /*    */
      height: 45px;
      background-color: white;
  }
  .modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.4);
    .modal-body {
      max-height: 500px;
    }
  }
  .show {
    display: block;
  }
  button {
    font-size: 16px;
  }
  #searchInput {
    /* text-align: center; */
    margin: auto;
  }
  .styled-input {
    display: block; /*      */
    margin: 0 auto; /*      */
    margin-top: 10px;
    padding: 10px; /*   */
    width: 250px; /*    */
    text-align: center; /*    */
    border: 1px solid #ccc; /*  */
    border-radius: 20px; /*   */
    outline: none; /*      */
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); /*   */
    transition: box-shadow 0.3s ease; /*    */
}

/*      */
.styled-input:focus,
.styled-input:hover {
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2); /*    */
    border-color: #007bff; /*      */
}
  </style>