<template>
    <div class="grid-container">
        <ag-grid-vue
        
        :rowData="rowData"
        :columnDefs="columnData"
        :theme="theme"
        @grid-ready="onReady"
        @rowClicked="selectRow"
        rowSelection="single"
    />
    </div>
</template>
<script>
import axios from "axios";
import { ajaxUrl } from '@/utils/commons.js';
import theme from "@/utils/agGridTheme";

export default{
    name : "partSelect",

    props: {
        machineData: Object,
    },

    data(){
        return{
            theme : theme,
            rowData:[], //  
            columnData : [
                {headerName : "", field:"part_num", flex: 1 },
                {headerName : "", field:"changeTypeList", flex: 1 },
                {headerName : "", field:"part_name", flex: 1 },
            ],
            selectedData : "", //  

            
        }
    },
    methods: {
        async getChangePartList(mType){
            let result = await axios.get(`${ajaxUrl}/parts/changePartList/${mType}`)
                                    .catch(err=> console.log(err));
            

            for(let i in result.data) {
              result.data[i].changeTypeList = result.data[i].machine_type.replace('p1', '')
                                                                         .replace('p2', '')
                                                                         .replace('p3', '');
            }
            this.rowData = result.data;
        },

        selectRow(event){
            const selectData = event.data;
            this.$emit('selectData', selectData);
        },
        
        onReady(event){
            this.gridApi = event.api;
        },
        
    },

    watch: {
      machineData: {
        handler() {
          this.getChangePartList(this.machineData.process_code);
        },
        deep: true
      },
    }

}
    
    
</script>
<style scoped lang="scss">
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
/* Firefox  */
input[type='number'] {
  -moz-appearance: textfield;
}

.card {
  box-shadow: none;
}
.emp:hover {
  background-color: $main3;
  cursor: pointer;
  transition: all .1s ease-in-out;
  h6, p {
    color: $white;
  }

}
.selected {
  background-color: $main1;
  h6, p {
    color: $white;
  }
}

.styled-input {
    display: block; /*      */
    margin: 0 auto; /*      */
    margin-top: 10px;
    padding: 10px; /*   */
    width: 250px; /*    */
    text-align: center; /*    */
    border: 1px solid #ccc; /*  */
    border-radius: 20px; /*   */
    outline: none; /*      */
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); /*   */
    transition: box-shadow 0.3s ease; /*    */
}

/*      */
.styled-input:focus,
.styled-input:hover {
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2); /*    */
    border-color: #007bff; /*      */
}
</style>