<template>
<div class="container-fluid py-4">
    <!--  -->
        <h3><span style="font-family:'Arial';">LOT</span></h3>
        <div class= "main-container ">
            <div class="ps-4">
                     <!--LOT   -->
                     <div class="pt-2">
                            <label class="col-sm-2 col-form-label fw-bold">LOT</label>
                            <div>
                                <label class="me-3" v-for="status in statusList" :key="status">
                                    {{ status }}
                                <input 
                                    class="form-check-input"
                                    :value="status"
                                    type="checkbox"
                                    v-model="pickedStatus"
                                >
                                </label>
                            </div>
                    </div>
                    <!-- -->
                    <div class="row align-items-center">
                        <label class="col-sm-2 col-form-label fw-bold" ></label>
                            <div class="col-sm-6 d-flex align-items-center">
                                <div class="col-sm-4">
                                <input 
                                type="date" 
                                id="startDate" class="form-control border p-2"
                                v-model="startDate"  autocomplete="off"  />
                            </div>
                            <div class="col-sm-1 text-center">~</div>
                            <div class="col-sm-4">
                                <input 
                                type="date" 
                                id="endDate" class="form-control border p-2"
                                v-model="endDate"  autocomplete="off"  />
                            </div>
                            <div class="col-sm-4 text-end">
                                <!--  -->
                                <material-button  size = "md" color="success" class="button" @click="searchLotNum"></material-button>
                                <material-button  size = "md" color="warning" class="button" @click="resetSearch"></material-button>
                            </div>
                        </div>   
                    </div>
                </div>   
            </div>
        </div>
        <div class="container-fluid py-4">
            <div>
                <!--  lot  -->
                <div class="grid-container">
                    <ag-grid-vue
                    style ="height: 600px; "
                    :rowData="LotNum"
                    :columnDefs="columnLotNum"
                    :theme="theme"
                    @grid-ready="onReady1"
                    :quickFilterText="inputListsearch1"
                    :noRowsOverlayComponent="noRowsOverlayComponent"
                    rowSelection="multiple"
                    :pagination="true"
                    :paginationPageSize="10"
                    :paginationPageSizeSelector="[10, 20, 50, 100]"
                />
                </div>
                <div style="display: none">
                    <CustomNoRowsOverlay/>
                </div>
            </div>
        </div>
</template>

<script>
import MaterialButton from "@/components/MaterialButton.vue";
import theme from "@/utils/agGridTheme";
import userDateUtils from '@/utils/useDates.js';
import CustomNoRowsOverlay from "@/views/natureBlendComponents/grid/noDataMsg.vue";
import axios from "axios";
import { ajaxUrl } from '@/utils/commons.js';



export default{
    name :"inputList",
    components:{
            MaterialButton,
            CustomNoRowsOverlay,
            
    },
    data(){
        return{
            //  
            startDate:"", //  
            endDate:"", //  
           

            //  ( )
            inputListsearch1:"", //2 (lot)

          

            
            noRowsOverlayComponent: 'CustomNoRowsOverlay',
            theme : theme,
           
            // lot    
             //    
            statusList:["","","",""], // 
            pickedStatus:[], //   

            LotNum :[],
            columnLotNum :[
            { headerName: "LOT ", field: "product_lot", resizable: true, sortable: true ,flex: 3, cellStyle: { textAlign: "center" }},
            { headerName: "", field: "product_code", resizable: true, sortable: true ,flex: 2, cellStyle: { textAlign: "center" }},
            { headerName: " ", field: "product_name", resizable: true, sortable: true,flex: 3, cellStyle: { textAlign: "left" } },
            { headerName: " "
            , field: "product_quantity"
            , resizable: true
            , sortable: true 
            ,flex: 2
            , cellStyle: { textAlign: "right" }
            ,cellRenderer: params =>{
                    if(params.value){
                        const formattedValue = params.value.toLocaleString(); //   
                        return `<span>${formattedValue}</span>`;
                    }
             }},
            { headerName: " ", field: "warehouse_name", resizable: true, sortable: true ,flex: 2, cellStyle: { textAlign: "left" }},
            { headerName: " ", field: "manufacturing_date", resizable: true, sortable: true,flex: 2, cellStyle: { textAlign: "center" } },
            { headerName: " ", field: "expire_date", resizable: true, sortable: true ,flex: 2, cellStyle: { textAlign: "center" }},
            { headerName: "", field: "product_status", resizable: true, sortable: true ,flex: 1, cellStyle: { textAlign: "left" }},
            {
                headerName : "",
                field : "status",
                editable : false,
                flex: 1, 
                cellStyle: { textAlign: "center" },
                cellRenderer: params =>{
                      //   .
                const expireDate = params.data.expire_date; //    
                const productStatus = params.data.product_status; // lot 
                const currentDate = new Date();
                if( new Date(expireDate) <= currentDate && productStatus === ''){
                    const disposeButton = document.createElement('button');
                    disposeButton.innerText = '';
                    disposeButton.style.marginRight = '10px';
                    disposeButton.style.cursor = 'pointer';
                    disposeButton.style.backgroundColor = '#f44335';
                    disposeButton.style.width = '60px';
                    disposeButton.style.height = '30px';
                    disposeButton.style.color = 'white';
                    disposeButton.style.border = 'none';
                    disposeButton.style.padding = '0';
                    disposeButton.style.borderRadius = '4px';
                    disposeButton.style.textAlign = 'center';
                    disposeButton.style.lineHeight = '30px';

                    disposeButton.addEventListener('click',()=>{
                        this.renderButton(params);
                    })
                    return disposeButton;
                }
            }
               
               

            }    
        ],


            isShowModal: false,

            
           

            
        }
    },
   
 

    methods:{
          onReady1(event){
            this.gridApi = event.api;
            // event.api.sizeColumnsToFit(); // api   
            //    
            const allPanels = document.querySelectorAll('.ag-paging-panel');

             //lot
             const paginationPanel1 = allPanels[0];
            if (paginationPanel1) {
               //  
               const container1 = document.createElement('div');
               container1.style.display = 'flex';
               container1.style.alignItems = 'center';
               container1.style.gap = '5px'; //    

               
                // 
                const inputText1 = document.createElement('input');
                inputText1.type = 'text';
                inputText1.placeholder = '';
                inputText1.style.padding = '5px';
                inputText1.style.width = '250px';
                inputText1.style.border = '1px solid #ccc';
                inputText1.style.borderRadius = '4px';

                //    ag 
                inputText1.addEventListener('input',(event)=>{
                    const value = event.target.value;
                    //console.log(" :", value);

                    //
                    this.inputListsearch1 = value;
                });

                // ,   
                
                container1.appendChild(inputText1);

                //  
                paginationPanel1.insertBefore(container1,paginationPanel1.firstChild);
            }
        },

        async searchLotNum(){
            if( new Date(this.startDate) > new Date(this.endDate)){
                this.$notify({
                        text: `     . `,
                        type: 'error',
                    });
                return;
            }
            this.filters  = {
                productStatus : this.pickedStatus,
                startDate : this.startDate,
                endDate : this.endDate,
            }

            //console.log(this.filters);
            let result = await axios.put(`${ajaxUrl}/inventory/lot`,this.filters )
                                    .catch(err => console.log(err));
            //console.log(result.data);
            this.LotNum = result.data;
            this.LotNum = result.data.map((col) => ({
                ...col,
                manufacturing_date: this.dateFormat(col.manufacturing_date, "yyyy-MM-dd"),
                expire_date: this.dateFormat(col.expire_date, "yyyy-MM-dd"),
                })
            );  

        },
        dateFormat(value, format) {
          return userDateUtils.dateFormat(value, format);
        }, 

        //   
        async renderButton(params) {

            let disposeLot = {
                disposeLots : params.data.product_lot
            };
            let result = await axios.put(`${ajaxUrl}/inventory/dispose`,disposeLot)
                                    .catch(err => console.log(err));
            //console.log(result.data.result);
            // ===      (true,false boolean   '' .)
            if(result.data.result === true){
                    this.$notify({
                    text: ` LOT  . `,
                    type: 'success',
                });
                window.location.reload(); 
            }else{
                this.$notify({
                    text: ` LOT    . `,
                    type: 'error',
                });
            }

        },
        resetSearch(){
            this.startDate = "",
            this.endDate = "",
            this.pickedStatus = [],
            this.LotNum = []
        },
      

       


    },// method  
};
</script>
<style lang="scss" scoped>
.main-container{
    background-color:  #e9ecef;
    border-radius: 10px;
    //height: 150px;
    padding:15px;
    
}



.text-center {
  text-align: center;
}

.button {
  margin: 0 10px; /*     */
}
/* material-button   */
material-button {
  padding: 5px 20px; /*     */
  font-size: 14px; /*     */
}

/*      */
.row {
  align-items: center; /*   */
}
.text-end {
  text-align: right; /*   */
}
/*  input   */
input {
  background-color: #ffffff; /*   */
  border: solid 1px #ced4da; /*   */
  color: #495057; /*   */
}
/* readonly  input   */
// input:read-only {
//   background-color: #ffffff; /*    */
//   color: #495057; /*    */
//   cursor: not-allowed; /*      */
// }

/* input     */
input:focus {
  background-color: #ffffff; /*      */
  border-color: #86b7fe; /*       */
  outline: none; /*      */
}

/* readonly input     */
// input:read-only:focus {
//   background-color: #ffffff; /*    */
//   border-color: #ced4da; /* readonly    */
//   outline: none; /*    */
// }
</style>
