<!--    machine/machineInfo -->
<template>
  <div class="container-fluid py-4">
    <!--  ( ) /    /    -->
    <div class="row align-items-center" style="justify-content: space-between;">
      <div class="col-auto mname">
        <h3>{{ machineData.machine_name }}({{ machineData.machine_location }})</h3>
      </div>

      <div class="col-auto">
        <div class="row">
          <div class="col">
            <button
              class="btn btn-success w-100 mb-0 toast-btn"
              type="button"
              data-target="warningToast"
              @click="machineUpdate"
              style="margin: 5px;"
              :disabled="!checkJob"
            >
               
            </button>
          </div>
          <div class="col">
            <button
              class="btn btn-danger w-100 mb-0 toast-btn"
              type="button"
              data-target="warningToast"
              @click="machineDelete"
              style="margin: 5px;"
              :disabled="!checkJob"
            >
               
            </button>
          </div>
        </div>
      </div>

    </div>

    <MachineManage
      :isShowModal="isShowModal"
      :machineNo="machineNo"
      :isUpdate="true"
      @closeModal="closeModal"
      @confirm="confirmModal"
    />
      
    <!--  /   -->
    <div class="row align-items-center machineInfo">
      <!--   -->
      <div class="col-3 mimg">
        <!--    (localUrl) -->
        <img v-if="machineData.machine_img" :src="`${localUrl}${machineData.machine_img}`"/>
        <div v-else class="noImg">
          <a>  </a>
        </div>
      </div>

      <!--    -->
      <div class="col-9 mdata">
        <div class="row">
          <div class="col">
            <div>
              <div class="mdata-header">
                 
              </div>
              <div class="mdata-body">
                {{ machineData.model_num }}
              </div>
            </div>
          </div>
          <div class="col">
            <div>
              <div class="mdata-header">
                 
              </div>
              <div class="mdata-body">
                {{ machineData.machine_type }}
              </div>
            </div>
          </div>
          <div class="col" v-if="machineData.machine_state == ''">
            <div>
              <div class="mdata-header">
                 
              </div>
              <div class="mdata-body">
                
              </div>
            </div>
          </div>
          <div class="col" v-else>
            <div>
              <div class="mdata-header">
                 
              </div>
              <div class="mdata-body">
                
              </div>
            </div>
          </div>
        </div>
        
        <div class="row">
          <div class="col">
            <div>
              <div class="mdata-header">
                  
              </div>
              <div class="mdata-body">
                {{ machineData.com_name }}
              </div>
            </div>
          </div>
          <div class="col">
            <div>
              <div class="mdata-header">
                 
              </div>
              <div class="mdata-body">
                {{ machineData.name }}
              </div>
            </div>
          </div>
          <div class="col">
            <div>
              <div class="mdata-header">
                  
              </div>
              <div class="mdata-body">
                {{ machineData.buy_date }}
              </div>
            </div>
          </div>
        </div>
        
        <div class="row">
          <div class="col">
            <div>
              <div class="mdata-header">
                UPH 
              </div>
              <div class="mdata-body">
                {{ machineData.uph + this.unit }}
              </div>
            </div>
          </div>
          <div class="col" v-if="machinePrdData.success_sum != null">
            <div>
              <div class="mdata-header">
                  
              </div>
              <div class="mdata-body">
                {{
                  machinePrdData.hour_sum > 0 
                  ? ((machinePrdData.success_sum / machinePrdData.hour_sum) * 24).toFixed(3) + this.unit 
                  : (machinePrdData.success_sum * 24).toFixed(3) + this.unit
                }}
              </div>
            </div>
          </div>
          <div class="col" v-else>
            <div>
              <div class="mdata-header">
                  
              </div>
              <div class="mdata-body">
                {{ machineData.upd + this.unit }}
              </div>
            </div>
          </div>
          <div class="col" v-if="machinePrdData.success_sum != null">
            <div>
              <div class="mdata-header">
                 
              </div>
              <div class="mdata-body">
                {{ ((machinePrdData.fail_sum / (Number(machinePrdData.success_sum) + Number(machinePrdData.fail_sum))) * 100).toFixed(3) }}%
              </div>
            </div>
          </div>
          <div class="col" v-else>
            <div>
              <div class="mdata-header">
                 : 
              </div>
              <div class="mdata-body">
                {{ 0 }}%
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>


    <!--   -->
    <div class="row pinfo">
      <div class="col" v-if="machinePartList.length >= 1">
        <div> : <a>{{ machinePartList[0].part_name }}</a></div>
        <div>  : <a>{{ dateFormat(machinePartList[0].replace_date, 'yyyy-MM-dd') }}</a></div>
        <div>  : <a>{{ replaceDate(machinePartList[0].replace_date, machinePartList[0].replace_cycle) }}</a></div>
      </div>
      <div class="col" v-if="machinePartList.length >= 2">
        <div> : <a>{{ machinePartList[1].part_name }}</a></div>
        <div>  : <a>{{ dateFormat(machinePartList[1].replace_date, 'yyyy-MM-dd') }}</a></div>
        <div>  : <a>{{ replaceDate(machinePartList[1].replace_date, machinePartList[1].replace_cycle) }}</a></div>
      </div>
    </div>
    <div class="row pinfo">
      <div class="col" v-if="machinePartList.length >= 3">
        <div> : <a>{{ machinePartList[2].part_name }}</a></div>
        <div>  : <a>{{ dateFormat(machinePartList[2].replace_date, 'yyyy-MM-dd') }}</a></div>
        <div>  : <a>{{ replaceDate(machinePartList[2].replace_date, machinePartList[2].replace_cycle) }}</a></div>
      </div>
      <div class="col" v-if="machinePartList.length >= 4">
        <div> : <a>{{ machinePartList[3].part_name }}</a></div>
        <div>  : <a>{{ dateFormat(machinePartList[3].replace_date, 'yyyy-MM-dd') }}</a></div>
        <div>  : <a>{{ replaceDate(machinePartList[3].replace_date, machinePartList[3].replace_cycle) }}</a></div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import { ajaxUrl, localUrl } from '@/utils/commons.js';
import userDateUtils from "@/utils/useDates.js";
import MachineManage from "./MachineManage.vue";

import { mapMutations } from "vuex";

export default {
  name: "machineInfo",

  components: {
    MachineManage,
  },
  data() {
    return {
      localUrl: localUrl,

      //    
      checkJob: this.$store.state.loginInfo.job == '' ? true : this.$store.state.loginInfo.position == '' ? true : false,

      machineData: {},
      machinePrdData: {},
      machinePartList: [],
      machineNo: 0,
      isShowModal: false,
      selectNo: null,

      unit: '',
    }
  },
  beforeMount() {
    this.selectNo = this.$route.params.mno;
    this.getMachineInfo(this.selectNo);
    this.getMachinePrdInfo(this.selectNo);
    this.getMachinePartInfo(this.selectNo);

    console.log(this.$store.state.loginInfo);
  },
  methods: {
    //  
    ...mapMutations(["addLoginInfo"]),

    //   
    async getMachineInfo(selectNo) {
      let result = await axios.get(`${ajaxUrl}/machine/machineInfo/${selectNo}`)
                              .catch(err => console.log(err));

      this.machineData = result.data;
      this.allFormat(this.machineData);
      this.machineNo = this.machineData.machine_num;
      this.machineData.machine_state = this.machineData.machine_state == 'run' ? '' : '';
    },
    //   
    async getMachinePrdInfo(selectNo) {
      let result = await axios.get(`${ajaxUrl}/machine/machinePrdInfo/${selectNo}`)
                              .catch(err => console.log(err));
      this.machinePrdData = result.data;
    },
    //   
    async getMachinePartInfo(selectNo) {
      let result = await axios.get(`${ajaxUrl}/machine/machinePartList/${selectNo}`)
                              .catch(err => console.log(err));

      let newPartInfo = [];
      for(let i in result.data) {
        let partNo = result.data[i].part_num;
        let partInfo = await this.getPartInfo(partNo);
        newPartInfo.push(partInfo);
      }
      this.machinePartList = [...newPartInfo];
    },
    //   
    async getPartInfo(pno) {
      let result = await axios.get(`${ajaxUrl}/parts/partInfo/${pno}`)
                              .catch(err => console.log(err));
      return result.data;
    },

    //    =    
    machineUpdate() {
      this.isShowModal = !this.isShowModal;
    },
    confirmModal() {
      let selectNo = this.$route.params.mno;
      this.getMachineInfo(selectNo);
      this.getMachinePrdInfo(selectNo);
      this.getMachinePartInfo(selectNo);
      this.closeModal();
    },
    closeModal() {
      this.isShowModal = false;
    },

    //   machineDelete
    async machineDelete() {
      let result = await axios.delete(`${ajaxUrl}/machine/machineDelete/${this.selectNo}`)
                              .catch(err => console.log(err));
      let delRes = result.data;
      if(delRes.result == 'success') {
        this.$notify({
          text: "  .",
          type: 'success',
        });
        this.$router.go(-1); //   (  )
      } else {
        this.$notify({
          text: "  .",
          type: 'error',
        });
      }
    },
    //   
    //   
    allFormat(data) {
      // , , ,  
      data.buy_date = this.dateFormat(data.buy_date, 'yyyy-MM-dd');

      if(data.machine_type == '') {
        this.unit = ' ml';
      } else {
        this.unit = ' ';
      }

      // ,      
    },
    //  
    dateFormat(value, format) {
      return userDateUtils.dateFormat(value, format);
    },
    //  
    replaceDate(replace_date, days) {
      let date = new Date(replace_date);

      date.setDate(date.getDate() + days);

      return userDateUtils.dateFormat(date, 'yyyy-MM-dd');
    }
  }
}
</script>

<style scoped lang="scss">
.mimg {
  padding: 12px;
  background-color: $gray-200;
  border-radius: 5px;
  text-align: center;
  height: 100%;
  border: 2px solid;
  border-radius: 5px;
}
img {
  width: 100%;
  height: 100%;
}
.noImg {
  display: flex;
  height: 100%;
  align-items: center;
  justify-content: center;
}

.machineInfo {
  height: 300px;
  margin: 20px 0;
  align-items: center;
}
.mdata {
  height: 100%;
}
.mdata .row {
  height: 35%;
  margin-left: 7px;
}
.mdata .row .col {
  background-color: $gray-200;
  margin: 0 5px 18px 5px;
  padding: 0 10px;
  border: 2px solid;
  border-radius: 5px;

  display: flex;
  align-items: center;

  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, .2), 0 2px 4px -1px rgba(0, 0, 0, .06);
}
.mdata-header {
  margin-top: 0;
  margin-bottom: 4px;
  justify-content: start;
}
.mdata-body {
  margin-top: 0;
  font-size: 20px;
  font-weight: 700;
  color: black;
}

.pinfo {
  height: 120px;
  align-items: center;
}
.pinfo .col {
  background-color: $gray-200;
  margin: 10px;
  padding: 10px;
  border-radius: 5px;
  border: 2px solid;
  border-radius: 5px;
  max-width: 48%;
  
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, .2), 0 2px 4px -1px rgba(0, 0, 0, .06);
}
.pinfo a {
  font-size: large;
  color: black !important;
  font-weight: 800;
}


</style>
