<!-- 
     : >> LOT  
-->
<template>
   <div class="p-3">
      <h3 style="font-family: Arial, Helvetica, sans-serif;">&nbsp;&nbsp; LOT </h3>
   </div>
 <!--    -->
 <div class="main-container">
   <div class="content" style="height: 250px;">
      <form class="row gx-3 gy-2 align-items-center">

         <!--   -->
         <div class="col-sm-2" style="width: 105px;" >
            <label class="col-form-label fw-bold" for="qty_state"> </label>
            <div id="qty_state" style="padding-left: 0px;">
               <select class="form-select" v-model="qty_state" style="width: 85px; border: solid 1px; background-color: white; text-align: center;" aria-label="  ">
                     <option value="a1"></option>
                     <option value="a2"></option>
                     <option value="a3"></option>
               </select>
            </div>
         </div>

         <!--  -->
         <div class="col-sm-2">
            <label class="col-form-label fw-bold" for="materialCode"></label>
            <input type="text" class="form-control" style="background-color: white; padding-left: 20px;" id="materialCode" v-model="materialCode" autocomplete="off" @keydown.enter="enterkey">
         </div>
 
         <!--  -->
         <div class="col-sm-2">
            <label class="col-form-label fw-bold" for="clientName"></label>
            <input type="text" class="form-control" style="background-color: white; padding-left: 20px;" id="clientName" v-model="clientName" autocomplete="off" @keydown.enter="enterkey">
         </div>
 
         <!--  -->
         <div class="col-sm-2">
            <label class="col-form-label fw-bold" for="POListCode">LOT</label>
            <input type="text" class="form-control" style="background-color: white; padding-left: 20px;" id="POListCode" v-model="POListCode" autocomplete="off" @keydown.enter="enterkey">
         </div>
 
          <!--   -->
          <div class="col-sm-2">
             <label class="col-form-label fw-bold" for="materialNomal"></label><br>
             <div id="materialNomal" style="padding-left: 20px; display: inline-flex; align-items: center; gap: 15px; white-space: nowrap;">
                <label style="white-space: nowrap;"><input type="checkbox" class="form-check-input" value="b1" v-model="materialNomal" />&nbsp;&nbsp;</label>
                <label style="white-space: nowrap;"><input type="checkbox" class="form-check-input" value="b2" v-model="materialNomal" />&nbsp;&nbsp;</label>
             </div>
          </div>

          <!--   -->
          <div class="col-sm-2">
             <label class="col-form-label fw-bold" for="materialLotState"> </label><br>
             <div id="materialLotState" style="padding-left: 20px; display: inline-flex; align-items: center; gap: 15px; white-space: nowrap;">
                <label style="white-space: nowrap;"><input type="checkbox" class="form-check-input" value="c1" v-model="materialLotState" />&nbsp;&nbsp;</label>
                <label style="white-space: nowrap;"><input type="checkbox" class="form-check-input" value="c2" v-model="materialLotState" />&nbsp;&nbsp;</label>
             </div>
          </div>
          
      </form>
 
      <div class="row gx-3 gy-2 mt-2">
         <!--  -->
         <div class="col-sm-2">
            <label class="col-form-label fw-bold" for="startDate">()</label>
            <div class="input-group">
               <input type="date" class="form-control" style="background-color: white; padding-left: 20px;" id="startDate" v-model="startDate" @keydown.enter="enterkey">
            </div>
         </div>
         <div class="col-sm-2">
            <label class="col-form-label fw-bold" for="endDate">()</label>
            <div class="input-group">
               <input type="date" class="form-control" style="background-color: white; padding-left: 20px;" id="endDate" v-model="endDate" @keydown.enter="enterkey">
            </div>
         </div>

         <!--   -->
         <div class="col-sm-2">
             <label class="col-form-label fw-bold" for="limitOut"></label><br>
             <div id="limitOut" style="padding-left: 10px; display: inline-flex; align-items: center; gap: 15px; white-space: nowrap;">
                <label style="white-space: nowrap;"><input type="checkbox" class="form-check-input" value="out" v-model="limitOut" />&nbsp;&nbsp;  </label>
             </div>
          </div>
      </div>
      
 
      <!--   -->
      <div class="col-auto mt-1 text-center">
         <button type="button" class="btn me-5" style="background-color: #4caf50; color: white;" @click="seachPoList"></button>
         <button type="button" class="btn me-5" style="background-color: #fb8c00; color: white;" @click="reSet"></button>
         <button type="button" class="btn" style="background-color: #0077ff; color: white;" @click="onExportClick"></button>
      </div>
   </div>
 </div>
 <!--     -->
 <div class="grid-container" style="padding-top: 10px;">
   <ag-grid-vue
      ref="gridRef"
      :rowData="rowData"
      :columnDefs="columnDefs"
      :theme="theme"
      :pagination="true"
      :paginationPageSize="9"
      :paginationPageSizeSelector="[9, 20, 50, 100]"
      @grid-ready="onReady"
      style="height: 471px;"
      rowSelection="multiple"
      :noRowsOverlayComponent="CustomNoRowsOverlay"
      :tooltipShowDelay="500"
      :tooltipHideDelay="3000"
   >
   </ag-grid-vue>
 </div>
 <div>
    <Modal :isShowModal="isShowModal" :deleteList="deleteList" @closeModal="closeModal" @confirm="confirm">
    </Modal>
 </div>
 </template>
 <script setup>
 import axios from 'axios';
 import { ajaxUrl } from '@/utils/commons.js';
 import userDateUtils from '@/utils/useDates.js';
 
 import Modal from "@/views/material/materialLotqtyModal.vue";

 import CustomNoRowsOverlay from "@/views/natureBlendComponents/grid/noDataMsg.vue";
 
 import theme from "@/utils/agGridTheme";
 import { ref, onBeforeMount } from 'vue'; //onBeforeMount
 
 import { useNotification } from "@kyvg/vue3-notification";  // 
 const { notify } = useNotification();  //  
 

//  
import { useRouter } from 'vue-router';
const router = useRouter();
//   
import { useStore } from 'vuex'; // Vuex  
const store = useStore();
const loginfo = ref({});
const loginInfo = () => {
   loginfo.value = store.state.loginInfo;
   if(loginfo.value.name){
      console.log(loginfo.value.job);
   }else{
      notify({
         //title: "",
         text: "   .",
         type: "error", // success, warn, error 
      });
      router.push({ name: 'MainPage' });
   }
};
 
 const qty_state = ref('a1'); //
 const materialCode = ref('');   //
 const clientName = ref('');  //
 const POListCode = ref('');  //
 const startDate = ref('');   //
 const endDate = ref('');  //
 const limitOut = ref(''); //  
 const materialNomal = ref([]);   //  
 const materialLotState = ref([]);   //  

 const seachcondition = ref({}); //  
 
 const deleteList = ref([]);   //  
 
 // 
 const reSet = () => {
   materialCode.value = '';
   clientName.value = '';
   POListCode.value = '';
   startDate.value = '';
   endDate.value = '';
   qty_state.value = 'a1';
   limitOut.value = '';
   materialNomal.value = [];
   materialLotState.value = [];
 
   seachcondition.value = {
    materialCode: '',
    clientName: '',
    POListCode: '',
    startDate: '',
    endDate: '',
    qty_state: '',
    limitOut: '',
    materialNomal: [],
    materialLotState: [],
   }
   lotqtylist();   //   
 
   // notify({
   //     title: "",
   //     text: "  .",
   //     type: "success", // success, warn, error 
   //  });
 
 };
 
 // 
 const seachPoList = () => {
   console.log(qty_state.value);
   console.log(materialCode.value);
   console.log(clientName.value);
   console.log(POListCode.value);
   console.log(startDate.value);
   console.log(endDate.value);
   console.log(materialNomal.value);
   console.log(materialLotState.value);
   console.log(limitOut.value);
 
   seachcondition.value = {
    materialCode: materialCode.value,
    clientName: clientName.value,
    POListCode: POListCode.value,
    startDate: startDate.value,
    endDate: endDate.value,
    materialNomal: materialNomal.value,
    materialLotState: materialLotState.value,
    qty_state: qty_state.value,
    limitOut: limitOut.value,
   }
 
   lotqtylist();
 };
 
  // api   
 const columnDefs = ref([
   { headerName: "No.", field: "lot_seq", width:90, cellStyle: { textAlign: "center" }, tooltipField: "lot_seq", },
   { headerName: " LOT", field: "lot_code", width:160, cellStyle: { textAlign: "center" } },
   { headerName: "", field: "material_name", cellStyle: { textAlign: "left" } },
   { headerName: "", field: "com_name", cellStyle: { textAlign: "left" } },
   { headerName: "", field: "in_qty", width:130, cellStyle: { textAlign: "right" } },
   { headerName: "", field: "name", width:120, cellStyle: { textAlign: "center" } },
   { headerName: "", field: "material_nomal", width:85, cellStyle: { textAlign: "center" } },
   { headerName: "", field: "stok_qty", width:130, cellStyle: { textAlign: "right" } },
   { headerName: "", field: "hold_qty", width:130, cellStyle: { textAlign: "right" } },
   { headerName: "", field: "out_qty", width:130, cellStyle: { textAlign: "right" } },
   { headerName: "", field: "inset_lot_date", width:130, cellStyle: { textAlign: "center" } },
   { headerName: "", field: "limit_date", width:130, cellStyle: { textAlign: "center" } },
   { headerName: "", field: "warehouse_name", width:120, cellStyle: { textAlign: "left" } },
   { headerName: "", field: "material_lot_state", width:80, cellStyle: { textAlign: "center" },
      tooltipValueGetter: params => params.data.material_dis_content ? ` : ${params.data.dis_name} /  : ${params.data.material_dis_content}`: "",  
   },
   {  
      headerName: "", 
      field: "", 
      width:100,
      editable: false,
      cellRenderer: params => {
         if(params.data.material_lot_state === "" && (loginfo.value.job === '' || loginfo.value.job === '') ){
            const div = document.createElement('div');
            div.style.display = 'flex';
            div.style.justifyContent = 'center';
            div.style.alignItems = 'center';
            div.style.height = '100%';

            const button2 = document.createElement('button');
            button2.innerText = '';
            button2.style.cursor = 'pointer';
            button2.style.backgroundColor = '#f44335';
            button2.style.width = '60px';
            button2.style.height = '30px';
            button2.style.color = 'white';
            button2.style.border = 'none';
            button2.style.borderRadius = '4px';
            button2.style.display = 'flex';
            button2.style.justifyContent = 'center';
            button2.style.alignItems = 'center';
            button2.addEventListener('click', () => {
               //console.log("  : ", JSON.stringify(params.data));
               //  1   ( )
               deleteList.value = params.data;
               //console.log('aaaaa', deleteList.value);
               const quantity = Number(deleteList.value.hold_qty.split(' ')[0]);
               if(quantity !== 0){
               notify({
                  //title: "",
                  text: "    .",
                  type: "error", // success, warn, error 
               });
               }else{
                  console.log(' ');
                  isShowModal.value = true;
               }
            });
            div.appendChild(button2);
            return div;
            //return button2;
            }
         }
   },
 ]);
 
 //  
 const isShowModal = ref(false);
 //  
 const closeModal = () => {
    isShowModal.value = false;
    notify({
       //title: "",
       text: "  .",
       type: "error", // success, warn, error 
    });
 };
 //  
 const confirm = (data1) => {
    console.log("   ");
   //  console.log('data1',data1);
   //  console.log('data2',deleteList.value);
    const newObject = {
      lot_seq: deleteList.value.lot_seq,
      trush_reason: data1,
      emp_num: loginfo.value.emp_num, //   
    };
    if( deleteList.value.lot_seq && data1){
       trush_go(newObject);
    }else{
      notify({
         //title: "",
         text: "  .",
         type: "error", // success, warn, error 
      });
    }
    isShowModal.value = false; //  
 };
 
 const trush_go = async (newObject) => {
   //console.log(newObject);
   let result = await axios.post(`${ajaxUrl}/material/trushGo`, newObject)
                           .catch(err=>console.log(err));
   //console.log('aaaaaa',result.data);
   if(result.data[0][0]['v_result']==='OK'){
      notify({
         //title: "",
         text: "  .",
         type: "success", // success, warn, error 
      });
      lotqtylist();
   }else{
      notify({
         //title: "",
         text: " "+result.data[0]['v_result']+"   .",
         type: "error", // success, warn, error 
      });
   }
 };

// AG Grid Vue  
const gridRef = ref(null);
// CSV  
const onExportClick = () => {
  if (gridRef.value) {
    const gridApi = gridRef.value.api; // AG Grid API
    gridApi.exportDataAsCsv({
      fileName: "_LOT__.csv",
    });
  }
};

 //   
 const enterkey = (event) => {
    event.preventDefault();
    seachPoList();
 };
 
 const onReady = (param) => {
   param.api.sizeColumnsToFit(); // api   
 }
 
 //   api 
 const rowData = ref([]);
 
 //    ( )
 const lotqtylist = async function(){
   let result = await axios.put(`${ajaxUrl}/material/lotqtylist`, seachcondition.value)
                       .catch(err=>console.log(err));
    console.log(result.data);
    rowData.value = result.data.map((col) => ({
       ...col,
       in_qty: col.material_name.includes('') ? (col.in_qty).toLocaleString() + " " : (col.in_qty * 0.001).toLocaleString() + " kg",
       stok_qty: col.material_name.includes('') ? (col.stok_qty).toLocaleString() + " " : (col.stok_qty * 0.001).toLocaleString() + " kg",
       hold_qty: col.material_name.includes('') ?  (col.hold_qty).toLocaleString() + " " : (col.hold_qty * 0.001).toLocaleString() + " kg",
       out_qty: col.material_name.includes('') ? (col.out_qty).toLocaleString() + " " : (col.out_qty * 0.001).toLocaleString() + " kg",
       inset_lot_date: userDateUtils.dateFormat(col.inset_lot_date, "yyyy-MM-dd"),
       limit_date: userDateUtils.dateFormat(col.limit_date, "yyyy-MM-dd"),
     })
   );
 }
 
 //   
 onBeforeMount(()=>{
   lotqtylist();   //  
   loginInfo();
 });
 
 </script>
 <style lang="scss" scoped>
 .main-container{
    background-color:  #e9ecef;
    margin-left: 20px;
    margin-right: 20px;
    margin-top: 0px;
    margin-bottom: 0px;
    border-radius: 10px;
 }
 .content{
   margin-left: 20px;
   margin-right: 20px;
   margin-top: 0px;
   margin-bottom: 0px;
   padding: 0px;
 }
 input {
      background-color: $white;
      border: solid 1px  ;
 }
 </style>