<template>
    <div>
      <h4 style="margin-bottom: 0px;">&nbsp;&nbsp;&nbsp;&nbsp;  </h4>
   </div>
    <div class="grid-container" style="width: 900px;padding-top: 0px; padding-bottom: 10px;">
        <ag-grid-vue
            :rowData="plandate"
            :columnDefs="plancolumnDefs"
            :theme="theme"
            :pagination="true"
            :paginationPageSize="4"
            :paginationPageSizeSelector="[4, 10, 20, 40]"
            @grid-ready="onReady"
            style="height: 261px;"
            rowSelection="multiple"
            :quickFilterText="plenlistsearch"
            :noRowsOverlayComponent="noRowsOverlayComponent"
        ><!--303-->
        </ag-grid-vue>
    </div>
    <div style="display: none">
        <CustomNoRowsOverlay/>
    </div>
    <div v-if="showprogress" style="width: 100%; height: 100%; background-color: #0005; position: fixed; top: 0px; left: 0px; z-index: 1000;">
        <div style="position: fixed; top: 50%; left: 30%;  width: 50%;">
            <material-progress color="success" :percentage="number2" />
        </div>
    </div>
</template>
<script>
import CustomNoRowsOverlay from "@/views/natureBlendComponents/grid/noDataMsg.vue";
import theme from "@/utils/agGridTheme";
import userDateUtils from '@/utils/useDates.js';
import axios from 'axios';
import { ajaxUrl } from '@/utils/commons.js';
import MaterialProgress from "@/components/MaterialProgress.vue";

export default {
    // 
    components: {
        CustomNoRowsOverlay,
        MaterialProgress,
    },
    //   
    data(){
        return{
            showprogress: false,
            number2: 0,
            noRowsOverlayComponent: 'CustomNoRowsOverlay',
            plenlistsearch: '',
            plancolumnDefs: [
                { 
                    headerCheckboxSelection: true,
                    checkboxSelection: true,
                    headerName: "",
                    width:45,
                },
                { headerName: "", field: "order_plan_num", width:100, cellStyle: { textAlign: "center" } },
                { headerName: "", field: "plan_num", width:100, cellStyle: { textAlign: "center" } },
                { headerName: "", field: "product_code", width:100, cellStyle: { textAlign: "center" } },
                { headerName: "", field: "product_name", cellStyle: { textAlign: "left" } },
                { headerName: "", field: "plan_qty", width:100, cellStyle: { textAlign: "right" } },
                { headerName: "", field: "plan_create_date", cellStyle: { textAlign: "center" } },
            ],
            plandate: [],
            theme : theme,
            needMaterialList: [],
        };
    },
    //   
    props: {
        productorderlist: {
            type: Array,  // 
            required: true, // 
            default: () => [],  //   []
        },
    },
    //   
    watch: {
        productorderlist: {
            //  
            handler(newList){
                this.plandate = newList.map((item) => ({
                    ...item, //  item 
                    plan_create_date: this.dateFormat(item.plan_create_date, 'yyyy-MM-dd'),
                    plan_qty: item.plan_qty+' ',
                }));
            },
            //   1   
            immediate: true, 
        }
    },
    methods: {
        //   
        onReady(event) {
            this.gridApi = event.api;
            event.api.sizeColumnsToFit(); // api   

            //    
            const paginationPanel = document.querySelector('.ag-paging-panel');
            if (paginationPanel) {
                const container1 = document.createElement('div');
                container1.style.display = 'flex';
                container1.style.alignItems = 'center';
                container1.style.gap = '5px'; //    

                const button = document.createElement('button');
                button.textContent = '';
                button.style.marginRight = '10px';
                button.style.cursor = 'pointer';
                button.style.backgroundColor = '#4caf50';
                button.style.color = 'white';
                button.style.border = 'none';
                button.style.padding = '5px 10px';
                button.style.borderRadius = '4px';
                button.style.position = 'absolute';
                button.style.left = '10px';
                //   
                button.addEventListener('click', () => {
                    this.allInput();
                });

                //   
                const inputText1 = document.createElement('input');
                inputText1.type = 'text';
                inputText1.placeholder = '';
                inputText1.style.padding = '5px';
                inputText1.style.width = '100px';
                inputText1.style.border = '1px solid #ccc';
                inputText1.style.borderRadius = '4px';
                inputText1.style.position = 'absolute';
                inputText1.style.left = '100px';

                //     ag  
                inputText1.addEventListener('input', (event) => {
                    const value1 = event.target.value;
                    //console.log(' :', value);
                    //    
                    this.plenlistsearch = value1;
                });
                
                container1.appendChild(button);
                container1.appendChild(inputText1);

                //      
                paginationPanel.insertBefore(container1, paginationPanel.firstChild);
            }
        },

        async allInput(){
            const selectedRows = this.gridApi.getSelectedRows(); //    
            let percentNum = 100/selectedRows.length;
            this.showprogress = true;
            if (selectedRows.length > 0) {
                for(let i=0; i<selectedRows.length; i++){
                    //console.log(selectedRows[i]['order_plan_num']);
                    this.number2 = this.number2 + percentNum;
                    await this.needMaterialOrder(selectedRows[i]['order_plan_num']);
                }

                // 1
            this.needMaterialList = this.needMaterialList.map((col) => ({
                ...col,
                stok_qty: col.material.includes('') ? Number(col.stok_qty).toLocaleString()+' ' : (col.stok_qty * 0.001).toLocaleString()+' kg',
                safety_inventory: col.material.includes('') ? Number(col.safety_inventory).toLocaleString()+' ' : (col.safety_inventory * 0.001).toLocaleString()+' kg',
                plan_qty: col.material.includes('') ? Number(col.plan_qty).toLocaleString()+' ' : (col.plan_qty * 0.001).toLocaleString()+' kg',
                ordering_qty: col.material.includes('') ? Number(col.ordering_qty).toLocaleString()+' ' : (col.ordering_qty * 0.001).toLocaleString()+' kg',
                //need_qty: col.material.includes('') ? (Number(col.need_qty)+Number(col.safety_inventory)).toLocaleString() : ((col.need_qty * 0.001)+(col.safety_inventory * 0.001)).toLocaleString(),
                need_qty: col.material.includes('') ? 
                (Number(col.safety_inventory) + Number(col.plan_qty) - Number(col.stok_qty) - Number(col.ordering_qty))
                :
                ((col.safety_inventory * 0.001) +  (col.plan_qty * 0.001) -  (col.stok_qty * 0.001) - (col.ordering_qty * 0.001))
                ,
            }));

            // 2 
            this.needMaterialList = this.needMaterialList.map((col) => ({
                ...col,
                //need_qty: Number(col.need_qty) > 0 ? Number(col.need_qty).toLocaleString() : 0,
                need_qty: (col.need_qty > 0 ? col.need_qty.toLocaleString() : '0'),
            }));

            // 3      
            this.needMaterialList = this.needMaterialList.map((col) => ({
                ...col,
                need_qty: col.material.includes('') ? col.need_qty+' ' : col.need_qty+' kg',
            }));

            console.log(' : ',this.needMaterialList);

                //      (2)
                this.$emit('planAndBomList', this.needMaterialList);
                this.needMaterialList = [];
                this.$notify({ text: '    .', type: 'success' });   // title:'', 
            } else {
                this.$notify({ text: '  .', type: 'error' }); // title:'', 
            }
            this.showprogress = false;
            this.number2 = 0;
        },

        // BOM     (1)
        async needMaterialOrder(planCode){
            let needMaterialOrderResult = await axios.get(`${ajaxUrl}/material/needOrderMaterial/${planCode}`)
                            .catch(err => console.log(err));

            //  +   (    )
            const updatedList = [...this.needMaterialList, ...needMaterialOrderResult.data];
            
            // material_code   
            const uniqueMaterials = updatedList.reduce((fullitem, item) => {
                //        
                const existingItem = fullitem.find(idx => idx.material_code === item.material_code);
                //    ,  
                if (existingItem) {
                    existingItem.plan_qty += item.plan_qty;
                    existingItem.need_qty = parseInt(existingItem.need_qty, 10) + parseInt(item.need_qty, 10);  //  
                } else {
                    //   
                    fullitem.push({ ...item });
                }
                return fullitem;
            }, []);
            
            this.needMaterialList = uniqueMaterials;
            
            //console.log(': ', this.needMaterialList);
        },

        // 
        dateFormat(value, format) {
            return userDateUtils.dateFormat(value, format);
        },
    },
};
</script>