<!-- 
     : >>    
-->
<template>
   <div class="p-3">
      <h3>&nbsp;&nbsp;  </h3>
   </div>
 <!--    -->
 <div class="main-container">
   <div class="content" style="height: 250px;">
      <form class="row gx-3 gy-2 align-items-center">

         <!--   -->
         <div class="col-sm-2" style="width: 105px;" >
            <label class="col-form-label fw-bold" for="qty_state"> </label>
            <div id="qty_state" style="padding-left: 0px;">
               <select class="form-select" v-model="qty_state" style="width: 85px; border: solid 1px; background-color: white; text-align: center;" aria-label="  ">
                     <option value="a1"></option>
                     <option value="a2"></option>
                     <option value="a3"></option>
               </select>
            </div>
         </div>

         <!--  -->
         <div class="col-sm-2">
            <label class="col-form-label fw-bold" for="materialCode"></label>
            <input type="text" class="form-control" style="background-color: white; padding-left: 20px;" id="materialCode" v-model="materialCode" autocomplete="off" @keydown.enter="enterkey">
         </div>
 
         <!--  -->
         <div class="col-sm-2">
            <label class="col-form-label fw-bold" for="materialName"></label>
            <input type="text" class="form-control" style="background-color: white; padding-left: 20px;" id="materialName" v-model="materialName" autocomplete="off" @keydown.enter="enterkey">
         </div>
          
      </form>
 
      <div class="row gx-3 gy-2 mt-2">
         <!--    -->
         <div class="col-sm-2">
            <label class="col-form-label fw-bold" for="startDate">()</label>
            <div class="input-group">
               <input type="date" class="form-control" style="background-color: white; padding-left: 20px;" id="startDate" v-model="startDate" @keydown.enter="enterkey">
            </div>
         </div>
         <div class="col-sm-2">
            <label class="col-form-label fw-bold" for="endDate">()</label>
            <div class="input-group">
               <input type="date" class="form-control" style="background-color: white; padding-left: 20px;" id="endDate" v-model="endDate" @keydown.enter="enterkey">
            </div>
         </div>

         <!--   -->
         <div class="col-sm-2" style="width:120px;">
            <label class="col-form-label fw-bold" for="limitOut"></label><br>
            <div id="limitOut" style="padding-left: 10px; display: inline-flex; align-items: center; gap: 15px; white-space: nowrap;">
               <label style="white-space: nowrap;"><input type="checkbox" class="form-check-input" value="out" v-model="limitOut" />&nbsp;&nbsp; </label>
            </div>
         </div>

         <!--  -->
         <div class="col-sm-2" style="width:120px;">
            <label class="col-form-label fw-bold" for="product_qty"></label><br>
            <div id="product_qty" style="padding-left: 10px; display: inline-flex; align-items: center; gap: 15px; white-space: nowrap;">
               <label style="white-space: nowrap;"><input type="checkbox" class="form-check-input" value="out" v-model="product_qty" />&nbsp;&nbsp; </label>
            </div>
         </div>

         <!--  -->
         <div class="col-sm-2" style="width:120px;">
            <label class="col-form-label fw-bold" for="order_qty"></label><br>
            <div id="order_qty" style="padding-left: 10px; display: inline-flex; align-items: center; gap: 15px; white-space: nowrap;">
               <label style="white-space: nowrap;"><input type="checkbox" class="form-check-input" value="out" v-model="order_qty" />&nbsp;&nbsp; </label>
            </div>
         </div>


      </div>
      
 
      <!--   -->
      <div class="col-auto mt-1 text-center">
         <button type="button" class="btn me-5" style="background-color: #4caf50; color: white;" @click="seachPoList"></button>
         <button type="button" class="btn me-5" style="background-color: #fb8c00; color: white;" @click="reSet"></button>
         <button type="button" class="btn" style="background-color: #0077ff; color: white;" @click="onExportClick"></button>
      </div>
   </div>
 </div>
 <!--     -->
 <div class="grid-container" style="padding-top: 10px;">
   <ag-grid-vue
      ref="gridRef"
      :rowData="rowData"
      :columnDefs="columnDefs"
      :theme="theme"
      :pagination="true"
      :paginationPageSize="9"
      :paginationPageSizeSelector="[9, 20, 50, 100]"
      @grid-ready="onReady"
      style="height: 471px;"
      rowSelection="multiple"
      :noRowsOverlayComponent="CustomNoRowsOverlay"
   >
   </ag-grid-vue>
 </div>
 </template>
 <script setup>
 import axios from 'axios';
 import { ajaxUrl } from '@/utils/commons.js';
 import userDateUtils from '@/utils/useDates.js';
 
 import theme from "@/utils/agGridTheme";
 import { ref, onBeforeMount } from 'vue'; //onBeforeMount
 
 import { useNotification } from "@kyvg/vue3-notification";  // 
 const { notify } = useNotification();  //  

 import CustomNoRowsOverlay from "@/views/natureBlendComponents/grid/noDataMsg.vue";
 
  //  
  import { useRouter } from 'vue-router';
const router = useRouter();
//   
import { useStore } from 'vuex'; // Vuex  
const store = useStore();
const loginfo = ref({});
const loginInfo = () => {
   loginfo.value = store.state.loginInfo;
   if(loginfo.value.name){
      console.log(loginfo.value.job);
   }else{
      notify({
         //title: "",
         text: "   .",
         type: "error", // success, warn, error 
      });
      router.push({ name: 'MainPage' });
   }
};
 
 const qty_state = ref('a1'); //
 const materialCode = ref('');   //
 const materialName = ref('');  //
 const startDate = ref('');   // 
 const endDate = ref('');  // 
 const limitOut = ref(''); //  
 const product_qty = ref(''); //
 const order_qty = ref('');   // 

 const seachcondition = ref({}); //  
 
 // 
 const reSet = () => {
   materialCode.value = '';
   materialName.value = '';
   startDate.value = '';
   endDate.value = '';
   qty_state.value = 'a1';
   limitOut.value = '';
   product_qty.value = '';
   order_qty.value = '';
 
   seachcondition.value = {
    materialCode: '',
    materialName: '',
    startDate: '',
    endDate: '',
    qty_state: '',
    limitOut: '',
    product_qty: '',
    order_qty: '',
   }
   qtylist();   //   
 
   // notify({
   //     title: "",
   //     text: "  .",
   //     type: "success", // success, warn, error 
   //  });
  
 };
 
 // AG Grid Vue  
const gridRef = ref(null);
// CSV  
const onExportClick = () => {
  if (gridRef.value) {
    const gridApi = gridRef.value.api; // AG Grid API
    gridApi.exportDataAsCsv({
      fileName: "__.csv",
    });
  }
};

 // 
 const seachPoList = () => {
   console.log(qty_state.value);
   console.log(materialCode.value);
   console.log(materialName.value);
   console.log(startDate.value);
   console.log(endDate.value);
   console.log(limitOut.value);
   console.log(product_qty.value);
   console.log(order_qty.value);
 
   seachcondition.value = {
    materialCode: materialCode.value,
    materialName: materialName.value,
    startDate: startDate.value,
    endDate: endDate.value,
    qty_state: qty_state.value,
    limitOut: limitOut.value,
    product_qty: product_qty.value,
    order_qty: order_qty.value,
   }
   qtylist();
 };
 
  // api   
   const columnDefs = ref([
      { headerName: "No.", field: "row_num", width: 100, cellStyle: { textAlign: "center" } },
      { headerName: "", field: "material_code", width: 250, cellStyle: { textAlign: "center" } },
      { headerName: "", field: "material_name", cellStyle: { textAlign: "left" } },
      { headerName: "", field: "stok_qty", cellStyle: { textAlign: "right" } },
      { headerName: "", field: "reject_qty", cellStyle: { textAlign: "right" } },
      { headerName: "", field: "trush_qty", cellStyle: { textAlign: "right" } },
      { headerName: "", field: "order_qty", cellStyle: { textAlign: "right" } },
      { headerName: "", field: "safety_inventory", cellStyle: { textAlign: "right" } },
      { headerName: "", field: "regi_date", cellStyle: { textAlign: "center" } },
   ]);

 //   
 const enterkey = (event) => {
    event.preventDefault();
    seachPoList();
 };
 
 const onReady = (param) => {
   param.api.sizeColumnsToFit(); // api   
 }
 
 //   api 
 const rowData = ref([]);
 
 //    ( )
 const qtylist = async function(){
   console.log('aaaaa', seachcondition.value);
   let result = await axios.post(`${ajaxUrl}/material/materialqtylist`, seachcondition.value)
                        .catch(err=>console.log(err));
   console.log(result.data);
   
   rowData.value = result.data.map(val => ({
      ...val,
      stok_qty: val.material_name.includes('') ? Number(val.stok_qty).toLocaleString() + ' ' : (Number(val.stok_qty)*0.001).toLocaleString() + ' kg',
      reject_qty : val.material_name.includes('') ? Number(val.reject_qty).toLocaleString() + ' ' : (Number(val.reject_qty)*0.001).toLocaleString() + ' kg',
      trush_qty : val.material_name.includes('') ? Number(val.trush_qty).toLocaleString() + ' ' : (Number(val.trush_qty)*0.001).toLocaleString() + ' kg', 
      order_qty : val.material_name.includes('') ? Number(val.order_qty).toLocaleString() + ' ' : (Number(val.order_qty)*0.001).toLocaleString() + ' kg',  
      safety_inventory : val.material_name.includes('') ? Number(val.safety_inventory).toLocaleString() + ' ' : (Number(val.safety_inventory)*0.001).toLocaleString() + ' kg',   
      regi_date : userDateUtils.dateFormat(val.regi_date, "yyyy-MM-dd"),
   }));
 }

 //   
 onBeforeMount(()=>{
   qtylist();   //  
   loginInfo();
 });
 
 </script>
 <style lang="scss" scoped>
 .main-container{
    background-color:  #e9ecef;
    margin-left: 20px;
    margin-right: 20px;
    margin-top: 0px;
    margin-bottom: 0px;
    border-radius: 10px;
 }
 .content{
   margin-left: 20px;
   margin-right: 20px;
   margin-top: 0px;
   margin-bottom: 0px;
   padding: 0px;
 }
 input {
      background-color: $white;
      border: solid 1px  ;
 }
</style>

<style lang="scss">
   .ag-header {
      .ag-header-cell-label {
         place-content: center;
      }
   }
</style>
 