<template>
  <flat-pickr
    class="form-control custom-input"
    placeholder="  "
    v-model="selectedDate"
    :config="dateConfig"
  />
  {{ selectedDate }}
</template>

<script>
import FlatPickr from 'vue-flatpickr-component';
import 'flatpickr/dist/flatpickr.css';
import { Korean } from 'flatpickr/dist/l10n/ko.js';

export default {
  components: { FlatPickr },
  data() {
    return {
      selectedDate: null,
      dateConfig: {
        defaultDate: null, //    
        enableTime: false, //    
        dateFormat: 'Y-m-d',  //
        //dateFormat: 'Y-m-d H:i',  //
        time_24hr: true, // 24 
        locale: Korean, //   
        mode: 'single', //   
        closeOnSelect: true, //     
      },
    };
  },
};
</script>
<style scoped>
.custom-input::placeholder {
  text-align: right;
}
.custom-input {
  width: 200px;
  background-color: white;
  border: solid 1px;
  text-align: center;
}
.flatpickr-calendar {
  z-index: 10000; /*   FlatPickr    */
}
</style>